import type {SizeType} from "../../model/map/map-type";
import {bottomStatusBarHeight} from "../../css/style";
import {getMaxInList} from "../../util/array";
import type {GameUnitType, GameUnitTypeEnum} from "../../model/game/unit/unit-type";
import {GamePlayerColorIdEnum} from "../../model/game/player/player-type";
import {defaultUnitData} from "../../model/game/unit/unit-const";
import type {GameUnitFightAnimationDataType, SpriteAnimationType} from "../animation-view/animation-type";
import type {PointType} from "../../model/game/point/point-type";
import {previousValueInList} from "../animation-view/animation-view-const";
import {getRandomNumber} from "../../util/number";

import {fightAnimationViewSize} from "./unit-fight-animation-const";
import {fightAnimationSpriteMap} from "./unit-fight-animation-map/fight-animation-sprite-map";

export function getFightAnimation(
    unitType: GameUnitTypeEnum,
    color: GamePlayerColorIdEnum
): GameUnitFightAnimationDataType {
    return fightAnimationSpriteMap[unitType][color];
}

function getFightAnimationViewSize(screenSize: SizeType): SizeType {
    return {
        height: screenSize.height - bottomStatusBarHeight,
        width: screenSize.width,
    };
}

export function getFightAnimationScale(screenSize: SizeType): number {
    const fullViewSize: SizeType = getFightAnimationViewSize(screenSize);

    return Math.min(
        fullViewSize.width / fightAnimationViewSize.width,
        fullViewSize.height / fightAnimationViewSize.height
    );
}

export function getMaxSizeAnimationSpiteList(spriteAnimationList: Array<SpriteAnimationType>): number {
    const maxSpriteAnimation = getMaxInList<SpriteAnimationType>(
        spriteAnimationList,
        (spriteAnimation: SpriteAnimationType): number => {
            return spriteAnimation.spriteList.length;
        }
    );

    return maxSpriteAnimation ? maxSpriteAnimation.spriteList.length : 0;
}

export function getNeedShowFightAnimation(
    isUnitFightAnimationEnable: boolean,
    unitA: GameUnitType | null,
    unitB: GameUnitType | null
): boolean {
    if (isUnitFightAnimationEnable === false || unitA === null || unitB === null) {
        return false;
    }

    return [unitA, unitB].every((unit: GameUnitType): boolean => {
        const unitFightAnimation = getFightAnimation(unit.unitType, GamePlayerColorIdEnum.gray);
        const {idle, attackStart, attackActive, attackRecovery} = unitFightAnimation;
        const animationList: Array<Array<SpriteAnimationType>> = [idle, attackStart, attackActive, attackRecovery];

        return animationList.every((spriteAnimationList: Array<SpriteAnimationType>): boolean => {
            if (spriteAnimationList.length === 0) {
                return false;
            }

            return spriteAnimationList.every((spriteAnimation: SpriteAnimationType): boolean => {
                return spriteAnimation.spriteList.length > 0;
            });
        });
    });
}

export function getIsRenderFightAnimationUnit(data: Record<"health" | "index" | "size", number>): boolean {
    const {health, index, size} = data;
    const oneUnitCost = Math.floor(defaultUnitData.health / size);
    const showUnitCount = Math.ceil(health / oneUnitCost);

    return index + 1 <= showUnitCount;
}

export function randomizeAnimationList(animationList: Array<SpriteAnimationType>): Array<SpriteAnimationType> {
    const currentRandomPosition: PointType = {x: 0, y: 0};

    return animationList.map<SpriteAnimationType>((effectSpriteAnimation: SpriteAnimationType): SpriteAnimationType => {
        const {
            randomPosition: {x: randomX, y: randomY},
        } = effectSpriteAnimation;

        if (randomX !== previousValueInList) {
            if (randomX >= 0) {
                currentRandomPosition.x = getRandomNumber(-randomX, randomX);
            } else {
                currentRandomPosition.x += getRandomNumber(-randomX, randomX);
            }
        }

        if (randomY !== previousValueInList) {
            if (randomY >= 0) {
                currentRandomPosition.y = getRandomNumber(-randomY, randomY);
            } else {
                currentRandomPosition.y += getRandomNumber(-randomY, randomY);
            }
        }

        return {
            ...effectSpriteAnimation,
            randomPosition: {...currentRandomPosition},
        };
    });
}
