import {useState} from "react";
import {useNavigate} from "react-router-dom";

import type {GameUnitTypeEnum} from "../../../../../model/game/unit/unit-type";
import {getUnitInfoByType, getUnitListByPlayerId} from "../../../../../model/game/unit/unit-helper";
import {Locale} from "../../../../../provider/locale/locale-context";
import {GameActionTypeEnum} from "../../../../../provider/game/game-context-type";
import {useGameContext} from "../../../../../provider/game/game-context";
import {IsRender} from "../../../../../layout/is-render/is-render";
import {getPlayerByPlayerId, getPlayerColorIdByPlayerId} from "../../../../../model/game/player/player-helper";
import type {GamePlayerColorIdEnum} from "../../../../../model/game/player/player-type";
import {unitImage} from "../../client-game-unit/image/unit-image";
import {UiButton} from "../../../../../layout/ui-button/ui-button";
import {BigText} from "../../../../../layout/game-font/text/big-text";
import {SmallText} from "../../../../../layout/game-font/text/small-text";

import {unitStoreIcon} from "./image/unit-store-icon";
import unitStoreItemStyle from "./unit-store-item.scss";

export interface UnitStoreItemPropsType {
    readonly cost: number;
    readonly isAvailableFromMap: boolean;
    readonly unitId: string;
    readonly unitType: GameUnitTypeEnum;
}
export function UnitStoreItem(props: UnitStoreItemPropsType): JSX.Element | null {
    const {unitType, cost, unitId, isAvailableFromMap} = props;
    const {pushAction, activePoint, gameState} = useGameContext();
    const {playerList, activeGamePlayerId, gameSettings, unitList} = gameState;
    const {langKey, moveSize, armor, attack} = getUnitInfoByType(unitType);
    const player = getPlayerByPlayerId(activeGamePlayerId, playerList);
    const [isShowDescription, setIsShowDescription] = useState<boolean>(false);
    const navigate = useNavigate();

    if (!player) {
        console.error(`can not find player by id: ${activeGamePlayerId}`);
        return <h1>can not find player by id: {activeGamePlayerId}</h1>;
    }

    const playerColor: GamePlayerColorIdEnum = getPlayerColorIdByPlayerId(activeGamePlayerId, playerList);
    const playerBalance: number = player.money;
    const isHasLimit: boolean = getUnitListByPlayerId(activeGamePlayerId, unitList).length >= gameSettings.unitLimit;

    return (
        <div className={unitStoreItemStyle.unit_store_item}>
            <h4 className={unitStoreItemStyle.unit_store_item__header}>
                <Locale stringKey={langKey.name} />
                <UiButton
                    isSquared
                    langKey="UI__INFO"
                    onClick={(): undefined => {
                        setIsShowDescription(!isShowDescription);
                    }}
                />
            </h4>
            <div className={unitStoreItemStyle.unit_store_item__main}>
                <div className={unitStoreItemStyle.unit_store_item__main_data}>
                    <img
                        alt={unitType}
                        className={unitStoreItemStyle.unit_store_item__unit_image}
                        src={unitImage[unitType][playerColor][0].src}
                    />
                    <div className={unitStoreItemStyle.unit_store_item__unit_stats_info}>
                        <div className={unitStoreItemStyle.unit_store_item__unit_stats_info__line}>
                            <img
                                alt="attack"
                                className={unitStoreItemStyle.unit_store_item__unit_stats_info__icon}
                                src={unitStoreIcon.attack.src}
                            />
                            <SmallText
                                className={unitStoreItemStyle.unit_store_item__unit_stats_info__text}
                                scale={2}
                                text={`${attack.min}-${attack.max}`}
                            />
                            <img
                                alt="defence"
                                className={unitStoreItemStyle.unit_store_item__unit_stats_info__icon}
                                src={unitStoreIcon.defence.src}
                            />
                            <SmallText
                                className={unitStoreItemStyle.unit_store_item__unit_stats_info__text}
                                scale={2}
                                text={armor.toString(10).padEnd(2, "S")}
                            />
                        </div>
                        <div className={unitStoreItemStyle.unit_store_item__unit_stats_info__line}>
                            <img
                                alt="move"
                                className={unitStoreItemStyle.unit_store_item__unit_stats_info__icon}
                                src={unitStoreIcon.move.src}
                            />
                            <SmallText
                                className={unitStoreItemStyle.unit_store_item__unit_stats_info__text}
                                scale={2}
                                text={moveSize.toString(10)}
                            />
                        </div>
                    </div>
                </div>

                <div className={unitStoreItemStyle.unit_store_item__unit_stats_info__buy_button_wrapper}>
                    <UiButton
                        isDisabled={isHasLimit || cost > playerBalance || !isAvailableFromMap}
                        onClick={async (): Promise<undefined> => {
                            navigate(-1);
                            await pushAction({
                                actionType: GameActionTypeEnum.buyUnit,
                                addUnitList: [],
                                briefing: null,
                                buyUnitType: unitType,
                                movePointList: [],
                                toPoint: activePoint,
                                unitCurrentPoint: activePoint,
                                unitId,
                            });
                        }}
                    >
                        <BigText
                            className={unitStoreItemStyle.unit_store_item__unit_cost}
                            scale={2}
                            text={`g ${isAvailableFromMap ? cost.toString(10) : "---"}`}
                        />
                    </UiButton>
                </div>
            </div>

            <IsRender isRender={isShowDescription}>
                <p className={unitStoreItemStyle.unit_store_item__unit_description}>
                    <Locale stringKey={langKey.description} />
                </p>
            </IsRender>
        </div>
    );
}
