import {useState, useEffect} from "react";
import {useNavigate} from "react-router-dom";

import {Page} from "../../../layout/page/page";
import {appRoute} from "../../../app/app-route";
import {serviceStyle} from "../../../css/style";
import clientHomeStyle from "../client-home/client-home.scss";
import logoImage from "../client-home/image/logo.png";
import {UiLink} from "../../../layout/ui-button/ui-link";
import {uiButtonImage} from "../../../layout/ui-button/image/ui-button-image";
import {PageHeader} from "../../../layout/page/page-header/page-header";
import {ClientLoadGamePopup} from "../client-game/client-load-game-popup/client-load-game-popup";
import {UiButton} from "../../../layout/ui-button/ui-button";
import {waitForNextFrame} from "../../../util/time";
import {usePlayTitleTheme} from "../../../media/play-media";
import {useGameContext} from "../../../provider/game/game-context";
import {RateApp} from "../../../layout/rate-app/rate-app";
import {noop} from "../../../util/function";

import clientPlayStyle from "./client-play.scss";

export function ClientPlay(): JSX.Element {
    const {resetState} = useGameContext();
    const [isLoadGamePopupOpen, setIsLoadGamePopupOpen] = useState<boolean>(false);
    const [isQuickLoad, setIsQuickLoad] = useState<boolean>(false);
    const navigate = useNavigate();

    usePlayTitleTheme();

    useEffect(resetState, [resetState]);

    return (
        <Page className={serviceStyle.double_block_wrapper}>
            <PageHeader className={clientPlayStyle.play__header} langKey="UI__BUTTON__PLAY" />
            <div className={serviceStyle.double_block_child}>
                <img alt="logo" className={clientHomeStyle.logo_image} src={logoImage} />
            </div>
            <div className={serviceStyle.double_block_child}>
                <div className={serviceStyle.double_block_child__button_wrapper}>
                    <UiLink
                        href={appRoute.companies.path}
                        iconSrc={uiButtonImage.sword.src}
                        langKey="UI__BUTTON__COMPANIES"
                    />

                    <UiLink
                        href={appRoute.skirmish.path}
                        iconSrc={uiButtonImage.castle.src}
                        langKey="UI__BUTTON__SKIRMISH"
                    />

                    <UiLink
                        href={appRoute.skirmishUserMaps.path}
                        iconSrc={uiButtonImage.castle.src}
                        langKey="UI__BUTTON__USER_MAPS"
                    />

                    <UiButton
                        iconSrc={uiButtonImage.star.src}
                        langKey="SAVE_LOAD__QUICK_LOAD_GAME"
                        onClick={(): undefined => {
                            setIsQuickLoad(true);
                            setIsLoadGamePopupOpen(true);
                        }}
                    />

                    <UiButton
                        iconSrc={uiButtonImage.diamondGreen.src}
                        langKey="UI__BUTTON__LOAD_GAME"
                        onClick={(): undefined => {
                            setIsQuickLoad(false);
                            setIsLoadGamePopupOpen(true);
                        }}
                    />
                </div>
            </div>
            <ClientLoadGamePopup
                afterLoad={async (): Promise<undefined> => {
                    navigate(appRoute.clientGame.path);
                    await waitForNextFrame();
                }}
                handleClose={(): undefined => {
                    setIsLoadGamePopupOpen(false);
                }}
                isLoadGamePopupOpen={isLoadGamePopupOpen}
                isQuickLoad={isQuickLoad}
                setIsGameMenuOpen={noop}
            />
            <RateApp />
        </Page>
    );
}
