/* global document, setTimeout, requestAnimationFrame, Keyframe, KeyframeAnimationOptions */

import type {PromiseResolveType} from "./promise";

export async function waitForTime(timeInMs: number): Promise<void> {
    return new Promise<void>((resolve: () => void) => {
        setTimeout(resolve, timeInMs);
    });
}

export async function waitForAnimateTime(timeInMs: number): Promise<void> {
    const transformList: Array<Keyframe> = [{transform: "translate3d(0, 0, 0)"}, {transform: "translate3d(0, 0, 0)"}];

    const timing: KeyframeAnimationOptions = {
        duration: timeInMs,
        fill: "forwards",
        iterations: 1,
    };

    return new Promise((resolve: PromiseResolveType<void>): void => {
        const animationNode = document.createElement("div");
        const animation = animationNode.animate(transformList, timing);

        animation.onfinish = (): undefined => {
            resolve();
        };
    });
}

export async function waitForNextFrame(): Promise<void> {
    return new Promise<void>((resolve: () => void) => {
        requestAnimationFrame(() => {
            resolve();
        });
    });
}

export async function waitForCallback(
    callBack: () => Promise<boolean> | boolean,
    maxCount: number,
    timeOutMs: number
): Promise<boolean> {
    if (maxCount === 0) {
        throw new Error("waitForCallback, timeout");
    }

    const isDone = await callBack();

    if (isDone) {
        return true;
    }

    await waitForTime(timeOutMs);

    return waitForCallback(callBack, maxCount - 1, timeOutMs);
}

export const oneHourMilliseconds = 60 * 60 * 1e3;
export const oneDayMilliseconds = 24 * oneHourMilliseconds;
export const oneYearMilliseconds = 365 * oneDayMilliseconds;
