import {Page} from "../../../layout/page/page";
import type {GameMapType} from "../../../model/map/map-type";
import {PageHeader} from "../../../layout/page/page-header/page-header";
import {getUserMapList} from "../../../model/map/user-map";
import {Locale, useLocale} from "../../../provider/locale/locale-context";
import {PageContent} from "../../../layout/page/page-content/page-content";
import {IsRender} from "../../../layout/is-render/is-render";

import {ClientSkirmishMapItem} from "./client-skirmish-map-item/client-skirmish-map-item";
import {getSeparatedMapByPlayerCount, mapNameSortCallback} from "./client-skirmish-helper";
import {ClientSkirmishMapSectionHeader} from "./client-skirmish-map-section-header/client-skirmish-map-section-header";
import clientSkirmishStyle from "./client-skirmish.scss";

export function ClientSkirmishUserMap(): JSX.Element {
    const userMapList: Array<GameMapType> = getUserMapList();
    const {localeName, getLocalizedString} = useLocale();

    const {two, three, four} = getSeparatedMapByPlayerCount(
        [...userMapList].sort((mapA: GameMapType, mapB: GameMapType): number => {
            return mapNameSortCallback(mapA, mapB, localeName, getLocalizedString);
        })
    );

    return (
        <Page>
            <PageHeader langKey="UI__BUTTON__USER_MAPS" />
            <PageContent>
                <div className={clientSkirmishStyle.client_skirmish_map_list}>
                    <IsRender isRender={userMapList.length === 0}>
                        <p className={clientSkirmishStyle.client_skirmish_map_list__no_saved_maps_text}>
                            <Locale stringKey="MAP_EDITOR__NO_SAVED_MAPS" />
                        </p>
                    </IsRender>

                    <IsRender isRender={two.length > 0}>
                        <ClientSkirmishMapSectionHeader count={2} />
                        {two.map<JSX.Element>((map: GameMapType): JSX.Element => {
                            return <ClientSkirmishMapItem key={map.mapId} map={map} />;
                        })}
                    </IsRender>
                    <IsRender isRender={three.length > 0}>
                        <ClientSkirmishMapSectionHeader count={3} />
                        {three.map<JSX.Element>((map: GameMapType): JSX.Element => {
                            return <ClientSkirmishMapItem key={map.mapId} map={map} />;
                        })}
                    </IsRender>
                    <IsRender isRender={four.length > 0}>
                        <ClientSkirmishMapSectionHeader count={4} />
                        {four.map<JSX.Element>((map: GameMapType): JSX.Element => {
                            return <ClientSkirmishMapItem key={map.mapId} map={map} />;
                        })}
                    </IsRender>
                </div>
            </PageContent>
        </Page>
    );
}
