/* global HTMLInputElement */
import {type SyntheticEvent, useState, useEffect} from "react";

import {GamePopup} from "../../../../layout/game-popup/game-popup";
import gamePopupStyle from "../../../../layout/game-popup/game-popup.scss";
import {Locale, useLocale} from "../../../../provider/locale/locale-context";
import {UiButton} from "../../../../layout/ui-button/ui-button";
import {serviceStyle} from "../../../../css/style";
import {useGameContext} from "../../../../provider/game/game-context";
import {getSaveName, saveGame} from "../../../../provider/game/action/save-game";
import {GameSnackbar} from "../../../../layout/game-snackbar/game-snackbar";

import clientSaveGamePopupStyle from "./client-save-game-popup.scss";

interface ClientSaveGamePopupType {
    readonly isSaveGamePopupOpen: boolean;
    readonly setIsSaveGamePopupOpen: (isOpen: boolean) => void;
}

export function ClientSaveGamePopup(props: ClientSaveGamePopupType): JSX.Element {
    const {isSaveGamePopupOpen, setIsSaveGamePopupOpen} = props;
    const {gameState} = useGameContext();
    const {stepCount} = gameState;
    const [saveDefaultName, setSaveDefaultName] = useState<string>("");
    const [saveName, setSaveName] = useState<string>(saveDefaultName);
    const [isGameSavedSnackbarOpen, setIsGameSavedSnackbarOpen] = useState<boolean>(false);
    const {getLocalizedString} = useLocale();

    useEffect(() => {
        const timedSaveName = getSaveName(gameState, getLocalizedString);

        if (isSaveGamePopupOpen) {
            setSaveDefaultName(timedSaveName);
            setSaveName(timedSaveName);
        }
    }, [gameState, getLocalizedString, isSaveGamePopupOpen, stepCount]);

    return (
        <>
            <GamePopup isOpen={isSaveGamePopupOpen} setIsOpen={setIsSaveGamePopupOpen}>
                <h3 className={gamePopupStyle.game_popup__header}>
                    <Locale stringKey="SAVE_LOAD__SAVE_GAME" />
                </h3>
                <div className={clientSaveGamePopupStyle.client_save_game_popup__text_input_wrapper}>
                    <input
                        className={serviceStyle.input_text}
                        defaultValue={saveDefaultName}
                        key={saveDefaultName}
                        onInput={(evt: SyntheticEvent<HTMLInputElement>): undefined => {
                            setSaveName(evt.currentTarget.value.trim());
                        }}
                        type="text"
                    />
                </div>
                <div className={gamePopupStyle.game_popup__footer}>
                    <UiButton
                        langKey="SAVE_LOAD__SAVE"
                        onClick={(): undefined => {
                            saveGame(saveName, gameState);
                            setIsSaveGamePopupOpen(false);
                            setIsGameSavedSnackbarOpen(true);
                        }}
                    />
                    <UiButton
                        langKey="UI__CLOSE"
                        onClick={(): undefined => {
                            setIsSaveGamePopupOpen(false);
                        }}
                    />
                </div>
            </GamePopup>
            <GameSnackbar
                isOpen={isGameSavedSnackbarOpen}
                setIsOpen={setIsGameSavedSnackbarOpen}
                useClosingOverlay={false}
            >
                <Locale stringKey="SAVE_LOAD__SAVE_SUCCESSFUL" />
            </GameSnackbar>
        </>
    );
}
