/* eslint-disable multiline-comment-style, capitalized-comments, line-comment-position, multiline-comment-style */

import {type CSSProperties, type MutableRefObject, useState, useCallback, useEffect, useRef, useMemo} from "react";

import type {GameUnitType} from "../../model/game/unit/unit-type";
import type {GamePlayerColorIdEnum} from "../../model/game/player/player-type";
import {getPlayerColorIdByPlayerId} from "../../model/game/player/player-helper";
import {useGameContext} from "../../provider/game/game-context";
import {noop} from "../../util/function";
import {AnimationArrayView} from "../animation-view/animation-array-view";
import type {SpriteAnimationType} from "../animation-view/animation-type";
import {waitForAnimateTime} from "../../util/time";
import {IsRender} from "../is-render/is-render";

import {getFightAnimation, randomizeAnimationList} from "./unit-fight-animation-helper";
import {unitDeathAnimation} from "./unit-fight-animation-additional/unit-die-animation/unit-die-animation";

interface UnitFightAnimationSinglePropsType {
    readonly delay: number;
    readonly frameTimeInterval: number;
    readonly isAggressor: boolean;
    readonly isAlive: boolean;
    readonly isIdle: boolean;
    readonly unit: GameUnitType;
    readonly unitX: number;
    readonly unitY: number;
}

// eslint-disable-next-line max-statements, complexity
export function UnitFightAnimationSingle(props: UnitFightAnimationSinglePropsType): JSX.Element {
    const [isPlaying, setIsPlaying] = useState<boolean>(false);
    const [isPlayingEffect, setIsPlayingEffect] = useState<boolean>(false);
    const {unit, isIdle, frameTimeInterval, isAggressor, unitX, unitY, isAlive, delay} = props;
    const {unitType, gamePlayerId} = unit;
    const {gameState} = useGameContext();
    const {playerList} = gameState;
    const unitColor: GamePlayerColorIdEnum = getPlayerColorIdByPlayerId(gamePlayerId, playerList);
    const {attackRecovery, idle, attackStart, attackActive, effect} = getFightAnimation(unitType, unitColor);
    const [animationIndex, setAnimationIndex] = useState<number>(0);
    const [animationList, setAnimationList] = useState<Array<Array<SpriteAnimationType>>>([]);
    const randomizedEffectRef: MutableRefObject<Array<SpriteAnimationType>> = useRef<Array<SpriteAnimationType>>(
        randomizeAnimationList(effect)
    );
    const [isWasKilled, setIsWasKilled] = useState<boolean>(false);
    const isAliveInitialRef: MutableRefObject<boolean> = useRef<boolean>(isAlive);
    const onAnimationEndCallback: MutableRefObject<(() => void) | null> = useRef<(() => void) | null>(null);
    const onAnimationPhase: MutableRefObject<number> = useRef<number>(0);

    useEffect(() => {
        (async (): Promise<undefined> => {
            await waitForAnimateTime(delay);
            setIsPlaying(true);
        })();
    }, [delay]);

    useEffect(() => {
        (async (): Promise<undefined> => {
            await waitForAnimateTime(delay);
            setIsWasKilled(isAlive === false);
        })();
    }, [delay, isAlive]);

    const idleAnimationList: Array<Array<SpriteAnimationType>> = useMemo((): Array<Array<SpriteAnimationType>> => {
        return Array.from<Array<SpriteAnimationType>>({length: 100}).fill(idle);
    }, [idle]);

    const fightAnimationList: Array<Array<SpriteAnimationType>> = useMemo((): Array<Array<SpriteAnimationType>> => {
        return [idle, attackStart, attackActive, attackRecovery, idle];
    }, [idle, attackStart, attackActive, attackRecovery]);

    const onOneAnimationEnd = useCallback(() => {
        if (onAnimationEndCallback.current) {
            onAnimationEndCallback.current();
            onAnimationEndCallback.current = null;
            return;
        }

        // run idle animation after fight
        if (isIdle === true && onAnimationPhase.current === 2) {
            onAnimationPhase.current = 3;
            setAnimationList(idleAnimationList);
            setAnimationIndex(0);
        }

        setAnimationIndex((currentAnimationIndex: number): number => {
            return currentAnimationIndex + 1;
        });
    }, [isIdle, idleAnimationList]);

    useEffect(() => {
        (async (): Promise<undefined> => {
            await waitForAnimateTime(delay);
            const updatedAnimationList: Array<Array<SpriteAnimationType>> = isIdle
                ? idleAnimationList
                : fightAnimationList;

            // just start first idle animation
            if (onAnimationPhase.current === 0) {
                onAnimationPhase.current = 1;
                setAnimationList(updatedAnimationList);
                setAnimationIndex(0);
                return;
            }

            // run fight animation for the next frame
            if (isIdle === false) {
                onAnimationEndCallback.current = (): undefined => {
                    onAnimationPhase.current = 2;
                    setAnimationList(updatedAnimationList);
                    setAnimationIndex(0);
                    setIsPlayingEffect(isAlive || isAggressor);
                };
            }
        })();
    }, [delay, fightAnimationList, idleAnimationList, isAggressor, isAlive, isIdle]);

    const wrapperStyle: CSSProperties = useMemo((): CSSProperties => {
        return {
            height: "100%",
            left: "0",
            position: "absolute",
            top: "0",
            transform: `scaleX(${isAggressor ? -1 : 1})`,
            width: "100%",
            zIndex: 0,
        };
    }, [isAggressor]);

    if (isAliveInitialRef.current === false) {
        return <div key={`initial-death-${animationIndex}`} style={wrapperStyle} />;
    }

    const spriteAnimationList = animationList.at(animationIndex);

    if (!spriteAnimationList) {
        return <div key={`no-animation-${animationIndex}`} style={wrapperStyle} />;
    }

    return (
        <>
            <IsRender isRender={isWasKilled === false}>
                <AnimationArrayView
                    delay={isPlaying ? 0 : 1e6}
                    frameTimeInterval={frameTimeInterval}
                    key={`${animationIndex}-${isPlaying}`}
                    onEnd={onOneAnimationEnd}
                    spriteAnimationList={spriteAnimationList}
                    wrapperStyle={{
                        ...wrapperStyle,
                        zIndex: (Number(wrapperStyle.zIndex) || 0) + unitY,
                    }}
                    x={unitX}
                    y={unitY}
                />
            </IsRender>

            <IsRender isRender={isPlayingEffect}>
                <AnimationArrayView
                    delay={0}
                    frameTimeInterval={frameTimeInterval}
                    onEnd={noop}
                    spriteAnimationList={randomizedEffectRef.current}
                    wrapperStyle={{
                        ...wrapperStyle,
                        zIndex: (Number(wrapperStyle.zIndex) || 0) + unitY + 1,
                    }}
                    x={unitX}
                    y={unitY}
                />
            </IsRender>

            <IsRender isRender={isWasKilled}>
                <AnimationArrayView
                    delay={0}
                    frameTimeInterval={frameTimeInterval}
                    onEnd={noop}
                    spriteAnimationList={unitDeathAnimation}
                    wrapperStyle={{
                        ...wrapperStyle,
                        zIndex: (Number(wrapperStyle.zIndex) || 0) + unitY + 2,
                    }}
                    x={unitX}
                    y={unitY}
                />
            </IsRender>
        </>
    );
}
