/* eslint-disable no-mixed-operators */

/* global IS_PRODUCTION, HTMLDivElement */
import type {MutableRefObject, CSSProperties} from "react";

import type {SpriteType} from "../animation-view/animation-type";

import spriteViewStyle from "./sprite-view.scss";

interface SpriteViewPropsType {
    readonly frame: MutableRefObject<HTMLDivElement | null>;
    readonly sprite: SpriteType;
    readonly style: CSSProperties;
    readonly x: number;
    readonly y: number;
}

export function SpriteView(props: SpriteViewPropsType): JSX.Element {
    const {sprite, y, x, frame, style} = props;
    const {scaleY, scaleX, imgSrc, height, width, offsetTop, offsetLeft} = sprite;

    return (
        <div
            ref={frame}
            style={{
                ...style,
                height: `${height}px`,
                left: `${Math.round(-width / 2 + x)}px`,
                position: "absolute",
                top: `${Math.round(-height / 2 + y)}px`,
                width: `${width}px`,
            }}
        >
            <img
                alt={
                    // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
                    IS_PRODUCTION ? "" : JSON.stringify(sprite)
                }
                className={spriteViewStyle.sprite_view__image}
                src={imgSrc}
                style={{transform: `translate(${offsetLeft}px, ${offsetTop}px) scale(${scaleX}, ${scaleY})`}}
            />
        </div>
    );
}
