import {GamePopup} from "../../../../layout/game-popup/game-popup";
import gamePopupStyle from "../../../../layout/game-popup/game-popup.scss";
import {Locale} from "../../../../provider/locale/locale-context";
import {UiButton} from "../../../../layout/ui-button/ui-button";
import {uiButtonImage} from "../../../../layout/ui-button/image/ui-button-image";

import clientGameMenuStyle from "./client-game-menu/client-game-menu.scss";
import {ClientGameMenu} from "./client-game-menu/client-game-menu";

interface ClientGameMenuPopupType {
    readonly isOpen: boolean;
    readonly setIsGameMenuOpen: (isGameMenuOpen: boolean) => void;
    readonly setIsGameStarMessageOpen: (isShow: boolean) => void;
}

export function ClientGameMenuPopup(props: ClientGameMenuPopupType): JSX.Element {
    const {setIsGameStarMessageOpen, isOpen, setIsGameMenuOpen} = props;

    return (
        <GamePopup isOpen={isOpen} setIsOpen={setIsGameMenuOpen}>
            <h3 className={gamePopupStyle.game_popup__header}>
                <Locale stringKey="BATTLE_MENU__MENU" />
            </h3>
            <div className={clientGameMenuStyle.client_game_menu}>
                <div className={clientGameMenuStyle.client_game_menu__button_container}>
                    <ClientGameMenu
                        setIsGameMenuOpen={setIsGameMenuOpen}
                        setIsGameStarMessageOpen={setIsGameStarMessageOpen}
                    />
                    <UiButton
                        iconSrc={uiButtonImage.sword.src}
                        langKey="BATTLE_MENU__RETURN_TO_GAME"
                        onClick={(): undefined => {
                            setIsGameMenuOpen(false);
                        }}
                    />
                </div>
            </div>
        </GamePopup>
    );
}
