/* global HTMLSelectElement, HTMLInputElement */

import {Fragment, type SyntheticEvent, useState} from "react";

import {serviceStyle} from "../../../../css/style";
import {Locale, useLocale} from "../../../../provider/locale/locale-context";
import {enUs} from "../../../../provider/locale/translation/en-us/data";
import {ruRu} from "../../../../provider/locale/translation/ru-ru/data";
import {LocaleNameEnum} from "../../../../provider/locale/locale-context-type";
import {stringToLocaleName} from "../../../../provider/locale/locale-context-helper";
import {useSettingContext} from "../../../../provider/setting/setting-context";
import {
    type BooleanSettingItemType,
    BotDifficultEnum,
    type SettingContextSettingType,
    type SoundSettingItemType,
} from "../../../../provider/setting/setting-type";
import {
    booleanSettingList,
    defaultNumberSettingState,
    soundSettingList,
} from "../../../../provider/setting/setting-const";
import {getEnumValueEnsure} from "../../../../util/enum";

import {AudioPlayerAudioRowEnum, type SoundSettingType} from "../../../../hook/audio-player-hook/audio-player-type";
import {
    getAudioByRow,
    getAudioRowIsMuted,
    getAudioRowVolume,
    playAudio,
    saveAudioRowIsMuted,
    saveAudioRowVolume,
} from "../../../../hook/audio-player-hook/audio-player-helper";
import {cls} from "../../../../util/css";
import {UiButton} from "../../../../layout/ui-button/ui-button";
import {IsRender} from "../../../../layout/is-render/is-render";

import clientSettingsContent from "./client-settings-content.scss";

const {animationStepTime: defaultAnimationStepTime} = defaultNumberSettingState;

export function ClientSettingsContent(): JSX.Element {
    const {localeName, setLocaleName} = useLocale();
    const {setting, setBoolean, setNumber, setSetting} = useSettingContext();
    const [soundSetting, setSoundSetting] = useState<SoundSettingType>({
        [AudioPlayerAudioRowEnum.ambient]: {
            isMuted: getAudioRowIsMuted(AudioPlayerAudioRowEnum.ambient),
            volume: getAudioRowVolume(AudioPlayerAudioRowEnum.ambient),
        },
        [AudioPlayerAudioRowEnum.master]: {
            isMuted: getAudioRowIsMuted(AudioPlayerAudioRowEnum.master),
            volume: getAudioRowVolume(AudioPlayerAudioRowEnum.master),
        },
        [AudioPlayerAudioRowEnum.fight]: {
            isMuted: getAudioRowIsMuted(AudioPlayerAudioRowEnum.fight),
            volume: getAudioRowVolume(AudioPlayerAudioRowEnum.fight),
        },
        [AudioPlayerAudioRowEnum.uiEffect]: {
            isMuted: getAudioRowIsMuted(AudioPlayerAudioRowEnum.uiEffect),
            volume: getAudioRowVolume(AudioPlayerAudioRowEnum.uiEffect),
        },
    });

    const soundSettingNodeList: Array<JSX.Element> = soundSettingList.map<JSX.Element>(
        (soundSettingItem: SoundSettingItemType): JSX.Element => {
            const {audioRowName, langKey} = soundSettingItem;
            const {isMuted: isAudioRowMuted, volume: audioRowVolume} = soundSetting[audioRowName];
            const {isMuted: isAudioMasterRowMuted} = soundSetting[AudioPlayerAudioRowEnum.master];

            const isAudioMasterRow: boolean = audioRowName === AudioPlayerAudioRowEnum.master;
            const isAudioAmbientRow: boolean = audioRowName === AudioPlayerAudioRowEnum.ambient;
            const isAudioRowMasteredMuted = isAudioMasterRowMuted || isAudioRowMuted;

            return (
                <Fragment key={audioRowName}>
                    {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
                    <label
                        className={cls({
                            [serviceStyle.input__checkbox__label]: true,
                            [serviceStyle.input__checkbox__label__disabled]: !isAudioMasterRow && isAudioMasterRowMuted,
                        })}
                    >
                        <input
                            className={serviceStyle.input__checkbox}
                            defaultChecked={!getAudioRowIsMuted(audioRowName)}
                            onChange={(evt: SyntheticEvent<HTMLInputElement>): void => {
                                const isMuted = !evt.currentTarget.checked;
                                saveAudioRowIsMuted(audioRowName, isMuted);
                                getAudioByRow(audioRowName).muted = isMuted;

                                if (isAudioAmbientRow) {
                                    saveAudioRowIsMuted(AudioPlayerAudioRowEnum.fight, isMuted);
                                    getAudioByRow(AudioPlayerAudioRowEnum.fight).muted = isMuted;
                                }

                                if (isAudioMasterRow) {
                                    soundSettingList.forEach((soundSettingToUpdate: SoundSettingItemType) => {
                                        getAudioByRow(soundSettingToUpdate.audioRowName).muted =
                                            isMuted || getAudioRowIsMuted(soundSettingToUpdate.audioRowName);
                                    });
                                }

                                setSoundSetting((currentSoundSetting: SoundSettingType): SoundSettingType => {
                                    return {
                                        ...currentSoundSetting,
                                        [audioRowName]: {
                                            ...currentSoundSetting[audioRowName],
                                            isMuted,
                                        },
                                    };
                                });
                            }}
                            type="checkbox"
                        />
                        <p className={serviceStyle.input__checkbox__text}>
                            <Locale stringKey={langKey} />
                        </p>
                    </label>

                    <label
                        className={cls({
                            [serviceStyle.input__checkbox__label]: true,
                            [serviceStyle.input__checkbox__label__align_center]: true,
                            [serviceStyle.input__checkbox__label__disabled]: isAudioRowMasteredMuted,
                        })}
                        key={String(isAudioRowMasteredMuted)}
                    >
                        <input
                            className={serviceStyle.input__range}
                            defaultValue={isAudioRowMasteredMuted ? 0 : getAudioRowVolume(audioRowName)}
                            disabled={isAudioRowMasteredMuted}
                            max="1"
                            min="0"
                            onChange={(evt: SyntheticEvent<HTMLInputElement>): void => {
                                const volume = Number.parseFloat(evt.currentTarget.value);
                                const audioVolume = isAudioMasterRow
                                    ? volume
                                    : volume * getAudioRowVolume(AudioPlayerAudioRowEnum.master);

                                saveAudioRowVolume(audioRowName, volume);
                                getAudioByRow(audioRowName).volume = audioVolume;

                                if (isAudioAmbientRow) {
                                    saveAudioRowVolume(AudioPlayerAudioRowEnum.fight, volume);
                                    getAudioByRow(AudioPlayerAudioRowEnum.fight).volume = audioVolume;
                                }

                                if (isAudioMasterRow) {
                                    soundSettingList.forEach((soundSettingToUpdate: SoundSettingItemType) => {
                                        getAudioByRow(soundSettingToUpdate.audioRowName).volume =
                                            volume * getAudioRowVolume(soundSettingToUpdate.audioRowName);
                                    });
                                }

                                setSoundSetting((currentSoundSetting: SoundSettingType): SoundSettingType => {
                                    return {
                                        ...currentSoundSetting,
                                        [audioRowName]: {
                                            ...currentSoundSetting[audioRowName],
                                            volume,
                                        },
                                    };
                                });
                            }}
                            step="0.01"
                            type="range"
                        />

                        <p className={serviceStyle.input__checkbox__text}>
                            {isAudioRowMasteredMuted ? 0 : Math.round(audioRowVolume * 100)}%
                        </p>

                        <IsRender isRender={audioRowName === AudioPlayerAudioRowEnum.uiEffect}>
                            <UiButton
                                isSquared
                                onClick={async (): Promise<void> => {
                                    await playAudio({
                                        audioRow: audioRowName,
                                        src: getAudioByRow(audioRowName).src,
                                    });
                                }}
                            >
                                <Locale stringKey="SETTINGS__UI_SOUND_EFFECTS__CLICK" />
                            </UiButton>
                        </IsRender>
                    </label>
                </Fragment>
            );
        }
    );

    const settingNodeList: Array<JSX.Element> = booleanSettingList.map<JSX.Element>(
        (settingItem: BooleanSettingItemType): JSX.Element => {
            const {key, langKey} = settingItem;
            const isEnable: boolean = setting.booleanSetting[key];

            return (
                // eslint-disable-next-line jsx-a11y/label-has-associated-control
                <label className={serviceStyle.input__checkbox__label} key={key}>
                    <input
                        className={serviceStyle.input__checkbox}
                        defaultChecked={isEnable}
                        onChange={(): undefined => {
                            setBoolean(key, !isEnable);
                        }}
                        type="checkbox"
                    />
                    <p className={serviceStyle.input__checkbox__text}>
                        <Locale stringKey={langKey} />
                    </p>
                </label>
            );
        }
    );

    return (
        <div className={clientSettingsContent.client_settings__content}>
            <h2 className={clientSettingsContent.client_settings__header}>
                <Locale stringKey="SETTINGS__HEADER__GENERAL_SETTINGS" />
            </h2>
            <label className={serviceStyle.input__select__label}>
                {/* eslint-disable-next-line jsx-a11y/no-onchange */}
                <select
                    className={serviceStyle.input__select}
                    defaultValue={localeName}
                    onChange={(evt: SyntheticEvent<HTMLSelectElement>): undefined => {
                        const rawValue: string = evt.currentTarget.value;

                        setLocaleName(stringToLocaleName(rawValue));
                    }}
                >
                    <option value={LocaleNameEnum.enUs}>{enUs.META__LANGUAGE_NAME}</option>
                    <option value={LocaleNameEnum.ruRu}>{ruRu.META__LANGUAGE_NAME}</option>
                </select>
                <p className={serviceStyle.input__select__text}>
                    <Locale stringKey="SETTINGS__LANGUAGE" />
                </p>
            </label>

            <label className={serviceStyle.input__select__label}>
                {/* eslint-disable-next-line jsx-a11y/no-onchange */}
                <select
                    className={serviceStyle.input__select}
                    defaultValue={setting.botDifficult}
                    onChange={(evt: SyntheticEvent<HTMLSelectElement>): undefined => {
                        const rawValue: string = evt.currentTarget.value;

                        setSetting((currentSetting: SettingContextSettingType): SettingContextSettingType => {
                            return {
                                ...currentSetting,
                                botDifficult: getEnumValueEnsure<BotDifficultEnum>(
                                    BotDifficultEnum,
                                    rawValue,
                                    BotDifficultEnum.normal
                                ),
                            };
                        });
                    }}
                >
                    <option value={BotDifficultEnum.easy}>
                        <Locale stringKey="SETTINGS__AI_DIFFICULT__EASY" />
                    </option>
                    <option value={BotDifficultEnum.normal}>
                        <Locale stringKey="SETTINGS__AI_DIFFICULT__NORMAL" />
                    </option>
                    <option value={BotDifficultEnum.hard}>
                        <Locale stringKey="SETTINGS__AI_DIFFICULT__HARD" />
                    </option>
                </select>
                <p className={serviceStyle.input__select__text}>
                    <Locale stringKey="SETTINGS__AI_DIFFICULTY" />
                </p>
            </label>

            {settingNodeList}

            <label className={serviceStyle.input__select__label}>
                {/* eslint-disable-next-line jsx-a11y/no-onchange */}
                <select
                    className={serviceStyle.input__select}
                    defaultValue={setting.numberSetting.animationStepTime}
                    onChange={(evt: SyntheticEvent<HTMLSelectElement>): undefined => {
                        const rawValue: string = evt.currentTarget.value;

                        setNumber("animationStepTime", Number.parseInt(rawValue, 10) || defaultAnimationStepTime);
                    }}
                >
                    <option value={defaultAnimationStepTime / 4}>
                        <Locale stringKey="SETTINGS__FASTEST" />
                    </option>
                    <option value={defaultAnimationStepTime / 2}>
                        <Locale stringKey="SETTINGS__FAST" />
                    </option>
                    <option value={defaultAnimationStepTime}>
                        <Locale stringKey="SETTINGS__NORMAL" />
                    </option>
                    <option value={defaultAnimationStepTime * 2}>
                        <Locale stringKey="SETTINGS__SLOW" />
                    </option>
                    <option value={defaultAnimationStepTime * 4}>
                        <Locale stringKey="SETTINGS__SLOWEST" />
                    </option>
                </select>
                <p className={serviceStyle.input__select__text}>
                    <Locale stringKey="SETTINGS__GAME_SPEED" />
                </p>
            </label>

            <h2 className={clientSettingsContent.client_settings__header}>
                <Locale stringKey="SETTINGS__HEADER__SOUND_SETTINGS" />
            </h2>

            {soundSettingNodeList}
        </div>
    );
}
