/* global Keyframe */

import type {SpriteAnimationType} from "../../../../animation-view/animation-type";
import {
    emptySprite,
    spriteSpark0,
    spriteSpark1,
    spriteSpark2,
    spriteSpark3,
    spriteSpark4,
    spriteSpark5,
} from "../../../unit-fight-animation-sprite-const";
import {arrayShuffle, makeArray} from "../../../../../util/array";

const sparkShakeDelta = 0;
const sparkDeltaX = 128;

export const sparkShakeList: Array<Keyframe> = [
    {transform: `translate(${-sparkDeltaX - sparkShakeDelta}px, ${-sparkShakeDelta}px)`},
    {transform: `translate(${-sparkDeltaX}px, ${-sparkShakeDelta}px)`},
    {transform: `translate(${-sparkDeltaX + sparkShakeDelta}px, ${-sparkShakeDelta}px)`},

    {transform: `translate(${-sparkDeltaX - sparkShakeDelta}px, 0px)`},
    {transform: `translate(${-sparkDeltaX}px, 0px)`},
    {transform: `translate(${-sparkDeltaX + sparkShakeDelta}px, 0px)`},

    {transform: `translate(${-sparkDeltaX - sparkShakeDelta}px, ${sparkShakeDelta}px)`},
    {transform: `translate(${-sparkDeltaX}px, ${sparkShakeDelta}px)`},
    {transform: `translate(${-sparkDeltaX + sparkShakeDelta}px, ${sparkShakeDelta}px)`},
];

export const wispFightAnimationEffect: Array<SpriteAnimationType> = [
    // Sparks
    {
        animationIndexList: [
            {
                frameIndex: 4,
                keyframeAnimationOptions: {
                    duration: sparkShakeList.length,
                    easing: `steps(${sparkShakeList.length})`,
                },
                keyframeList: arrayShuffle(sparkShakeList),
            },
        ],
        randomPosition: {x: 10, y: 10},
        spriteList: [
            ...makeArray(emptySprite, 4),
            spriteSpark0,
            spriteSpark1,
            spriteSpark2,
            spriteSpark3,
            spriteSpark4,
            spriteSpark5,
            spriteSpark2,
            spriteSpark1,
            spriteSpark0,
            emptySprite,
        ],
        zIndex: 0,
    },
];
