/* global document, KeyframeAnimationOptions, Keyframe */
import {useState, useEffect, useCallback} from "react";
import {useNavigate} from "react-router-dom";

import {useGameContext} from "../../../../provider/game/game-context";
import {GamePopup} from "../../../../layout/game-popup/game-popup";
import {getIsCompaniesMap} from "../../../../model/map/map-helper";
import gamePopupStyle from "../../../../layout/game-popup/game-popup.scss";
import {Locale} from "../../../../provider/locale/locale-context";
import {UiButton} from "../../../../layout/ui-button/ui-button";
import {GameActionTypeEnum} from "../../../../provider/game/game-context-type";
import {useAudioPlayer} from "../../../../hook/audio-player-hook/audio-player-hook";
import {defeatSound, gameTheme, victorySound} from "../../../../media/play-media";
import type {AudioPlayerConfigType} from "../../../../hook/audio-player-hook/audio-player-type";
import {useSettingContext} from "../../../../provider/setting/setting-context";

interface CompaniesEndGamePopupPropsType {
    readonly setIsBlockToExit: (isBlock: boolean) => void;
}

export function CompaniesEndGamePopup(props: CompaniesEndGamePopupPropsType): JSX.Element | null {
    const {
        setting: {
            numberSetting: {animationStepTime},
        },
    } = useSettingContext();
    const {setIsBlockToExit} = props;
    const {gameState, animationList} = useGameContext();
    const {mapId} = gameState;
    const [isWin, setIsWin] = useState<boolean>(false);
    const [isOpen, setIsOpen] = useState<boolean>(false);
    const isCompanies = getIsCompaniesMap(mapId);
    const navigate = useNavigate();

    const [musicPlayerState, setMusicPlayerState] = useState<AudioPlayerConfigType>(gameTheme);
    const {play} = useAudioPlayer({...musicPlayerState});

    const first = animationList.at(0);

    // eslint-disable-next-line complexity
    useEffect(() => {
        if (!first) {
            return;
        }

        const {action} = first;

        if (action.actionType === GameActionTypeEnum.companiesForceDefeat) {
            setIsWin(false);
            setIsOpen(true);
            setIsBlockToExit(false);
            play();
            setMusicPlayerState(defeatSound);
        }

        if (action.actionType === GameActionTypeEnum.companiesForceWin) {
            setIsWin(true);
            setIsOpen(true);
            setIsBlockToExit(false);
            play();
            setMusicPlayerState(victorySound);
        }
    }, [first, play, setIsBlockToExit]);

    useEffect(() => {
        if (!first) {
            return;
        }

        const {animationOnFinish} = first;

        if (!isOpen) {
            return;
        }

        const moveTransformList: Array<Keyframe> = [{opacity: 0, transform: "translate3d(0%, 0, 0)"}];

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: animationStepTime,
            easing: "linear",
            fill: "forwards",
            iterations: 1,
        };

        const animation = document.createElement("div").animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [animationStepTime, first, isOpen]);

    const hidePopup = useCallback((): void => {
        navigate(-1);
    }, [navigate]);

    if (!isCompanies) {
        return null;
    }

    return (
        <GamePopup isOpen={isOpen} setIsOpen={hidePopup}>
            <h3 className={gamePopupStyle.game_popup__header}>
                <Locale stringKey={isWin ? "END_GAME__VICTORY" : "END_GAME__DEFEAT"} />
            </h3>

            <div className={gamePopupStyle.game_popup__footer}>
                <UiButton langKey="UI__OK" onClick={hidePopup} />
            </div>
        </GamePopup>
    );
}
