export function getNextPlayerId(
    activeGamePlayerId: GamePlayerIdEnum,
    playerList: Array<GamePlayerType>
): GamePlayerIdEnum {
    const currentPlayerIndex = playerList.findIndex((player: GamePlayerType): boolean => {
        return player.gamePlayerId === activeGamePlayerId;
    });

    if (currentPlayerIndex === -1) {
        throw new Error("currentPlayerIndex === -1");
    }

    if (currentPlayerIndex === playerList.length - 1) {
        return playerList[0].gamePlayerId;
    }

    return playerList[currentPlayerIndex + 1].gamePlayerId;
}
