import type {GameSnackbar} from "../../layout/game-snackbar/game-snackbar";
import type {GameFacedSnackbar} from "../../layout/game-faced-snackbar-list/game-faced-snackbar";

export enum SnackbarConstructorNameEnum {
    gameFacedSnackbar = "gameFacedSnackbar",
    gameSnackbar = "gameSnackbar",
}

export type SnackbarArgumentType =
    | {
          constructorName: SnackbarConstructorNameEnum.gameFacedSnackbar;
          elementProps: Parameters<typeof GameFacedSnackbar>[0];
          id: string;
      }
    | {
          constructorName: SnackbarConstructorNameEnum.gameSnackbar;
          elementProps: Parameters<typeof GameSnackbar>[0];
          id: string;
      };

export interface SnackbarContextType {
    snackbar: (data: SnackbarArgumentType) => void;
}
