import type {GameBuildingType} from "../../../../model/game/building/building-type";
import type {GamePlayerIdEnum} from "../../../../model/game/player/player-type";
import {getBuildingByPoint, getBuildingInfo} from "../../../../model/game/building/building-helper";
import type {GameUnitType, GameUnitTypeEnum} from "../../../../model/game/unit/unit-type";
import {getUnitByPoint, getUnitInfoByType, getUnitOverUnit} from "../../../../model/game/unit/unit-helper";
import type {PointType} from "../../../../model/game/point/point-type";
import {getRandomStringWithPrefix} from "../../../../util/string";
import {additionalCostOfTheHeroDead} from "../../../../model/game/unit/unit-const";
import type {SizeType} from "../../../../model/map/map-type";
import type {GameStateType} from "../../../../model/game/game-type";
import {getLandscapeSize} from "../../../../model/game/landscape/landscape-helper";

import type {UnitPriceItemType} from "./unit-store-type";

export function getIsHasPlaceToBuyUnit(point: PointType, unitList: Array<GameUnitType>, mapSize: SizeType): boolean {
    const {x, y} = point;
    const pointToPlaceUnitList: Array<PointType> = [
        {x, y},
        {x: x + 1, y},
        {x: x - 1, y},
        {x, y: y + 1},
        {x, y: y - 1},
    ];

    return pointToPlaceUnitList.some((pointToPlace: PointType): boolean => {
        if (pointToPlace.x < 0 || pointToPlace.y < 0) {
            return false;
        }

        if (pointToPlace.x >= mapSize.width || pointToPlace.y >= mapSize.height) {
            return false;
        }

        return !getUnitByPoint(pointToPlace, unitList);
    });
}

// eslint-disable-next-line complexity
export function getCanOpenStoreByPoint(point: PointType, gameState: GameStateType): boolean {
    const {unitList, activeGamePlayerId, landscape, buildingList} = gameState;
    const building: GameBuildingType | null = getBuildingByPoint(point, buildingList);

    if (!building) {
        return false;
    }

    if (!getBuildingInfo(building).canBeStore) {
        return false;
    }

    if (building.gamePlayerId !== activeGamePlayerId) {
        return false;
    }

    if (getUnitOverUnit(unitList)) {
        return false;
    }

    return getIsHasPlaceToBuyUnit(point, unitList, getLandscapeSize(landscape));
}

export function getDeadUnitCost(unitType: GameUnitTypeEnum, deathCount: number): number {
    const {cost} = getUnitInfoByType(unitType);

    // eslint-disable-next-line no-mixed-operators
    return cost + deathCount * additionalCostOfTheHeroDead;
}

export function getUnitPriceList(
    activeGamePlayerId: GamePlayerIdEnum,
    availableUnitTypeList: Array<GameUnitTypeEnum>,
    cemetery: Array<GameUnitType>
): Array<UnitPriceItemType> {
    const priceList: Array<UnitPriceItemType> = availableUnitTypeList.map<UnitPriceItemType>(
        (unitType: GameUnitTypeEnum): UnitPriceItemType => {
            const {cost} = getUnitInfoByType(unitType);

            return {
                cost,
                isHero: false,
                unitId: getRandomStringWithPrefix(unitType),
                unitType,
            };
        }
    );

    cemetery.forEach((deadHeroUnit: GameUnitType) => {
        if (deadHeroUnit.gamePlayerId !== activeGamePlayerId) {
            return;
        }

        const {unitId, unitType, deathCount} = deadHeroUnit;

        priceList.unshift({
            cost: getDeadUnitCost(unitType, deathCount),
            isHero: true,
            unitId,
            unitType,
        });
    });

    return priceList;
}
