import type {ActionArgumentType, GameAnimationType} from "../game-context-type";
import type {GameStateType} from "../../../model/game/game-type";
import {getPlayerByPlayerId} from "../../../model/game/player/player-helper";
import {getUnitByUnitId, getUnitInfoByType} from "../../../model/game/unit/unit-helper";
import type {GamePlayerType} from "../../../model/game/player/player-type";
import type {GameUnitType} from "../../../model/game/unit/unit-type";
import {defaultUnitData} from "../../../model/game/unit/unit-const";
import {getDeadUnitCost} from "../../../page/client/client-game/unit-store/unit-store-helper";
import {waitForNextFrame} from "../../../util/time";
import type {PromiseResolveType} from "../../../util/promise";

export async function addUnitListAction(data: ActionArgumentType): Promise<void> {
    const {action, setGameState, gameState} = data;
    const {addUnitList} = action;
    const {activeGamePlayerId} = gameState;

    if (addUnitList.length === 0) {
        setGameState((currentGameState: GameStateType): GameStateType => {
            return {
                ...currentGameState,
                unitList: [...currentGameState.unitList],
            };
        });
        await waitForNextFrame();
        return;
    }

    setGameState((currentGameState: GameStateType): GameStateType => {
        const telefragList: Array<[GameUnitType, GameUnitType]> = [];

        // eslint-disable-next-line unicorn/prefer-set-has
        const unitToRemoveList: Array<GameUnitType> = currentGameState.unitList.filter<GameUnitType>(
            (oldUnit: GameUnitType): oldUnit is GameUnitType => {
                const addedUnitOverOldUit: GameUnitType | undefined = addUnitList.find(
                    (addedUnit: GameUnitType): boolean => {
                        return addedUnit.x === oldUnit.x && addedUnit.y === oldUnit.y;
                    }
                );

                if (!addedUnitOverOldUit) {
                    return false;
                }

                const needToRemove: boolean = activeGamePlayerId !== addedUnitOverOldUit.gamePlayerId;

                if (needToRemove) {
                    telefragList.push([addedUnitOverOldUit, oldUnit]);
                }

                return needToRemove;
            }
        );

        const filteredForNewUnitList: Array<GameUnitType> = currentGameState.unitList.filter<GameUnitType>(
            (unit: GameUnitType): unit is GameUnitType => {
                return !unitToRemoveList.includes(unit);
            }
        );

        return {
            ...currentGameState,
            telefragList,
            unitList: [...filteredForNewUnitList, ...addUnitList],
        };
    });

    await waitForNextFrame();
}

// eslint-disable-next-line max-statements
export async function buyUnitListAction(data: ActionArgumentType): Promise<void> {
    const {action, gameState, setGameState, setAnimationList, removeAnimationByAction} = data;
    const {buyUnitType, toPoint, unitId} = action;
    const {activeGamePlayerId, playerList, unitList, cemetery} = gameState;
    const player = getPlayerByPlayerId(activeGamePlayerId, playerList);
    const updatedCemetery: Array<GameUnitType> = [...cemetery];

    if (!player) {
        console.error(`can not find player by id: ${activeGamePlayerId}`);
        return waitForNextFrame();
    }

    // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
    if (!buyUnitType) {
        console.error("buyUnitType is null");
        return waitForNextFrame();
    }

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>): Array<GameAnimationType> => {
            return [
                ...animList,
                {
                    action,
                    // eslint-disable-next-line @typescript-eslint/no-misused-promises
                    animationOnFinish: async (): Promise<void> => {
                        const unitInfo = getUnitInfoByType(buyUnitType);
                        const deadUnit: GameUnitType | null = getUnitByUnitId(unitId, updatedCemetery);
                        const endUnitCost = deadUnit
                            ? getDeadUnitCost(deadUnit.unitType, deadUnit.deathCount)
                            : unitInfo.cost;
                        const updatedPlayerList: Array<GamePlayerType> = [...playerList];

                        updatedPlayerList[updatedPlayerList.indexOf(player)] = {
                            ...player,
                            money: player.money - endUnitCost,
                        };

                        const updatedUnit: GameUnitType = {
                            ...defaultUnitData,
                            ...toPoint,
                            gamePlayerId: player.gamePlayerId,
                            unitId,
                            unitType: buyUnitType,
                        };

                        if (deadUnit) {
                            updatedCemetery.splice(updatedCemetery.indexOf(deadUnit), 1);
                            updatedUnit.damageGiven = deadUnit.damageGiven;
                            updatedUnit.damageReceived = deadUnit.damageReceived;
                            updatedUnit.deathCount = deadUnit.deathCount;
                        }

                        const updatedUnitList: Array<GameUnitType> = [...unitList, updatedUnit];

                        setGameState((currentGameState: GameStateType): GameStateType => {
                            return {
                                ...currentGameState,
                                cemetery: updatedCemetery,
                                playerList: updatedPlayerList,
                                unitList: updatedUnitList,
                            };
                        });

                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
