import {unitImage} from "../../client-game/client-game-unit/image/unit-image";
import {GamePlayerColorIdEnum} from "../../../../model/game/player/player-type";
import {GameUnitTypeEnum} from "../../../../model/game/unit/unit-type";
import {cls} from "../../../../util/css";
import {IsRender} from "../../../../layout/is-render/is-render";

// eslint-disable-next-line id-length
import clientSkirmishMapSectionHeaderStyle from "./client-skirmish-map-section-header.scss";

const {demonLord, galamar, saeth, valadorn} = GameUnitTypeEnum;

interface ClientSkirmishMapSectionHeaderPropsType {
    readonly count: 2 | 3 | 4;
}

export function ClientSkirmishMapSectionHeader(props: ClientSkirmishMapSectionHeaderPropsType): JSX.Element {
    const {count} = props;

    return (
        <div
            className={cls(
                clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header,
                [3, 4].includes(count)
                    ? clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header_next
                    : ""
            )}
        >
            <IsRender isRender={count === 2}>
                <img
                    alt={galamar}
                    className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image}
                    src={unitImage[galamar][GamePlayerColorIdEnum.blue][0].src}
                />
                <img
                    alt={valadorn}
                    className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image}
                    src={unitImage[valadorn][GamePlayerColorIdEnum.red][0].src}
                />
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
            </IsRender>
            <IsRender isRender={count === 3}>
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
                <img
                    alt={demonLord}
                    className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image}
                    src={unitImage[demonLord][GamePlayerColorIdEnum.green][0].src}
                />
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
            </IsRender>
            <IsRender isRender={count === 4}>
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
                <div className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image} />
                <img
                    alt={saeth}
                    className={clientSkirmishMapSectionHeaderStyle.client_skirmish_map_section_header__image}
                    src={unitImage[saeth][GamePlayerColorIdEnum.black][0].src}
                />
            </IsRender>
        </div>
    );
}
