import {useCallback} from "react";
import {useNavigate} from "react-router-dom";

import {cls} from "../../../util/css";
import type {LangKeyType} from "../../../provider/locale/translation/type";
import {Locale} from "../../../provider/locale/locale-context";
import {usePlayClick} from "../../../media/play-media";

import pageHeaderStyle from "./page-header.scss";

interface PageHeaderType {
    // eslint-disable-next-line unicorn/no-keyword-prefix
    readonly className?: string;
    readonly langKey: LangKeyType;
    readonly onBackButtonClick?: () => void;
}

export function PageHeader(props: PageHeaderType): JSX.Element {
    const navigate = useNavigate();
    // eslint-disable-next-line unicorn/no-keyword-prefix
    const {className, langKey, onBackButtonClick} = props;
    const {play: playClick} = usePlayClick();

    const handleBackButtonClick = useCallback(() => {
        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        playClick();
        if (onBackButtonClick) {
            onBackButtonClick();
            return;
        }
        navigate(-1);
    }, [playClick, navigate, onBackButtonClick]);

    return (
        <header className={cls(pageHeaderStyle.page_header, className)}>
            <button className={pageHeaderStyle.page_header__back_button} onClick={handleBackButtonClick} type="button">
                &lt;&lt;
            </button>
            <span className={pageHeaderStyle.page_header__text}>
                <Locale stringKey={langKey} />
            </span>
        </header>
    );
}
