export enum GameUnitTypeEnum {
    archer = "archer",
    catapult = "catapult",
    crystal = "crystal",
    demonLord = "demon-lord",
    direWolf = "dire-wolf",
    dragon = "dragon",
    elemental = "elemental",
    galamar = "galamar",
    golem = "golem",
    saeth = "saeth",
    saethHeavensFury = "saeth-heavens-fury",
    skeleton = "skeleton",
    soldier = "soldier",
    sorceress = "sorceress",
    valadorn = "valadorn",
    wisp = "wisp",
}

export enum GameUnitMoveTypeEnum {
    float = "float",
    fly = "fly",
    walk = "walk",
}

export interface GameUnitType {
    damageGiven: number;
    damageReceived: number;
    deathCount: number;
    didAttackBuilding: boolean;
    didAttackUnit: boolean;
    didFixBuilding: boolean;
    didMove: boolean;
    didOccupyBuilding: boolean;
    didRaiseSkeleton: boolean;
    gamePlayerId: GamePlayerIdEnum;
    health: number;
    poisonTurnCount: number;
    unitId: string;
    unitType: GameUnitTypeEnum;
    x: number;
    y: number;
}

export interface GameGraveType {
    createdStepCount: number;
    x: number;
    y: number;
}

