/* global document, HTMLDivElement, Animation, KeyframeAnimationOptions, Keyframe */

import {type ReactNode, type MutableRefObject, useCallback, useEffect, useRef, useState} from "react";
import {createPortal} from "react-dom";

import {UiButton} from "../ui-button/ui-button";
import {cls} from "../../util/css";
import {useSettingContext} from "../../provider/setting/setting-context";

import gameSnackbarStyle from "./game-snackbar.scss";
import {snackbarUiImage} from "./image/sknackbar-image";

export interface GameSnackbarPropsType {
    readonly children: ReactNode;
    readonly hasError?: boolean;
    readonly isOpen: boolean;
    readonly setIsOpen: (isOpen: boolean) => void;
    readonly useClosingOverlay: boolean;
}

// eslint-disable-next-line sonarjs/cognitive-complexity
export function GameSnackbar(props: GameSnackbarPropsType): JSX.Element | null {
    const {
        setting: {
            numberSetting: {animationStepTime},
        },
    } = useSettingContext();
    const {children, isOpen, setIsOpen, hasError, useClosingOverlay} = props;
    const [isViewOpen, setIsViewOpen] = useState<boolean>(false);
    const contentNode = useRef<HTMLDivElement>(document.createElement("div"));
    const progressBarNode = useRef<HTMLDivElement | null>(document.createElement("div"));
    const contentShowAnimationRef: MutableRefObject<Animation | null> = useRef<Animation | null>(null);

    const handleShowOff = useCallback(() => {
        setIsViewOpen(false);
    }, []);

    const handleShowOn = useCallback(() => {
        setIsViewOpen(true);
    }, []);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        const snackbarAnimationOptions: KeyframeAnimationOptions = {
            duration: animationStepTime * 2,
            easing: "ease-in-out",
            fill: "forwards",
            iterations: 1,
        };

        const contentKeyframeList: Array<Keyframe> = [
            {opacity: 0, transform: "translate3d(-50%, 200%, 0)"},
            {opacity: 1, transform: "translate3d(-50%, 0, 0)"},
        ];

        const isOpening: boolean = isOpen && !isViewOpen;
        const isClosing: boolean = !isOpen && isViewOpen;

        if (isOpening) {
            handleShowOn();

            contentShowAnimationRef.current?.cancel();

            contentShowAnimationRef.current = contentNode.current.animate(
                contentKeyframeList,
                snackbarAnimationOptions
            );

            const progressBarAnimationOptions: KeyframeAnimationOptions = {
                delay: animationStepTime * 2,
                duration: animationStepTime * 10,
                easing: "linear",
                fill: "forwards",
                iterations: 1,
            };

            const progressBarKeyframeList: Array<Keyframe> = [{transform: "scaleX(1)"}, {transform: "scaleX(0)"}];

            if (progressBarNode.current) {
                progressBarNode.current.animate(progressBarKeyframeList, progressBarAnimationOptions).onfinish =
                    (): undefined => {
                        setIsOpen(false);
                    };
            }
            return;
        }

        if (isClosing) {
            if (contentShowAnimationRef.current) {
                if (contentShowAnimationRef.current.playbackRate > 0) {
                    contentShowAnimationRef.current.reverse();
                    contentShowAnimationRef.current.onfinish = handleShowOff;
                }
                return;
            }
            console.error("[Game Snackbar]: NO content animation");
            handleShowOff();
        }
    }, [animationStepTime, setIsOpen, isOpen, isViewOpen, handleShowOn, handleShowOff]);

    if (typeof document === "undefined") {
        return null;
    }

    if (!isOpen && !isViewOpen) {
        return null;
    }

    return createPortal(
        <>
            {useClosingOverlay ? (
                /* eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */
                <div
                    className={gameSnackbarStyle.game_snackbar__overlay}
                    onClick={(): undefined => {
                        setIsOpen(false);
                    }}
                />
            ) : null}
            <div
                className={cls(gameSnackbarStyle.game_snackbar__content, {
                    [gameSnackbarStyle.game_snackbar__content__error]: Boolean(hasError),
                })}
                ref={contentNode}
            >
                <div className={gameSnackbarStyle.game_snackbar__children}>{children}</div>
                <div className={gameSnackbarStyle.game_snackbar__progress_bar} ref={progressBarNode} />
                <UiButton
                    iconSrc={snackbarUiImage.close.src}
                    isSquared
                    onClick={(): undefined => {
                        setIsOpen(false);
                    }}
                />
            </div>
        </>,
        document.body
    );
}
