import type {PointType} from "../point/point-type";
import type {GameGraveType} from "../unit/unit-type";
import {getIsEqualPoint} from "../point/point-helper";

export function getGraveByPoint(point: PointType, graveList: Array<GameGraveType>): GameGraveType | null {
    return (
        graveList.find<GameGraveType>((grave: GameGraveType): grave is GameGraveType => {
            return grave.x === point.x && grave.y === point.y;
        }) ?? null
    );
}

export function removeGraveByPoint(point: PointType, graveList: Array<GameGraveType>): Array<GameGraveType> {
    return graveList.filter<GameGraveType>((graveInList: GameGraveType): graveInList is GameGraveType => {
        return !getIsEqualPoint(point, {x: graveInList.x, y: graveInList.y});
    });
}

export function removeGraveByPointProcedure(point: PointType, graveList: Array<GameGraveType>): undefined {
    const filteredList: Array<GameGraveType> = removeGraveByPoint(point, graveList);

    graveList.splice(0);

    graveList.push(...filteredList);
}
