import type {ActionArgumentType, GameAnimationType} from "../game-context-type";
import {type GameGraveType, type GameUnitType, GameUnitTypeEnum} from "../../../model/game/unit/unit-type";
import type {GameStateType} from "../../../model/game/game-type";
import {defaultUnitData} from "../../../model/game/unit/unit-const";
import {getUnitByPoint} from "../../../model/game/unit/unit-helper";
import {getGraveByPoint, removeGraveByPointProcedure} from "../../../model/game/grave/grave-helper";
import {waitForNextFrame} from "../../../util/time";
import type {PromiseResolveType} from "../../../util/promise";
import {getHash} from "../../../util/string";

// eslint-disable-next-line sonarjs/cognitive-complexity
export async function raiseSkeletonAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState} = data;
    const sorceress: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, gameState.unitList);
    const grave: GameGraveType | null = getGraveByPoint(action.toPoint, gameState.graveList);

    if (!sorceress || !grave) {
        return waitForNextFrame();
    }

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>): Array<GameAnimationType> => {
            return [
                ...animList,
                {
                    action,
                    // eslint-disable-next-line @typescript-eslint/no-misused-promises
                    animationOnFinish: async (): Promise<void> => {
                        setGameState((currentGameState: GameStateType): GameStateType => {
                            const updatedUnitList = [...currentGameState.unitList];
                            const updatedGraveList = [...currentGameState.graveList];

                            const unitInListToUpdate: GameUnitType | null = getUnitByPoint(
                                {
                                    x: sorceress.x,
                                    y: sorceress.y,
                                },
                                updatedUnitList
                            );

                            if (unitInListToUpdate) {
                                updatedUnitList[updatedUnitList.indexOf(unitInListToUpdate)] = {
                                    ...unitInListToUpdate,
                                    didRaiseSkeleton: true,
                                };
                            }

                            removeGraveByPointProcedure({x: grave.x, y: grave.y}, updatedGraveList);

                            if (unitInListToUpdate) {
                                updatedUnitList.push({
                                    ...defaultUnitData,
                                    didAttackUnit: true,
                                    didMove: true,
                                    gamePlayerId: unitInListToUpdate.gamePlayerId,
                                    unitId: [
                                        getHash(gameState),
                                        getHash(gameState.unitList),
                                        getHash(gameState.graveList),
                                        getHash(gameState.cemetery),
                                        gameState.stepCount,
                                    ].join("-"),
                                    unitType: GameUnitTypeEnum.skeleton,
                                    x: grave.x,
                                    y: grave.y,
                                });
                            }

                            const updatedGameState: GameStateType = {
                                ...currentGameState,
                                graveList: updatedGraveList,
                                unitList: updatedUnitList,
                            };

                            return updatedGameState;
                        });
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
