import type {GameMapType} from "../../../model/map/map-type";
import type {LocaleNameEnum} from "../../../provider/locale/locale-context-type";
import type {LangKeyType} from "../../../provider/locale/translation/type";
import {sortCompareAsc} from "../../../util/string";
import {getMaxPlayerByMap} from "../../../model/map/map-helper";

// eslint-disable-next-line @typescript-eslint/max-params
export function mapNameSortCallback(
    mapA: GameMapType,
    mapB: GameMapType,
    localeName: LocaleNameEnum,
    getLocalizedString: (stringKey: LangKeyType) => string
): number {
    const mapLocalizedNameA: string = getLocalizedString(mapA.mapNameLangKey).trim() || mapA.mapName.trim();
    const mapLocalizedNameB: string = getLocalizedString(mapB.mapNameLangKey).trim() || mapB.mapName.trim();

    return sortCompareAsc(localeName, mapLocalizedNameA, mapLocalizedNameB);
}

type SeparatedMapType = Record<"four" | "three" | "two", Array<GameMapType>>;

export function getSeparatedMapByPlayerCount(mapList: Array<GameMapType>): SeparatedMapType {
    const result: SeparatedMapType = {
        four: [],
        three: [],
        two: [],
    };

    mapList.forEach((map: GameMapType) => {
        const playerCount: number = getMaxPlayerByMap(map);

        switch (playerCount) {
            case 2: {
                result.two.push(map);
                return;
            }
            case 3: {
                result.three.push(map);
                return;
            }
            case 4: {
                result.four.push(map);
                return;
            }
            default: {
                console.error(`can not detect max player count: ${playerCount}`);
            }
        }
    });

    return result;
}
