import {createContext, type ReactNode, useMemo, useState, useContext, useCallback} from "react";

import {GameSnackbar} from "../../layout/game-snackbar/game-snackbar";
import {GameFacedSnackbar} from "../../layout/game-faced-snackbar-list/game-faced-snackbar";
import {NeverError} from "../../util/error";

import {type SnackbarArgumentType, SnackbarConstructorNameEnum, type SnackbarContextType} from "./snackbar-type";
import {defaultSnackbarContextData} from "./snackbar-const";

export const SnackbarContext = createContext<SnackbarContextType>(defaultSnackbarContextData);

export function useSnackbarContext(): SnackbarContextType {
    return useContext<SnackbarContextType>(SnackbarContext);
}

const {Provider: SnackbarContextProvider} = SnackbarContext;

interface SnackbarContextPropsType {
    readonly children: ReactNode;
}

export function SnackbarProvider(props: SnackbarContextPropsType): JSX.Element {
    const [snackbarDataList, setSnackbarDataList] = useState<Array<SnackbarArgumentType>>([]);
    const {children} = props;
    const closeGameSnackbarById = useCallback((snackBarId: string) => {
        setSnackbarDataList((currentSnackbarDataList: Array<SnackbarArgumentType>): Array<SnackbarArgumentType> => {
            const updatedSnackbarDataList: Array<SnackbarArgumentType> = [...currentSnackbarDataList];

            updatedSnackbarDataList.forEach((snackbarDataInList: SnackbarArgumentType) => {
                if (snackbarDataInList.id === snackBarId) {
                    // eslint-disable-next-line no-param-reassign
                    snackbarDataInList.elementProps = {
                        ...snackbarDataInList.elementProps,
                        isOpen: false,
                    };
                }
            });

            return updatedSnackbarDataList;
        });
    }, []);

    const showSnackbar = useCallback((data: SnackbarArgumentType) => {
        setSnackbarDataList((currentSnackbarDataList: Array<SnackbarArgumentType>): Array<SnackbarArgumentType> => {
            const updatedSnackbarDataList: Array<SnackbarArgumentType> = [...currentSnackbarDataList];

            const updatedSnackbarDataListFiltered: Array<SnackbarArgumentType> =
                updatedSnackbarDataList.filter<SnackbarArgumentType>(
                    (snackbarDataInList: SnackbarArgumentType): snackbarDataInList is SnackbarArgumentType => {
                        return snackbarDataInList.id !== data.id;
                    }
                );

            updatedSnackbarDataListFiltered.push({...data});

            return updatedSnackbarDataListFiltered;
        });
    }, []);

    const providedData: SnackbarContextType = useMemo(() => {
        return {
            snackbar: showSnackbar,
        };
    }, [showSnackbar]);

    return (
        <SnackbarContextProvider value={providedData}>
            {children}
            {snackbarDataList.map((snackbarData: SnackbarArgumentType): JSX.Element => {
                const {constructorName, elementProps, id} = snackbarData;

                switch (constructorName) {
                    case SnackbarConstructorNameEnum.gameSnackbar: {
                        return (
                            <GameSnackbar
                                hasError={Boolean(elementProps.hasError)}
                                isOpen={elementProps.isOpen}
                                key={id}
                                setIsOpen={(): undefined => {
                                    closeGameSnackbarById(id);
                                }}
                                useClosingOverlay={elementProps.useClosingOverlay}
                            >
                                {elementProps.children}
                            </GameSnackbar>
                        );
                    }
                    case SnackbarConstructorNameEnum.gameFacedSnackbar: {
                        return (
                            <GameFacedSnackbar
                                dataList={elementProps.dataList}
                                isOpen={elementProps.isOpen}
                                key={id}
                                // eslint-disable-next-line react/jsx-handler-names
                                onHideLastItem={elementProps.onHideLastItem}
                                refId={elementProps.refId}
                            />
                        );
                    }
                    default: {
                        throw new NeverError(constructorName);
                    }
                }
            })}
        </SnackbarContextProvider>
    );
}
