/* global localStorage */
import type {RateAppSavedStateType} from "./rate-app-type";
import {defaultRateAppSavedState} from "./rate-app-const";

const rateAppSavedStateStorageKey = "rate-app-saved-state-storage-key-v.1.0.0";

// eslint-disable-next-line complexity
export function getRateAppSavedState(): RateAppSavedStateType {
    if (typeof localStorage === "undefined") {
        return {...defaultRateAppSavedState};
    }

    const rawSavedState = localStorage.getItem(rateAppSavedStateStorageKey);

    // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
    if (!rawSavedState) {
        return {...defaultRateAppSavedState};
    }

    try {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
        const savedData: RateAppSavedStateType = JSON.parse(rawSavedState);

        const result: RateAppSavedStateType = {
            firstRunDate: savedData.firstRunDate || defaultRateAppSavedState.firstRunDate,
            lastShowPopupDate: savedData.lastShowPopupDate || defaultRateAppSavedState.lastShowPopupDate,
        };

        return result;
    } catch (error: unknown) {
        console.log(error);
        return {...defaultRateAppSavedState};
    }
}

export function setRateAppSavedState(data: Partial<RateAppSavedStateType>): void {
    if (typeof localStorage === "undefined") {
        return;
    }

    const savedState: RateAppSavedStateType = getRateAppSavedState();
    const updatedState: RateAppSavedStateType = {
        ...savedState,
        ...data,
    };

    localStorage.setItem(rateAppSavedStateStorageKey, JSON.stringify(updatedState));
}

// eslint-disable-next-line jest/require-hook
(function rateAppSavedStateInit(): undefined {
    const savedState: RateAppSavedStateType = getRateAppSavedState();

    if (!savedState.firstRunDate) {
        setRateAppSavedState({firstRunDate: new Date().toISOString()});
    }
})();
