import type {GamePlayerIdEnum, GamePlayerType} from "../player/player-type";
import type {PointType} from "../point/point-type";
import {getTeamIdByPlayerId} from "../player/player-helper";
import {getIsEqualPoint} from "../point/point-helper";
import {type GameBuildingType, GameBuildingStateEnum} from "../building/building-type";
import {getBuildingByPoint, getBuildingInfo} from "../building/building-helper";
import type {SizeType} from "../../map/map-type";

import type {GameUnitType} from "./unit-type";
import {getMaxSquareByMoveSize} from "./unit-path";
import {addHealthToUnit, getUnitInfoByUnit} from "./unit-helper";

export function getIsUnitPoisoned(unit: GameUnitType, currentGameTurnCount: number): boolean {
    const {canBePoisoned} = getUnitInfoByUnit(unit);

    if (canBePoisoned === false) {
        return false;
    }

    return unit.poisonTurnCount > currentGameTurnCount;
}

// eslint-disable-next-line @typescript-eslint/max-params
export function getIsUnitUnderWispAura(
    unit: GameUnitType,
    playerList: Array<GamePlayerType>,
    unitList: Array<GameUnitType>,
    mapSize: SizeType
): boolean {
    const {isWispAuraAccept} = getUnitInfoByUnit(unit);

    if (isWispAuraAccept === false) {
        return false;
    }

    const wispLikeAllyUnitList: Array<GameUnitType> = unitList.filter<GameUnitType>(
        (unitInList: GameUnitType): unitInList is GameUnitType => {
            return (
                getTeamIdByPlayerId(unit.gamePlayerId, playerList) ===
                    getTeamIdByPlayerId(unitInList.gamePlayerId, playerList) &&
                getUnitInfoByUnit(unitInList).wispAuraRange > 0
            );
        }
    );

    const wispAuraPointList: Array<PointType> = [];

    wispLikeAllyUnitList.forEach((wispLikeUnit: GameUnitType) => {
        wispAuraPointList.push(
            ...getMaxSquareByMoveSize(
                {x: wispLikeUnit.x, y: wispLikeUnit.y},
                mapSize,
                getUnitInfoByUnit(wispLikeUnit).wispAuraRange
            )
        );
    });

    return wispAuraPointList.some((point: PointType): boolean => {
        return getIsEqualPoint(point, {x: unit.x, y: unit.y});
    });
}

// eslint-disable-next-line complexity, @typescript-eslint/max-params
export function getSwitchTurnUnitState(
    activeGamePlayerId: GamePlayerIdEnum,
    unit: GameUnitType,
    buildingList: Array<GameBuildingType>,
    playerList: Array<GamePlayerType>
): GameUnitType {
    // Find building to health
    const unitSwitchTurnByDefault: GameUnitType = {
        ...unit,
        didAttackBuilding: false,
        didAttackUnit: false,
        didFixBuilding: false,
        didMove: false,
        didOccupyBuilding: false,
        didRaiseSkeleton: false,
    };

    if (unit.gamePlayerId !== activeGamePlayerId) {
        return unitSwitchTurnByDefault;
    }

    const unitPoint: PointType = {x: unit.x, y: unit.y};

    const building = getBuildingByPoint(unitPoint, buildingList);

    if (!building) {
        return unitSwitchTurnByDefault;
    }

    if (building.state !== GameBuildingStateEnum.buildingOk) {
        return unitSwitchTurnByDefault;
    }

    const buildingInfo = getBuildingInfo(building);

    if (
        buildingInfo.canHealthAll ||
        getTeamIdByPlayerId(building.gamePlayerId, playerList) === getTeamIdByPlayerId(unit.gamePlayerId, playerList)
    ) {
        unitSwitchTurnByDefault.health = addHealthToUnit(unit.health, buildingInfo.healthBonus);
    }

    return unitSwitchTurnByDefault;
}
