/* global Keyframe */
/* eslint-disable sonarjs/no-duplicate-string */

const groundShakePercent = 0.5;
const groundScalePercent = 1.01;

export const groundShakeList: Array<Keyframe> = [
    {transform: `scale(${groundScalePercent}) translate(-${groundShakePercent}%, -${groundShakePercent}%)`},
    {transform: `scale(${groundScalePercent}) translate(-${groundShakePercent}%, 0%)`},
    {transform: `scale(${groundScalePercent}) translate(-${groundShakePercent}%, ${groundShakePercent}%)`},
    {transform: `scale(${groundScalePercent}) translate(0%, -${groundShakePercent}%)`},
    // {transform: `scale(${groundScalePercent}) translate(0%, 0%)`},
    {transform: `scale(${groundScalePercent}) translate(0%, ${groundShakePercent}%)`},
    {transform: `scale(${groundScalePercent}) translate(${groundShakePercent}%, -${groundShakePercent}%)`},
    {transform: `scale(${groundScalePercent}) translate(${groundShakePercent}%, 0%)`},
    {transform: `scale(${groundScalePercent}) translate(${groundShakePercent}%, ${groundShakePercent}%)`},
];

// eslint-disable-next-line id-length
export const unitFightAnimationOverlayKeyframeList: Array<Keyframe> = [{opacity: 0}, {opacity: 1}];

// eslint-disable-next-line id-length
export const unitFightAnimationWrapperFadeOutKeyframeList: Array<Keyframe> = [
    {opacity: 0, transform: "translate3d(0, 110%, 0)"},
    {opacity: 1, transform: "translate3d(0, 0, 0)"},
];
