import type {
    GameUnitFightAnimationDataSourceType,
    GameUnitFightAnimationDataType,
    SpriteAnimationSourceType,
    SpriteAnimationType,
    SpriteIndexSourceType,
    SpriteType,
} from "../animation-view/animation-type";
import type {GameUnitTypeEnum} from "../../model/game/unit/unit-type";
import {GamePlayerColorIdEnum} from "../../model/game/player/player-type";

import {fightSpriteMap} from "./fight-sprite-map";

const {black, red, blue, green, gray} = GamePlayerColorIdEnum;

function getSpriteAnimation(
    spriteAnimationSource: SpriteAnimationSourceType,
    unitType: GameUnitTypeEnum,
    color: GamePlayerColorIdEnum
): SpriteAnimationType {
    const {animationIndexList, spriteIndexList, zIndex} = spriteAnimationSource;

    return {
        animationIndexList,
        randomPosition: {x: 0, y: 0},
        spriteList: spriteIndexList.map<SpriteType>((spriteSourceIndex: SpriteIndexSourceType): SpriteType => {
            return fightSpriteMap[unitType][color][spriteSourceIndex];
        }),
        zIndex,
    };
}

function getSpriteAnimationList(
    animationSourceList: Array<SpriteAnimationSourceType>,
    unitType: GameUnitTypeEnum,
    color: GamePlayerColorIdEnum
): Array<SpriteAnimationType> {
    return animationSourceList.map<SpriteAnimationType>(
        (animationSource: SpriteAnimationSourceType): SpriteAnimationType => {
            return getSpriteAnimation(animationSource, unitType, color);
        }
    );
}

// eslint-disable-next-line @typescript-eslint/max-params
function getColorUnitAnimation(
    gameUnitFightAnimationDataSource: GameUnitFightAnimationDataSourceType,
    unitType: GameUnitTypeEnum,
    color: GamePlayerColorIdEnum,
    effectAnimation: Array<SpriteAnimationType>
): GameUnitFightAnimationDataType {
    const {attackStartUpTime, unitPositionList, attackRecovery, attackStart, attackActive, idle, unitPositionRandom} =
        gameUnitFightAnimationDataSource;

    return {
        attackActive: getSpriteAnimationList(attackActive, unitType, color),
        attackRecovery: getSpriteAnimationList(attackRecovery, unitType, color),
        attackStart: getSpriteAnimationList(attackStart, unitType, color),
        attackStartUpTime,
        effect: effectAnimation,
        idle: getSpriteAnimationList(idle, unitType, color),
        unitPositionList,
        unitPositionRandom,
    };
}

export function getFullColorUnitAnimation(
    gameUnitFightAnimationDataSource: GameUnitFightAnimationDataSourceType,
    unitType: GameUnitTypeEnum,
    effectAnimation: Array<SpriteAnimationType>
): Record<GamePlayerColorIdEnum, GameUnitFightAnimationDataType> {
    return {
        [black]: getColorUnitAnimation(gameUnitFightAnimationDataSource, unitType, black, effectAnimation),
        [blue]: getColorUnitAnimation(gameUnitFightAnimationDataSource, unitType, blue, effectAnimation),
        [gray]: getColorUnitAnimation(gameUnitFightAnimationDataSource, unitType, gray, effectAnimation),
        [green]: getColorUnitAnimation(gameUnitFightAnimationDataSource, unitType, green, effectAnimation),
        [red]: getColorUnitAnimation(gameUnitFightAnimationDataSource, unitType, red, effectAnimation),
    };
}
