/* eslint-disable multiline-comment-style, capitalized-comments, line-comment-position, multiline-comment-style */
import type {ActionArgumentType, GameAnimationType} from "../game-context-type";
import {getNextPlayerId} from "../game-context-helper";
import type {GameStateType} from "../../../model/game/game-type";
import type {GameGraveType, GameUnitType} from "../../../model/game/unit/unit-type";
import {graveLiveCount} from "../../../page/client/client-game/client-game-grave/client-game-grave-helper";
import {getSwitchTurnUnitState} from "../../../model/game/unit/unit-state";
import type {GamePlayerIdEnum, GamePlayerType} from "../../../model/game/player/player-type";
import {getPlayerByPlayerId, getPlayerTurnIncome} from "../../../model/game/player/player-helper";
import type {PromiseResolveType} from "../../../util/promise";
import {waitForNextFrame} from "../../../util/time";

export async function changeTurnAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState} = data;
    const {playerList, activeGamePlayerId, buildingList} = gameState;
    const updatedPlayerList: Array<GamePlayerType> = [...playerList];

    const nextActiveGamePlayerId: GamePlayerIdEnum = getNextPlayerId(activeGamePlayerId, updatedPlayerList);
    const nextPlayer: GamePlayerType | null = getPlayerByPlayerId(nextActiveGamePlayerId, updatedPlayerList);

    if (!nextPlayer) {
        console.error("cen not find next player");
        console.error(activeGamePlayerId, "->", nextActiveGamePlayerId);
        return waitForNextFrame();
    }

    nextPlayer.money += getPlayerTurnIncome(nextActiveGamePlayerId, buildingList);

    // count additional healths for unit
    // count additional money for player
    // and set it into state
    setGameState((currentGameState: GameStateType): GameStateType => {
        const {stepCount, graveList, unitList} = currentGameState;
        const updatedStepCount = stepCount + 1;

        return {
            ...currentGameState,
            activeGamePlayerId: nextActiveGamePlayerId,
            graveList: graveList.filter<GameGraveType>((grave: GameGraveType): grave is GameGraveType => {
                return grave.createdStepCount + graveLiveCount > updatedStepCount;
            }),
            playerList: updatedPlayerList,
            stepCount: updatedStepCount,
            unitList: unitList.map<GameUnitType>((unit: GameUnitType): GameUnitType => {
                return getSwitchTurnUnitState(nextActiveGamePlayerId, unit, buildingList, playerList);
            }),
        };
    });

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>): Array<GameAnimationType> => {
            return [
                ...animList,
                {
                    action,
                    // eslint-disable-next-line @typescript-eslint/no-misused-promises
                    animationOnFinish: async (): Promise<void> => {
                        await waitForNextFrame();
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
