/* eslint-disable multiline-comment-style, capitalized-comments, line-comment-position, multiline-comment-style */

/* global document, HTMLDivElement, Keyframe, KeyframeAnimationOptions */

import {type MutableRefObject, useEffect, useMemo, useRef, useState} from "react";
import {createPortal} from "react-dom";
import {useScreenSize} from "react-system-hook";

import {GameActionTypeEnum, type GameAnimationType} from "../../../../provider/game/game-context-type";
import type {GameUnitType} from "../../../../model/game/unit/unit-type";
import {getUnitByPoint} from "../../../../model/game/unit/unit-helper";
import {getUnitToUnitAttackResult} from "../../../../model/game/unit/unit-to-unit-attack";
import {getRandomStringWithPrefix} from "../../../../util/string";
import {useGameContext} from "../../../../provider/game/game-context";
import {Page} from "../../../../layout/page/page";
import {useSettingContext} from "../../../../provider/setting/setting-context";
import {UnitFightAnimation} from "../../../../layout/unit-fight-animation/unit-fight-animation";
import {waitForAnimateTime, waitForNextFrame} from "../../../../util/time";
import {getPlayerColorIdByPlayerId} from "../../../../model/game/player/player-helper";
import {
    getFightAnimation,
    getFightAnimationScale,
    getMaxSizeAnimationSpiteList,
    getNeedShowFightAnimation,
} from "../../../../layout/unit-fight-animation/unit-fight-animation-helper";
import {useUnitFightAnimation} from "../../../../layout/unit-fight-animation/use-unit-fight-animation";
import {fightAnimationViewSize} from "../../../../layout/unit-fight-animation/unit-fight-animation-const";
import type {SizeType} from "../../../../model/map/map-type";
import {arrayShuffle} from "../../../../util/array";
import {defaultUnitData} from "../../../../model/game/unit/unit-const";
import gamePopupStyle from "../../../../layout/game-popup/game-popup.scss";
import {getAudioByRow} from "../../../../hook/audio-player-hook/audio-player-helper";
import {AudioPlayerAudioRowEnum} from "../../../../hook/audio-player-hook/audio-player-type";
import {usePlayFightTheme} from "../../../../media/play-media";

import {ClientGameUnitFightField} from "./client-game-unit-fight-field/client-game-unit-fight-field";
import clientGameUnitFightStyle from "./client-game-unit-fight.scss";
import {
    groundShakeList,
    unitFightAnimationOverlayKeyframeList,
    unitFightAnimationWrapperFadeOutKeyframeList,
} from "./client-game-unit-fight-const";
import {ClientGameUnitFightBottomBar} from "./client-game-unit-fight-bottom-bar/client-game-unit-fight-bottom-bar";

// eslint-disable-next-line complexity, sonarjs/cognitive-complexity, max-statements
export function ClientGameUnitFight(): JSX.Element | null {
    const {gameState, animationList, setLocalAnimationList} = useGameContext();
    const screenSize: SizeType = useScreenSize();
    const leftGroundRef: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(document.createElement("div"));
    const rightGroundRef: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(document.createElement("div"));
    const fightWrapperRef: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(document.createElement("div"));
    const [first, setFirst] = useState<GameAnimationType | null>(null);
    const [isShow, setIsShow] = useState<boolean>(false);
    const {
        setting: {
            booleanSetting: {isUnitFightAnimationEnable, isHitUnitAnimationEnable},
            numberSetting: {fightAnimationFrameTime, animationStepTime},
        },
    } = useSettingContext();
    const [aggressorUnitToAnimate, setAggressorUnitToAnimate] = useState<GameUnitType | null>(null);
    const [defenderUnitToAnimate, setDefenderUnitToAnimate] = useState<GameUnitType | null>(null);
    const [isAggressorAnimationIdle, setIsAggressorAnimationIdle] = useState<boolean>(false);
    const [isDefenderAnimationIdle, setIsDefenderAnimationIdle] = useState<boolean>(false);
    const isUseUnitFightAnimation = useUnitFightAnimation();
    const overlayNode = useRef<HTMLDivElement>(document.createElement("div"));
    const fightPlayer = usePlayFightTheme();

    useEffect(() => {
        if (isShow) {
            getAudioByRow(AudioPlayerAudioRowEnum.ambient).pause();

            fightPlayer.play();

            return;
        }

        (async (): Promise<void> => {
            await waitForNextFrame();

            try {
                await getAudioByRow(AudioPlayerAudioRowEnum.ambient).play();
            } catch (error: unknown) {
                console.log("ERROR play audio in fight animation");
                console.log(String(error));
            }

            fightPlayer.stop();

            await waitForNextFrame();
            fightPlayer.stop();
        })();
    }, [isShow, fightPlayer]);

    useEffect(() => {
        const firstToSave = animationList.at(0);

        if (isShow || isUnitFightAnimationEnable === false) {
            return;
        }

        if (firstToSave) {
            setFirst({...firstToSave});
        }
    }, [animationList, isShow, isUnitFightAnimationEnable]);

    const popupAnimationOptions: KeyframeAnimationOptions = useMemo((): KeyframeAnimationOptions => {
        return {
            duration: animationStepTime,
            easing: "ease-in-out",
            fill: "forwards",
            iterations: 1,
        };
    }, [animationStepTime]);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first || isShow) {
            return;
        }

        const {action, animationOnFinish} = first;

        if (action.actionType !== GameActionTypeEnum.unitToUnitAttack) {
            return;
        }

        const aggressorBefore: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, gameState.unitList);
        const defenderBefore: GameUnitType | null = getUnitByPoint(action.toPoint, gameState.unitList);

        if (getNeedShowFightAnimation(isUnitFightAnimationEnable, aggressorBefore, defenderBefore) === false) {
            return;
        }

        const fightAnimationId = getRandomStringWithPrefix("fight-animation");

        setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
            // eslint-disable-next-line unicorn/prefer-spread
            return new Set<string>([...Array.from(localAnimationList), fightAnimationId]);
        });

        setIsShow(true);

        if (!aggressorBefore || !defenderBefore) {
            console.error("ClientGameUnitFight in use effect - can not find units");
            console.log(first);
            console.error(aggressorBefore, defenderBefore);
            setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
                localAnimationList.delete(fightAnimationId);

                // eslint-disable-next-line unicorn/prefer-spread
                return new Set<string>(Array.from(localAnimationList));
            });
            setIsShow(false);
            animationOnFinish();
            return;
        }

        // eslint-disable-next-line max-statements
        (async (): Promise<undefined> => {
            setIsAggressorAnimationIdle(true);
            setIsDefenderAnimationIdle(true);
            setAggressorUnitToAnimate({...aggressorBefore});
            setDefenderUnitToAnimate({...defenderBefore});

            const activeAttackShift = 0;

            await waitForNextFrame();
            await waitForNextFrame();
            await waitForNextFrame();

            overlayNode.current.animate(unitFightAnimationOverlayKeyframeList, popupAnimationOptions);

            const fightWrapperAnimationOptions: KeyframeAnimationOptions = {
                duration: fightAnimationFrameTime * 3,
                easing: "ease-in-out",
                fill: "forwards",
            };

            fightWrapperRef.current.animate(unitFightAnimationWrapperFadeOutKeyframeList, fightWrapperAnimationOptions);

            const groundShakeKeyFrameList: Array<Keyframe> = [
                ...groundShakeList,
                ...groundShakeList,
                ...groundShakeList,
            ];
            const groundShakeOption: KeyframeAnimationOptions = {
                duration: fightAnimationFrameTime * 3,
                easing: `steps(${groundShakeKeyFrameList.length}, end)`,
            };

            async function onFightEnd(waitFrameCount: number): Promise<void> {
                await waitForAnimateTime(fightAnimationFrameTime * Math.min(10, waitFrameCount));

                animationOnFinish();

                const hideAnimation = fightWrapperRef.current.animate(
                    [...unitFightAnimationWrapperFadeOutKeyframeList].reverse(),
                    fightWrapperAnimationOptions
                );

                overlayNode.current.animate(
                    [...unitFightAnimationOverlayKeyframeList].reverse(),
                    popupAnimationOptions
                );

                hideAnimation.onfinish = async (): Promise<undefined> => {
                    setFirst(null);

                    await waitForNextFrame();

                    setIsShow(false);

                    await waitForNextFrame();

                    setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
                        localAnimationList.delete(fightAnimationId);

                        // eslint-disable-next-line unicorn/prefer-spread
                        return new Set<string>(Array.from(localAnimationList));
                    });
                };
            }

            // eslint-disable-next-line @typescript-eslint/no-unused-vars, no-unused-vars
            const {aggressor, defender, hasStrikeBack} = getUnitToUnitAttackResult(
                {aggressor: aggressorBefore, defender: defenderBefore},
                gameState
            );

            const aggressorAnimationData = getFightAnimation(
                aggressor.unitType,
                getPlayerColorIdByPlayerId(aggressor.gamePlayerId, gameState.playerList)
            );
            const defenderAnimationData = getFightAnimation(
                defender.unitType,
                getPlayerColorIdByPlayerId(defender.gamePlayerId, gameState.playerList)
            );

            const aggressorAnimationFullSize =
                getMaxSizeAnimationSpiteList(aggressorAnimationData.idle) +
                getMaxSizeAnimationSpiteList(aggressorAnimationData.attackStart) +
                getMaxSizeAnimationSpiteList(aggressorAnimationData.attackActive) +
                getMaxSizeAnimationSpiteList(aggressorAnimationData.attackRecovery) +
                getMaxSizeAnimationSpiteList(aggressorAnimationData.idle);

            const defenderAnimationFullSize =
                getMaxSizeAnimationSpiteList(defenderAnimationData.idle) +
                getMaxSizeAnimationSpiteList(defenderAnimationData.attackStart) +
                getMaxSizeAnimationSpiteList(defenderAnimationData.attackActive) +
                getMaxSizeAnimationSpiteList(defenderAnimationData.attackRecovery) +
                getMaxSizeAnimationSpiteList(defenderAnimationData.idle);

            // idle animation before aggressor's attack
            await waitForAnimateTime(
                getMaxSizeAnimationSpiteList(aggressorAnimationData.idle) * 4 * fightAnimationFrameTime
            );

            // show aggressor's attack
            setIsAggressorAnimationIdle(false);

            // wait for idle + start up
            await waitForAnimateTime(
                (aggressorAnimationData.attackStartUpTime + activeAttackShift) * fightAnimationFrameTime
            );

            if (isHitUnitAnimationEnable) {
                rightGroundRef.current.animate(arrayShuffle(groundShakeKeyFrameList), groundShakeOption);
            }

            setDefenderUnitToAnimate(defender);

            const aggressorRecoveryFrameCount =
                aggressorAnimationFullSize - aggressorAnimationData.attackStartUpTime - activeAttackShift;
            // wait to return to start position

            await waitForAnimateTime(aggressorRecoveryFrameCount * fightAnimationFrameTime);

            setIsAggressorAnimationIdle(true);

            if (hasStrikeBack === false) {
                await onFightEnd(
                    Math.max(
                        getMaxSizeAnimationSpiteList(aggressorAnimationData.effect),
                        getMaxSizeAnimationSpiteList(aggressorAnimationData.attackRecovery)
                    )
                );
                return;
            }

            // idle animation before defender's attack
            await waitForAnimateTime(
                getMaxSizeAnimationSpiteList(defenderAnimationData.idle) * 2 * fightAnimationFrameTime
            );

            // show defender's attack
            setIsDefenderAnimationIdle(false);

            // wait for idle + start up
            await waitForAnimateTime(
                (defenderAnimationData.attackStartUpTime + activeAttackShift) * fightAnimationFrameTime
            );

            if (isHitUnitAnimationEnable) {
                leftGroundRef.current.animate(arrayShuffle(groundShakeKeyFrameList), groundShakeOption);
            }

            setAggressorUnitToAnimate(aggressor);

            const defenderRecoveryFrameCount =
                defenderAnimationFullSize - defenderAnimationData.attackStartUpTime - activeAttackShift;

            // wait to return to start position
            await waitForAnimateTime(defenderRecoveryFrameCount * fightAnimationFrameTime);

            setIsDefenderAnimationIdle(true);

            await onFightEnd(
                Math.max(
                    getMaxSizeAnimationSpiteList(defenderAnimationData.effect),
                    getMaxSizeAnimationSpiteList(defenderAnimationData.attackRecovery)
                )
            );
        })();
    }, [
        fightAnimationFrameTime,
        first,
        gameState,
        isHitUnitAnimationEnable,
        isShow,
        isUnitFightAnimationEnable,
        popupAnimationOptions,
        setLocalAnimationList,
    ]);

    if (!first) {
        return null;
    }

    const {action: actionForBackground} = first;

    if (actionForBackground.actionType !== GameActionTypeEnum.unitToUnitAttack || isUseUnitFightAnimation === false) {
        return null;
    }

    if (!isShow) {
        return null;
    }

    const aggressorAnimationView: JSX.Element | null = aggressorUnitToAnimate ? (
        <UnitFightAnimation
            frameTimeInterval={fightAnimationFrameTime}
            isAggressor
            isIdle={isAggressorAnimationIdle}
            key="aggressor"
            unit={aggressorUnitToAnimate}
        />
    ) : null;

    const defenderAnimationView: JSX.Element | null = defenderUnitToAnimate ? (
        <UnitFightAnimation
            frameTimeInterval={fightAnimationFrameTime}
            isAggressor={false}
            isIdle={isDefenderAnimationIdle}
            key="defender"
            unit={defenderUnitToAnimate}
        />
    ) : null;

    const animationNodeList: Array<JSX.Element | null> = isDefenderAnimationIdle
        ? [defenderAnimationView, aggressorAnimationView]
        : [aggressorAnimationView, defenderAnimationView];

    return createPortal(
        <Page className={clientGameUnitFightStyle.client_game_unit_fight__page}>
            <div
                className={clientGameUnitFightStyle.client_game_unit_fight__wrapper}
                ref={fightWrapperRef}
                style={{width: `${Math.floor(fightAnimationViewSize.width * getFightAnimationScale(screenSize))}px`}}
            >
                <div className={clientGameUnitFightStyle.client_game_unit_fight__left}>
                    <div className={clientGameUnitFightStyle.client_game_unit_fight__ground} ref={leftGroundRef}>
                        <ClientGameUnitFightField isRight={false} point={actionForBackground.unitCurrentPoint} />
                    </div>
                </div>
                <div className={clientGameUnitFightStyle.client_game_unit_fight__right}>
                    <div className={clientGameUnitFightStyle.client_game_unit_fight__ground} ref={rightGroundRef}>
                        <ClientGameUnitFightField isRight point={actionForBackground.toPoint} />
                    </div>
                </div>
                <div className={clientGameUnitFightStyle.client_game_unit_fight__unit_animation}>
                    <div
                        className={clientGameUnitFightStyle.client_game_unit_fight__unit_animation_scaled}
                        style={{
                            height: `${fightAnimationViewSize.height}px`,
                            transform: `scale(${getFightAnimationScale(screenSize)})`,
                            width: `${fightAnimationViewSize.width}px`,
                        }}
                    >
                        {animationNodeList}
                    </div>
                </div>
                <div className={clientGameUnitFightStyle.client_game_unit_fight__bottom_bar}>
                    <ClientGameUnitFightBottomBar
                        aggressor={aggressorUnitToAnimate ?? defaultUnitData}
                        defender={defenderUnitToAnimate ?? defaultUnitData}
                    />
                </div>
            </div>
            <div className={gamePopupStyle.game_popup__overlay} ref={overlayNode} />
        </Page>,
        document.body
    );
}
