import {useEffect, useCallback, useState} from "react";

import {useGameContext} from "../../../../provider/game/game-context";
import {Locale} from "../../../../provider/locale/locale-context";
import type {GameStateType} from "../../../../model/game/game-type";
import {GamePopup} from "../../../../layout/game-popup/game-popup";
import gamePopupStyle from "../../../../layout/game-popup/game-popup.scss";
import {UiButton} from "../../../../layout/ui-button/ui-button";
import type {GameUnitType} from "../../../../model/game/unit/unit-type";
import unitStoreItemStyle from "../unit-store/unit-store-item/unit-store-item.scss";
import {unitImage} from "../client-game-unit/image/unit-image";
import type {GamePlayerColorIdEnum} from "../../../../model/game/player/player-type";
import {getPlayerColorIdByPlayerId} from "../../../../model/game/player/player-helper";

import clientGameTelefragStyle from "./client-game-telefrag.scss";

export function ClientGameTelefrag(): JSX.Element {
    const {gameState, setGameState} = useGameContext();
    const {telefragList = [], playerList} = gameState;
    const [telefragListToShow, setTelefragListToShow] = useState<Array<[GameUnitType, GameUnitType]>>([]);

    useEffect(() => {
        if (telefragList.length > 0) {
            setTelefragListToShow([...telefragList]);
        }
    }, [telefragList]);

    const setIsOpenPopup = useCallback(
        (isOpen: boolean): void => {
            if (isOpen) {
                return;
            }

            setGameState((currentGameState: GameStateType): GameStateType => {
                return {
                    ...currentGameState,
                    telefragList: [],
                };
            });
        },
        [setGameState]
    );

    return (
        <GamePopup isOpen={telefragList.length > 0} setIsOpen={setIsOpenPopup}>
            <h3 className={gamePopupStyle.game_popup__header}>
                <Locale stringKey="POPUP__SUDDEN_DEATH" />
            </h3>

            <p className={gamePopupStyle.game_popup__text}>
                <Locale stringKey="POPUP__SUDDEN_DEATH__KILLERS" />
            </p>

            <div className={clientGameTelefragStyle.client_game_telefrag}>
                {telefragListToShow.map<JSX.Element>((coupleUnit: [GameUnitType, GameUnitType]): JSX.Element => {
                    const [killerUnit, ignoredVictimUnit] = coupleUnit;
                    const {unitType, gamePlayerId, unitId} = killerUnit;
                    const playerColor: GamePlayerColorIdEnum = getPlayerColorIdByPlayerId(gamePlayerId, playerList);

                    return (
                        <img
                            alt={unitType}
                            className={unitStoreItemStyle.unit_store_item__unit_image}
                            key={unitId}
                            src={unitImage[unitType][playerColor][0].src}
                        />
                    );
                })}
            </div>

            <p className={gamePopupStyle.game_popup__text}>
                <Locale stringKey="POPUP__SUDDEN_DEATH__VICTIMS" />
            </p>

            <div className={clientGameTelefragStyle.client_game_telefrag}>
                {telefragListToShow.map<JSX.Element>((coupleUnit: [GameUnitType, GameUnitType]): JSX.Element => {
                    const [ignoredKillerUnit, victimUnit] = coupleUnit;
                    const {unitType, gamePlayerId, unitId} = victimUnit;
                    const playerColor: GamePlayerColorIdEnum = getPlayerColorIdByPlayerId(gamePlayerId, playerList);

                    return (
                        <img
                            alt={unitType}
                            className={unitStoreItemStyle.unit_store_item__unit_image}
                            key={unitId}
                            src={unitImage[unitType][playerColor][0].src}
                        />
                    );
                })}
            </div>

            <div className={gamePopupStyle.game_popup__footer}>
                <UiButton
                    onClick={(): void => {
                        setIsOpenPopup(false);
                    }}
                >
                    <Locale stringKey="UI__OK" />
                </UiButton>
            </div>
        </GamePopup>
    );
}
