import type {ActionArgumentType, GameAnimationType} from "../game-context-type";
import type {GameUnitType} from "../../../model/game/unit/unit-type";
import {type GameBuildingType, GameBuildingStateEnum} from "../../../model/game/building/building-type";
import type {GameStateType} from "../../../model/game/game-type";
import {levelUnitData} from "../../../model/game/unit/unit-level";
import {GamePlayerIdEnum} from "../../../model/game/player/player-type";
import {getUnitByPoint} from "../../../model/game/unit/unit-helper";
import {getBuildingByPoint} from "../../../model/game/building/building-helper";
import {waitForNextFrame} from "../../../util/time";
import type {PromiseResolveType} from "../../../util/promise";

export async function unitToBuildingAttackAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState} = data;
    const unitAggressor: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, gameState.unitList);
    const buildingToDestroy: GameBuildingType | null = getBuildingByPoint(action.toPoint, gameState.buildingList);

    if (!unitAggressor || !buildingToDestroy) {
        return waitForNextFrame();
    }

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>): Array<GameAnimationType> => {
            return [
                ...animList,
                {
                    action,
                    // eslint-disable-next-line @typescript-eslint/no-misused-promises
                    animationOnFinish: async (): Promise<void> => {
                        setGameState((currentGameState: GameStateType): GameStateType => {
                            const updatedUnitList = [...currentGameState.unitList];
                            const updatedBuildingList = [...currentGameState.buildingList];

                            const unitInListToUpdate: GameUnitType | null = getUnitByPoint(
                                {
                                    x: unitAggressor.x,
                                    y: unitAggressor.y,
                                },
                                updatedUnitList
                            );

                            if (unitInListToUpdate) {
                                updatedUnitList[updatedUnitList.indexOf(unitInListToUpdate)] = {
                                    ...unitInListToUpdate,
                                    damageGiven:
                                        unitInListToUpdate.damageGiven + levelUnitData.experience.destroyBuilding,
                                    didAttackBuilding: true,
                                };
                            }

                            const buildingInListToUpdate: GameBuildingType | null = getBuildingByPoint(
                                {
                                    x: buildingToDestroy.x,
                                    y: buildingToDestroy.y,
                                },
                                updatedBuildingList
                            );

                            if (buildingInListToUpdate) {
                                updatedBuildingList[updatedBuildingList.indexOf(buildingInListToUpdate)] = {
                                    ...buildingInListToUpdate,
                                    gamePlayerId: GamePlayerIdEnum.playerEmpty,
                                    state: GameBuildingStateEnum.buildingBroken,
                                };
                            }

                            const updatedGameState: GameStateType = {
                                ...currentGameState,
                                buildingList: updatedBuildingList,
                                unitList: updatedUnitList,
                            };

                            return updatedGameState;
                        });
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
