/* global document */
import {useState, useEffect} from "react";

import {GamePopup} from "../game-popup/game-popup";
import gamePopupStyle from "../game-popup/game-popup.scss";
import {Locale} from "../../provider/locale/locale-context";
import {UiButton} from "../ui-button/ui-button";
import {oneDayMilliseconds, oneYearMilliseconds} from "../../util/time";
import {showRateAppBanner} from "../../util/android-app-loaded";

import likeImageSrc from "./image/like.png";
import dislikeImageSrc from "./image/dislike.png";
import {getRateAppSavedState, setRateAppSavedState} from "./rate-app-helper";
import type {RateAppSavedStateType} from "./rate-app-type";
import rateAppStyle from "./rate-app.scss";

export function RateApp(): JSX.Element | null {
    const [isOpen, setIsOpen] = useState<boolean>(false);
    const showFirstTimePeriod = oneDayMilliseconds * 7; // seven days
    const showOtherTimePeriod = oneDayMilliseconds * 7; // seven days
    const savedState: RateAppSavedStateType = getRateAppSavedState();

    // first time
    useEffect((): void => {
        const {lastShowPopupDate, firstRunDate} = savedState;

        // savedState.lastShowPopupDate === '' => never show this popup before
        if (lastShowPopupDate) {
            return;
        }

        const isNeedToShow = Date.now() - new Date(firstRunDate).getTime() >= showFirstTimePeriod;

        if (!isNeedToShow) {
            return;
        }

        console.log("[Rate App]: first show ask for rate app");

        setRateAppSavedState({lastShowPopupDate: new Date().toISOString()});
        setIsOpen(true);
    }, [savedState, showFirstTimePeriod]);

    // others time
    useEffect((): void => {
        const {lastShowPopupDate} = savedState;

        if (lastShowPopupDate === "") {
            return;
        }

        const isNeedToShow = Date.now() - new Date(lastShowPopupDate).getTime() >= showOtherTimePeriod;

        if (!isNeedToShow) {
            return;
        }

        console.log("[Rate App]: others show ask for rate app");

        setRateAppSavedState({lastShowPopupDate: new Date().toISOString()});
        setIsOpen(true);
    }, [savedState, showOtherTimePeriod]);

    if (typeof document === "undefined") {
        return null;
    }

    return (
        <GamePopup isOpen={isOpen} setIsOpen={setIsOpen}>
            <h3 className={gamePopupStyle.game_popup__header}>
                <Locale stringKey="RATE_APP__DO_YOU_LIKE_THIS_GAME" />
                {/* <br />*/}
                {/* <Locale stringKey="RATE_APP__PLEASE_RATE_THE_GAME_ON_GOOGLE_PLAY" />*/}
            </h3>

            <div className={rateAppStyle.rate_app__yes_no}>
                <UiButton
                    iconSrc={likeImageSrc}
                    langKey="UI__YES"
                    onClick={(): undefined => {
                        showRateAppBanner();
                        setRateAppSavedState({
                            lastShowPopupDate: new Date(Date.now() + oneYearMilliseconds).toISOString(),
                        });
                        setIsOpen(false);
                    }}
                />
                <UiButton
                    iconSrc={dislikeImageSrc}
                    langKey="UI__NO"
                    onClick={(): undefined => {
                        setRateAppSavedState({
                            lastShowPopupDate: new Date(Date.now() + oneYearMilliseconds).toISOString(),
                        });
                        setIsOpen(false);
                    }}
                />
            </div>

            <div className={gamePopupStyle.game_popup__footer}>
                <UiButton
                    langKey="UI__CLOSE"
                    onClick={(): undefined => {
                        setRateAppSavedState({lastShowPopupDate: new Date().toISOString()});
                        setIsOpen(false);
                    }}
                />
            </div>
        </GamePopup>
    );
}
