import {useCallback, useEffect, useMemo} from "react";
import {useDocumentVisibility} from "react-system-hook";
import {useLocation, type Location} from "react-router-dom";

import {playAudio} from "../hook/audio-player-hook/audio-player-helper";
import {
    AudioPlayerAudioRowEnum,
    type AudioPlayerConfigType,
    type AudioPlayerType,
} from "../hook/audio-player-hook/audio-player-type";
import {useAudioPlayer} from "../hook/audio-player-hook/audio-player-hook";
import {appRoute} from "../app/app-route";

import type {UsePlayAudioType} from "./play-media-type";
import {mediaAudio} from "./media";

export function usePlayClick(): UsePlayAudioType {
    const play = useCallback(async (): Promise<unknown> => {
        return playAudio({
            audioRow: AudioPlayerAudioRowEnum.uiEffect,
            src: mediaAudio.click.src,
        });
    }, []);

    const playAudioHook: UsePlayAudioType = useMemo((): UsePlayAudioType => {
        return {play};
    }, [play]);

    return playAudioHook;
}

export function usePlayTitleTheme(): AudioPlayerType {
    const location: Location<unknown> = useLocation();
    const isSettingInGame: boolean =
        location.pathname.startsWith(appRoute.clientGame.path) && location.pathname.includes(appRoute.gameSetting.path);
    const isDocumentVisible = useDocumentVisibility();

    const audioPlayer: AudioPlayerType = useAudioPlayer({
        audioRow: AudioPlayerAudioRowEnum.ambient,
        isLoop: true,
        isPlaying: isSettingInGame === false,
        isShuffle: false,
        trackList: [mediaAudio.mainTheme.src],
    });

    const {play, pause} = audioPlayer;

    useEffect(() => {
        if (isSettingInGame) {
            return;
        }

        if (isDocumentVisible) {
            play();
            return;
        }

        pause();
    }, [isDocumentVisible, play, pause, isSettingInGame]);

    return audioPlayer;
}

export const gameTheme: AudioPlayerConfigType = {
    audioRow: AudioPlayerAudioRowEnum.ambient,
    isLoop: true,
    isPlaying: true,
    isShuffle: true,
    trackList: [mediaAudio.bg1.src, mediaAudio.bg2.src],
};

export const fightTheme: AudioPlayerConfigType = {
    audioRow: AudioPlayerAudioRowEnum.fight,
    isLoop: true,
    isPlaying: false,
    isShuffle: true,
    trackList: [mediaAudio.battle1.src, mediaAudio.battle2.src],
};

export function usePlayFightTheme(): AudioPlayerType {
    const audioPlayer: AudioPlayerType = useAudioPlayer(fightTheme);

    return audioPlayer;
}

