import {isObjectInclude} from "./object";

export function arrayMove<ItemType>(list: Array<ItemType>, fromIndex: number, toIndex: number): Array<ItemType> {
    const item = list[fromIndex];

    list.splice(fromIndex, 1);
    list.splice(toIndex, 0, item);

    return list;
}

export function findInArray<ItemType extends Record<string, unknown>>(
    list: Array<ItemType>,
    query: Partial<ItemType>
): ItemType | null {
    return (
        list.find((item: ItemType): boolean => {
            return isObjectInclude(item, query);
        }) ?? null
    );
}

export function findInArrayEnsure<ItemType extends Record<string, unknown>>(
    list: Array<ItemType>,
    query: Partial<ItemType>,
    defaultValue: ItemType
): ItemType {
    return findInArray<ItemType>(list, query) ?? defaultValue;
}

export function findInArrayByValue<ItemType>(list: Array<ItemType>, value: unknown): ItemType | null {
    return (
        list.find((item: ItemType): boolean => {
            return item === value;
        }) ?? null
    );
}

export function findInArrayByValueEnsure<ItemType>(
    list: Array<ItemType>,
    value: unknown,
    defaultValue: ItemType
): ItemType {
    const findResult = findInArrayByValue<ItemType>(list, value);

    return findResult ?? defaultValue;
}

export function getUniqueListByKey<ItemType>(list: Array<ItemType>, keyName: keyof ItemType): Array<ItemType> {
    const savedValueList: Array<unknown> = [];

    return list.reduce<Array<ItemType>>((accum: Array<ItemType>, item: ItemType): Array<ItemType> => {
        const value = item[keyName];

        if (!savedValueList.includes(value)) {
            savedValueList.push(value);
            accum.push(item);
        }

        return accum;
    }, []);
}

export function makeRange(length: number): Array<number> {
    return Array.from({length}).map<number>((ignoredValue: unknown, index: number): number => {
        return index;
    });
}

export function makeArray<ItemType>(item: ItemType, length: number): Array<ItemType> {
    return Array.from<ItemType>({length}).fill(item);
}

export function getMaxInList<ItemType>(list: Array<ItemType>, getRate: (item: ItemType) => number): ItemType | null {
    const listLength = list.length;

    if (listLength === 0) {
        return null;
    }

    let [maxItem] = list;
    let maxItemRate: number = getRate(maxItem);

    // eslint-disable-next-line no-loops/no-loops
    for (const item of list) {
        const itemRate: number = getRate(item);

        if (itemRate > maxItemRate) {
            maxItem = item;
            maxItemRate = itemRate;
        }
    }

    return maxItem;
}

export function getMinInList<ItemType>(list: Array<ItemType>, getRate: (item: ItemType) => number): ItemType | null {
    return getMaxInList<ItemType>(list, (item: ItemType): number => {
        return -getRate(item);
    });
}

export function hasListInList(data: Record<"big" | "small", Array<unknown>>): boolean {
    const {big, small} = data;

    if (small.length === 0) {
        return true;
    }

    return small.every((smallItem: unknown): boolean => {
        return big.includes(smallItem);
    });
}

export function getNextArrayLoopIndex(arrayLength: number, currentIndex: number): number {
    const increasedIndex = currentIndex + 1;

    return increasedIndex >= arrayLength ? 0 : increasedIndex;
}

export function arrayShuffle<ItemType>(list: Array<ItemType>): Array<ItemType> {
    return [...list].sort((): number => {
        return Math.random() - 0.5;
    });
}

export type FixedSizeArrayType<ItemType, ArrayLength extends number> = ArrayLength extends 0
    ? Array<never>
    : ReadonlyArray<ItemType> & {0: ItemType; length: ArrayLength};

export function getHasArrayIndex(array: Array<unknown>, index: number): boolean {
    return index in array;
}
