import type {PointType} from "../point/point-type";
import type {GameUnitType} from "../unit/unit-type";

import type {GameTriggerPlaceType} from "./trigger-type";

function getIsPointInRectangle(point: PointType, rectangle: [PointType, PointType]): boolean {
    const [pointA, pointB] = rectangle;
    const minX: number = Math.min(pointA.x, pointB.x);
    const maxX: number = Math.max(pointA.x, pointB.x);
    const minY: number = Math.min(pointA.y, pointB.y);
    const maxY: number = Math.max(pointA.y, pointB.y);

    const {x: pointX, y: pointY} = point;

    return pointX >= minX && pointX <= maxX && pointY >= minY && pointY <= maxY;
}

export function getIsPointOnPlace(unit: GameUnitType, triggerPlace: GameTriggerPlaceType): boolean {
    const {unitTypeList, rectangleList} = triggerPlace;

    if (!unitTypeList.includes(unit.unitType)) {
        return false;
    }

    const unitPoint: PointType = {x: unit.x, y: unit.y};

    return rectangleList.some((rectangle: [PointType, PointType]): boolean => {
        return getIsPointInRectangle(unitPoint, rectangle);
    });
}
