// eslint-disable-next-line @typescript-eslint/no-unnecessary-condition, @typescript-eslint/strict-boolean-expressions
if (![].at) {
    // eslint-disable-next-line no-extend-native,no-use-extend-native/no-use-extend-native,id-length,func-names, @typescript-eslint/explicit-function-return-type
    Array.prototype.at = function (position) {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-argument, @typescript-eslint/no-unsafe-return
        return this.slice(position, position + 1)[0];
    };
}
