/* global HTMLInputElement, File, IS_PRODUCTION */
import {type FormEvent, useState, useCallback} from "react";

import {UiButton} from "../../../../layout/ui-button/ui-button";
import {mapEditorImage} from "../image/map-editor-image";
import type {MapEditorDrawPanelStateType} from "../map-editor-type";
import {usePromptContext} from "../../../../provider/prompt/prompt-context";
import {noop} from "../../../../util/function";
import {useGameContext} from "../../../../provider/game/game-context";
import type {GameStateType} from "../../../../model/game/game-type";
import {
    buildingListToMapFile,
    getIsDevelopmentDomain,
    getMapEditorDefaultMap,
    getMapFromGameState,
    landscapeToMapFile,
    unitListToMapFile,
} from "../map-editor-helper";
import type {GameMapType} from "../../../../model/map/map-type";
import {getUserMapById, getUserMapList, saveUserMap, updateUserMap} from "../../../../model/map/user-map";
import {IsRender} from "../../../../layout/is-render/is-render";
import {GameSnackbar} from "../../../../layout/game-snackbar/game-snackbar";
import {Locale} from "../../../../provider/locale/locale-context";
import {getMaxPlayerByMap} from "../../../../model/map/map-helper";
import {downloadFile, readFileAsText} from "../../../../util/file";
import {UiInputFile} from "../../../../layout/ui-button/ui-input-file";

import mapEditorBottomBarStyle from "./map-editor-bottom-bar.scss";
import {UserMapManagerPopup} from "./user-map-manager-popup/user-map-manager-popup";

interface MapEditorBottomBarPropsType {
    readonly drawPanelState: MapEditorDrawPanelStateType;
    readonly loadMap: (mapToLoad: GameMapType) => undefined;
}

export function MapEditorBottomBar(props: MapEditorBottomBarPropsType): JSX.Element {
    const {drawPanelState, loadMap} = props;
    const {setGameState, gameState} = useGameContext();
    const {prompt} = usePromptContext();
    const isMapExists = Boolean(getUserMapById(drawPanelState.mapId, getUserMapList()));
    const [isMapSavedSnackbarOpen, setIsMapSavedSnackbarOpen] = useState<boolean>(false);
    const [isEnterMapNameSnackbarOpen, setIsEnterMapNameSnackbarOpen] = useState<boolean>(false);
    const [isAddMoreItemsSnackbarOpen, setIsAddMoreItemsSnackbarOpen] = useState<boolean>(false);
    const [isUserMapManagerPopupOpen, setIsUserMapManagerPopupOpen] = useState<boolean>(false);

    const handleSave = useCallback(() => {
        const mapToSave: GameMapType = getMapFromGameState(gameState, drawPanelState);

        if (drawPanelState.mapName.trim() === "") {
            setIsEnterMapNameSnackbarOpen(true);
            return;
        }

        if (getMaxPlayerByMap(mapToSave) <= 1) {
            setIsAddMoreItemsSnackbarOpen(true);
            return;
        }

        const isAlreadyMapExists = Boolean(getUserMapById(drawPanelState.mapId, getUserMapList()));

        if (isAlreadyMapExists) {
            updateUserMap(drawPanelState.mapId, mapToSave);
        } else {
            saveUserMap(mapToSave);
        }

        setIsMapSavedSnackbarOpen(true);
    }, [gameState, drawPanelState]);

    const importMap = useCallback(
        async (mapFile: File): Promise<void> => {
            const fileContent: string = await readFileAsText(mapFile);
            // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
            const importedMap: GameMapType = JSON.parse(fileContent);

            loadMap(importedMap);
        },
        [loadMap]
    );

    return (
        <>
            <div className={mapEditorBottomBarStyle.bottom_status_bar}>
                <IsRender isRender={!isMapExists}>
                    <UiButton iconSrc={mapEditorImage.saveIcon.src} isSquared langKey="UI__SAVE" onClick={handleSave} />
                </IsRender>

                <IsRender isRender={isMapExists}>
                    <UiButton
                        iconSrc={mapEditorImage.replaceIcon.src}
                        isSquared
                        langKey="UI__REPLACE"
                        onClick={handleSave}
                    />
                </IsRender>

                <UiButton
                    iconSrc={mapEditorImage.openIcon.src}
                    isSquared
                    langKey="MAP_EDITOR__MAPS"
                    onClick={(): undefined => {
                        setIsUserMapManagerPopupOpen(true);
                    }}
                />

                <UiButton
                    iconSrc={mapEditorImage.cleanIcon.src}
                    isSquared
                    langKey="UI__CLEAN"
                    onClick={(): undefined => {
                        prompt({
                            header: "MAP_EDITOR__CLEAN_MAP_NOT_SAVED_DATA_WILL_BE_LOST",
                            index: 0,
                            onNo: noop,
                            onYes: () => {
                                setGameState((currentGameState: GameStateType): GameStateType => {
                                    const mapEditorDefaultMap: GameMapType = getMapEditorDefaultMap();

                                    return {
                                        ...currentGameState,
                                        buildingList: mapEditorDefaultMap.buildingList,
                                        landscape: mapEditorDefaultMap.landscape,
                                        unitList: mapEditorDefaultMap.unitList,
                                    };
                                });
                            },
                        });
                    }}
                />

                <IsRender isRender={getIsDevelopmentDomain()}>
                    <UiInputFile
                        accept="application/json"
                        iconSrc={mapEditorImage.importIcon.src}
                        isMultiple={false}
                        isSquared
                        langKey="UI__IMPORT"
                        onInput={(evt: FormEvent<HTMLInputElement>): undefined => {
                            const {currentTarget} = evt;
                            const {files} = currentTarget;
                            const firstFile: File | undefined = files?.[0];

                            if (!firstFile) {
                                console.error("can not first firstFile");
                                return;
                            }

                            prompt({
                                header: "MAP_EDITOR__YOU_ARE_SURE_TO_IMPORT_MAP",
                                index: 0,
                                onNo: () => {
                                    // Let trigger onInput even user choose the same file twice
                                    currentTarget.value = "";
                                },
                                // eslint-disable-next-line @typescript-eslint/no-misused-promises
                                onYes: async (): Promise<void> => {
                                    await importMap(firstFile);

                                    // Let trigger onInput even user choose the same file twice
                                    currentTarget.value = "";
                                },
                                text: "MAP_EDITOR__UNSAVED_MAP_PROGRESS_WILL_BE_LOST",
                            });
                        }}
                    />
                </IsRender>

                <IsRender isRender={getIsDevelopmentDomain()}>
                    <UiButton
                        iconSrc={mapEditorImage.exportIcon.src}
                        isSquared
                        langKey="UI__EXPORT"
                        onClick={(): undefined => {
                            const mapToExport: GameMapType = getMapFromGameState(gameState, drawPanelState);

                            downloadFile({
                                data: JSON.stringify(mapToExport, null, 4),
                                name: `${mapToExport.mapName}.json`,
                                type: "application/json",
                            });
                        }}
                    />
                </IsRender>

                <IsRender
                    isRender={
                        // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
                        !IS_PRODUCTION
                    }
                >
                    <UiButton
                        iconSrc={mapEditorImage.replaceIcon.src}
                        isSquared
                        onClick={(): undefined => {
                            console.log(
                                [
                                    `landscape: ${landscapeToMapFile(gameState.landscape)}`,
                                    `buildingList: ${buildingListToMapFile(gameState.buildingList)}`,
                                    `unitList: ${unitListToMapFile(gameState.unitList)}`,
                                    "",
                                ].join(",\n")
                            );
                        }}
                    >
                        log map to console
                    </UiButton>
                </IsRender>
            </div>

            <UserMapManagerPopup
                isUserMapManagerPopupOpen={isUserMapManagerPopupOpen}
                loadMap={loadMap}
                setIsUserMapManagerPopupOpen={setIsUserMapManagerPopupOpen}
            />

            <GameSnackbar
                isOpen={isMapSavedSnackbarOpen}
                setIsOpen={setIsMapSavedSnackbarOpen}
                useClosingOverlay={false}
            >
                <Locale
                    stringKey="MAP_EDITOR__MAP_HAS_BEEN_SAVED"
                    valueMap={{mapName: drawPanelState.mapName.trim()}}
                />
            </GameSnackbar>
            <GameSnackbar
                hasError
                isOpen={isEnterMapNameSnackbarOpen}
                setIsOpen={setIsEnterMapNameSnackbarOpen}
                useClosingOverlay={false}
            >
                <Locale stringKey="MAP_EDITOR__ENTER_MAP_NAME" />
            </GameSnackbar>
            <GameSnackbar
                hasError
                isOpen={isAddMoreItemsSnackbarOpen}
                setIsOpen={setIsAddMoreItemsSnackbarOpen}
                useClosingOverlay={false}
            >
                <Locale stringKey="MAP_EDITOR__NEED_MORE_UNITS_OR_BUILDINGS" />
            </GameSnackbar>
        </>
    );
}
