import type {GamePlayerIdEnum} from "../player/player-type";

export enum GameBuildingTypeEnum {
    castle = "castle",
    farm = "farm",
    temple = "temple",
    well = "well",
}

export enum GameBuildingStateEnum {
    "buildingBroken" = "buildingBroken",
    "buildingOk" = "buildingOk",
}

export interface GameBuildingType {
    buildingType: GameBuildingTypeEnum;
    gamePlayerId: GamePlayerIdEnum;
    state: GameBuildingStateEnum;
    x: number;
    y: number;
}

export interface BuildingInfoType {
    canBeStore: boolean;
    canHealthAll: boolean;
    healthBonus: number;
    moneyIncome: number;
}
