/* global localStorage */
import type {GameStateType} from "../../../model/game/game-type";
import {getRandomString} from "../../../util/string";

import type {SavedGameType} from "./save-game";

const autoSavedGameListKey = "auto-saved-game-list-v.1.0";

export function getAutoSavedGameList(): Array<SavedGameType> {
    const autoSavedGameListRaw: string = localStorage.getItem(autoSavedGameListKey) ?? "[]";
    // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
    const autoSavedGameList: Array<SavedGameType> = JSON.parse(autoSavedGameListRaw);

    return autoSavedGameList;
}

export function saveGameAuto(gameStateToSave: GameStateType, saveName: string): boolean {
    const maxAutoSaveCount = 100;

    const gameToSave: SavedGameType = {
        date: new Date().toISOString(),
        gameState: gameStateToSave,
        id: getRandomString(),
        name: saveName,
    };

    const currentAutoSavedGameList: Array<SavedGameType> = [...getAutoSavedGameList()];
    const gameStateToSaveString: string = JSON.stringify(gameStateToSave);
    const lastAutoSave = currentAutoSavedGameList.at(-1);
    const isAutoSaveAlreadyExists = Boolean(
        lastAutoSave && JSON.stringify(lastAutoSave.gameState) === gameStateToSaveString
    );

    if (isAutoSaveAlreadyExists) {
        return false;
    }

    const autoSavedGameList: Array<SavedGameType> = [...currentAutoSavedGameList, gameToSave];

    localStorage.setItem(autoSavedGameListKey, JSON.stringify(autoSavedGameList.slice(-maxAutoSaveCount)));

    return true;
}

export function removeAllAutoSavedGame(): void {
    localStorage.setItem(autoSavedGameListKey, JSON.stringify([]));
}
