import {useState, useEffect, useRef} from "react";

import {useGameContext} from "../../provider/game/game-context";

import type {GameFacedSnackbarType} from "./game-faced-snackbar-type";
import {GameFacedSnackbarItem} from "./game-faced-snackbar-item/game-faced-snackbar-item";

interface GameFacedSnackbarPropsType {
    readonly dataList: Array<GameFacedSnackbarType>;
    readonly isOpen: boolean;
    readonly onHideLastItem: () => void;
    readonly refId: string;
}

export function GameFacedSnackbar(props: GameFacedSnackbarPropsType): JSX.Element | null {
    const {setLocalAnimationList} = useGameContext();
    const {dataList, onHideLastItem, isOpen, refId} = props;
    const facedSnackbarIdRef = useRef<string>(refId);
    const [isOpenList, setIsOpenList] = useState<Array<boolean>>(
        Array.from<boolean>({length: dataList.length}).fill(false)
    );

    useEffect(() => {
        setIsOpenList(Array.from<boolean>({length: dataList.length}).fill(false));
    }, [dataList]);

    useEffect(() => {
        if (isOpen && dataList.length === 0) {
            onHideLastItem();
        }
    }, [onHideLastItem, isOpen, dataList.length]);

    useEffect(() => {
        if (isOpen && dataList.length > 0) {
            setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
                // eslint-disable-next-line unicorn/prefer-spread
                return new Set<string>([...Array.from(localAnimationList), facedSnackbarIdRef.current]);
            });

            setIsOpenList((currentIsOpenList: Array<boolean>): Array<boolean> => {
                const updatedIsOpenList: Array<boolean> = [...currentIsOpenList];

                updatedIsOpenList[0] = true;

                return updatedIsOpenList;
            });

            return;
        }

        setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
            const updatedLocalAnimationList: Set<string> = new Set<string>(localAnimationList);

            updatedLocalAnimationList.delete(facedSnackbarIdRef.current);

            return updatedLocalAnimationList;
        });
    }, [setLocalAnimationList, isOpen, dataList.length]);

    if (!isOpen || dataList.length === 0) {
        return null;
    }

    return (
        <>
            {dataList.map<JSX.Element>((gameFacedSnackbarData: GameFacedSnackbarType, index: number): JSX.Element => {
                return (
                    <GameFacedSnackbarItem
                        facedData={gameFacedSnackbarData}
                        index={index}
                        isOpenList={isOpenList}
                        key={`faced-snackbar-${index.toString(10)}`}
                        onHideLastItem={onHideLastItem}
                        setIsOpenList={setIsOpenList}
                        useClosingOverlay
                    />
                );
            })}
        </>
    );
}
