/* global document, File, Blob, URL, HTMLAnchorElement */

import {waitForNextFrame} from "./time";
import {throwError} from "./error";

export function downloadFile(fileInfo: Record<"data" | "name" | "type", string>): undefined {
    const {data, name, type} = fileInfo;
    const file: Blob = new Blob([data], {type});
    const anchor: HTMLAnchorElement = document.createElement("a");
    const url: string = URL.createObjectURL(file);

    anchor.href = url;
    anchor.download = name;

    // eslint-disable-next-line unicorn/prefer-dom-node-append
    document.body.appendChild(anchor);
    anchor.click();

    waitForNextFrame()
        .then(() => {
            // eslint-disable-next-line unicorn/prefer-dom-node-remove
            document.body.removeChild(anchor);
            URL.revokeObjectURL(url);
        })
        .catch(throwError);
}

export async function readFileAsText(file: File): Promise<string> {
    return new Blob([file]).text();
}
