import {noop} from "../../util/function";

import type {PromptProviderDataItemType} from "./prompt-type";

export function createDefaultPromptProviderData(index: number): PromptProviderDataItemType {
    return {
        header: "UI__YES",
        index,
        isOpen: false,
        onNo: noop,
        onYes: noop,
        text: "UI__YES",
    };
}
