/* global localStorage */
import type {GameStateType} from "../../../model/game/game-type";
import {getRandomString} from "../../../util/string";
import type {LangKeyType} from "../../locale/translation/type";
import {getIsCompaniesMap, getIsSkirmishMap, getIsUsersMap, getMapById} from "../../../model/map/map-helper";
import type {GameMapType} from "../../../model/map/map-type";

const savedGameListKey = "saved-game-list-v.1.0";

export interface SavedGameType {
    date: string;
    gameState: GameStateType;
    id: string;
    name: string;
}

export function getSavedGameList(): Array<SavedGameType> {
    const savedGameListRaw: string = localStorage.getItem(savedGameListKey) ?? "[]";
    // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
    const savedGameList: Array<SavedGameType> = JSON.parse(savedGameListRaw);

    return savedGameList;
}

export function saveGame(name: string, gameState: GameStateType): undefined {
    const gameToSave: SavedGameType = {
        date: new Date().toISOString(),
        gameState,
        id: getRandomString(),
        name,
    };

    const savedGameList: Array<SavedGameType> = [...getSavedGameList(), gameToSave];

    localStorage.setItem(savedGameListKey, JSON.stringify(savedGameList));
}

export function deleteSavedGame(idToDelete: string): undefined {
    const savedGameList: Array<SavedGameType> = getSavedGameList().filter<SavedGameType>(
        (savedGame: SavedGameType): savedGame is SavedGameType => {
            return savedGame.id !== idToDelete;
        }
    );

    localStorage.setItem(savedGameListKey, JSON.stringify(savedGameList));
}

