export function getSaveName(gameState: GameStateType, getLocalizedString: (langKey: LangKeyType) => string): string {
    const {mapId, stepCount} = gameState;
    const isSkirmish = getIsSkirmishMap(mapId);
    const isUsersMap = getIsUsersMap(mapId);
    const isCompanyMap = getIsCompaniesMap(mapId);
    const step: string = stepCount.toString(10).padStart(3, "0");

    const gameType: LangKeyType = ((): LangKeyType => {
        if (isCompanyMap) {
            return "UI__BUTTON__COMPANIES";
        }

        if (isUsersMap) {
            return "UI__BUTTON__USER_MAPS";
        }

        if (isSkirmish) {
            return "UI__BUTTON__SKIRMISH";
        }

        console.error("can not detect game type");

        return "UI__EMPTY_STRING";
    })();

    const map: GameMapType | null = getMapById(mapId);

    if (!map) {
        console.error(`can not get may by id: ${mapId}`);
        return `${getLocalizedString(gameType)}: no name map / ${step}, ${new Date().toLocaleString()}`;
    }

    const mapName: string = getLocalizedString(map.mapNameLangKey).trim() || map.mapName || "no name map";

    return `${getLocalizedString(gameType)}: ${mapName} / ${step}, ${new Date().toLocaleString()}`;
}
