/* eslint-disable multiline-comment-style, capitalized-comments, line-comment-position, multiline-comment-style */

import {useEffect, useState} from "react";

import {useGameContext} from "../../../../provider/game/game-context";
import {getPlayerByPlayerId} from "../../../../model/game/player/player-helper";
import {GamePlayerTypeEnum} from "../../../../model/game/player/player-type";
import {type MovedGameActionType, GameActionTypeEnum} from "../../../../provider/game/game-context-type";
import {getMovePathFromToByPathList} from "../../../../model/game/unit/unit-path";
import {getMaxAction} from "../../../../model/game/bot/bot-rate";
import {getIsEqualPoint} from "../../../../model/game/point/point-helper";
import {waitForNextFrame} from "../../../../util/time";
import {throwError} from "../../../../util/error";
// import {getIsCompaniesMap} from '../../../../model/map/map-helper';
// import {BotDifficultEnum} from '../../../../provider/setting/setting-type';
import {useSettingContext} from "../../../../provider/setting/setting-context";

// eslint-disable-next-line sonarjs/cognitive-complexity
export function ClientBot(): null {
    const {moveViewToPoint, gameState, animationList, pushAction, localAnimationList, setLocalAnimationList} =
        useGameContext();
    const {setting} = useSettingContext();
    const [isReady, setIsReady] = useState<boolean>(false);
    // const isCompanies = getIsCompaniesMap(gameState.mapId);
    // const botDifficult: BotDifficultEnum = isCompanies ? BotDifficultEnum.easy : setting.botDifficult;
    const {botDifficult} = setting;

    useEffect(() => {
        waitForNextFrame()
            .then(() => {
                setIsReady(true);
            })
            .catch(throwError);

        console.log("It will be twice!");
    }, []);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!isReady) {
            return;
        }
        if (localAnimationList.size > 0) {
            console.log("localAnimationList", localAnimationList);
            return;
        }

        if (animationList.length > 0) {
            console.log("animationList", animationList);
            return;
        }

        const {activeGamePlayerId, playerList} = gameState;
        const currentPlayer = getPlayerByPlayerId(activeGamePlayerId, playerList);

        if (!currentPlayer) {
            console.error(`[BOT]: can not get player by id: ${activeGamePlayerId}`);
            return;
        }

        if (currentPlayer.playerType !== GamePlayerTypeEnum.bot) {
            console.log("[BOT]: currentPlayer.playerType !== GamePlayerTypeEnum.bot");
            return;
        }

        const movedAction: MovedGameActionType = getMaxAction(gameState, botDifficult);

        const isNeedToMoveView: boolean = GameActionTypeEnum.changeTurn !== movedAction.gameAction.actionType;

        const movePromise: Promise<unknown> = isNeedToMoveView
            ? moveViewToPoint(movedAction.moveFrom)
            : waitForNextFrame();

        setIsReady(false);

        movePromise
            .then(async (): Promise<void> => {
                if (movedAction.gameAction.actionType === GameActionTypeEnum.unitMove) {
                    return pushAction(movedAction.gameAction);
                }

                if (movedAction.gameAction.actionType === GameActionTypeEnum.changeTurn) {
                    return pushAction(movedAction.gameAction);
                }

                if (getIsEqualPoint(movedAction.moveFrom, movedAction.moveTo)) {
                    return pushAction(movedAction.gameAction);
                }

                return pushAction({
                    actionType: GameActionTypeEnum.unitMove,
                    addUnitList: [],
                    briefing: null,
                    buyUnitType: null,
                    movePointList: getMovePathFromToByPathList(
                        movedAction.moveFrom,
                        movedAction.moveTo,
                        movedAction.movePathList
                    ),
                    toPoint: movedAction.moveTo,
                    unitCurrentPoint: movedAction.moveFrom,
                    unitId: movedAction.gameAction.unitId,
                });
            })
            .then(waitForNextFrame)
            .then((): void => {
                setIsReady(true);
            })
            .catch((error: Error): void => {
                setIsReady(true);
                throwError(error);
            });

        // send action - after that wait for animation is end OR wait for end of action
    }, [
        botDifficult,
        moveViewToPoint,
        isReady,
        gameState,
        pushAction,
        animationList,
        localAnimationList,
        setLocalAnimationList,
    ]);

    return null;
}
