import {type CSSProperties, type MutableRefObject, useState, useEffect, useCallback, useRef} from "react";

import {waitForAnimateTime} from "../../util/time";

import type {SpriteAnimationType} from "./animation-type";
import {AnimationView} from "./animation-view";

interface AnimationArrayViewPropsType {
    readonly delay: number;
    readonly frameTimeInterval: number;
    readonly onEnd: () => void;
    readonly spriteAnimationList: Array<SpriteAnimationType>;
    readonly wrapperStyle: CSSProperties;
    readonly x: number;
    readonly y: number;
}

export function AnimationArrayView(props: AnimationArrayViewPropsType): JSX.Element {
    const {spriteAnimationList, onEnd, x, y, frameTimeInterval, wrapperStyle, delay} = props;
    const [isPlaying, setIsPlaying] = useState<boolean>(delay === 0);
    const finishCountRef: MutableRefObject<number> = useRef<number>(0);

    useEffect(() => {
        (async (): Promise<undefined> => {
            await waitForAnimateTime(delay);
            setIsPlaying(true);
        })();
    }, [delay]);

    const handleAnimationViewEnd = useCallback(() => {
        finishCountRef.current += 1;

        if (spriteAnimationList.length === finishCountRef.current) {
            onEnd();
        }
    }, [onEnd, spriteAnimationList.length]);

    return (
        <>
            {spriteAnimationList.map<JSX.Element>(
                (spriteAnimation: SpriteAnimationType, index: number): JSX.Element => {
                    return (
                        <AnimationView
                            frameTimeInterval={isPlaying ? frameTimeInterval : 1e9}
                            key={JSON.stringify(spriteAnimation) + delay + index.toString(10) + isPlaying}
                            onEnd={handleAnimationViewEnd}
                            spriteAnimation={spriteAnimation}
                            wrapperStyle={{
                                ...wrapperStyle,
                                zIndex: (Number(wrapperStyle.zIndex) || 0) + spriteAnimation.randomPosition.y,
                            }}
                            x={x + spriteAnimation.randomPosition.x}
                            y={y + spriteAnimation.randomPosition.y}
                        />
                    );
                }
            )}
        </>
    );
}
