/* global localStorage */

import type {UseGestureConfigStateType} from "../../../../hook/use-gesture/use-gesture-type";

import {defaultGestureState} from "./client-game-view-container-const";

const gestureStateLocalStorageKey = "gesture-state-v1.0";

export function saveGestureState(gestureState: UseGestureConfigStateType): undefined {
    localStorage.setItem(gestureStateLocalStorageKey, JSON.stringify(gestureState));
}

export function getGestureState(): UseGestureConfigStateType {
    const savedGestureStateRaw: string = localStorage.getItem(gestureStateLocalStorageKey) ?? "";
    // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
    const savedGestureState: UseGestureConfigStateType = savedGestureStateRaw
        ? JSON.parse(savedGestureStateRaw)
        : defaultGestureState;

    return savedGestureState;
}
