import {useState} from "react";

import {UiButton} from "../../../../../layout/ui-button/ui-button";
import gamePopupStyle from "../../../../../layout/game-popup/game-popup.scss";
import {usePromptContext} from "../../../../../provider/prompt/prompt-context";
import type {GameMapType} from "../../../../../model/map/map-type";
import {deleteUserMap, getUserMapList} from "../../../../../model/map/user-map";
import {GamePopup} from "../../../../../layout/game-popup/game-popup";
import {Locale, useLocale} from "../../../../../provider/locale/locale-context";
import {sortCompareAsc} from "../../../../../util/string";
import {noop} from "../../../../../util/function";
import {GameSnackbar} from "../../../../../layout/game-snackbar/game-snackbar";
import {snackbarUiImage} from "../../../../../layout/game-snackbar/image/sknackbar-image";

import userMapManagerPopupStyle from "./user-map-manager-popup.scss";

export interface UserMapManagerPopupPropsType {
    readonly isUserMapManagerPopupOpen: boolean;
    readonly loadMap: (mapToLoad: GameMapType) => void;
    readonly setIsUserMapManagerPopupOpen: (isOpen: boolean) => void;
}

export function UserMapManagerPopup(props: UserMapManagerPopupPropsType): JSX.Element {
    const {localeName} = useLocale();
    const {isUserMapManagerPopupOpen, setIsUserMapManagerPopupOpen, loadMap} = props;
    const {prompt} = usePromptContext();
    const userMapList: Array<GameMapType> = getUserMapList();
    const [isMapLoadedSnackbarOpen, setIsMapLoadedSnackbarOpen] = useState<boolean>(false);
    const [isMapRemovedSnackbarOpen, setIsMapRemovedSnackbarOpen] = useState<boolean>(false);

    return (
        <>
            <GamePopup isOpen={isUserMapManagerPopupOpen} setIsOpen={setIsUserMapManagerPopupOpen}>
                <h3 className={gamePopupStyle.game_popup__header}>
                    <Locale stringKey="MAP_EDITOR__MAPS" />
                </h3>

                <div className={userMapManagerPopupStyle.user_map_manager_popup__saved_game_list}>
                    {userMapList.length === 0 ? (
                        <p className={userMapManagerPopupStyle.user_map_manager_popup__no_saved_game_text}>
                            <Locale stringKey="MAP_EDITOR__NO_SAVED_MAPS" />
                        </p>
                    ) : null}

                    {[...userMapList]
                        .sort((mapA: GameMapType, mapB: GameMapType): number => {
                            return sortCompareAsc(localeName, mapA.mapName, mapB.mapName);
                        })
                        .map<JSX.Element>((userMap: GameMapType): JSX.Element => {
                            return (
                                <div
                                    className={userMapManagerPopupStyle.user_map_manager_popup__saved_game_item}
                                    key={userMap.mapId}
                                >
                                    <p
                                        className={
                                            userMapManagerPopupStyle.user_map_manager_popup__saved_game_item__name
                                        }
                                    >
                                        {userMap.mapName}
                                    </p>
                                    <div
                                        className={
                                            // eslint-disable-next-line max-len
                                            userMapManagerPopupStyle.user_map_manager_popup__saved_game_item__button_list
                                        }
                                    >
                                        <UiButton
                                            langKey="UI__DELETE"
                                            onClick={(): undefined => {
                                                prompt({
                                                    header: "MAP_EDITOR__YOU_ARE_SURE_TO_DELETE_MAP",
                                                    index: 0,
                                                    onNo: noop,
                                                    onYes: () => {
                                                        deleteUserMap(userMap.mapId);
                                                        setIsMapRemovedSnackbarOpen(true);
                                                    },
                                                });
                                                setIsUserMapManagerPopupOpen(false);
                                            }}
                                        />
                                        <UiButton
                                            langKey="UI__LOAD"
                                            onClick={(): undefined => {
                                                prompt({
                                                    header: "UI__LOAD",
                                                    index: 0,
                                                    onNo: noop,
                                                    onYes: () => {
                                                        loadMap(userMap);
                                                        setIsMapLoadedSnackbarOpen(true);
                                                    },
                                                    text: "MAP_EDITOR__UNSAVED_MAP_PROGRESS_WILL_BE_LOST",
                                                });
                                                setIsUserMapManagerPopupOpen(false);
                                            }}
                                        />
                                    </div>
                                </div>
                            );
                        })}
                </div>

                <div className={gamePopupStyle.game_popup__footer}>
                    <UiButton
                        iconSrc={snackbarUiImage.close.src}
                        langKey="UI__CLOSE"
                        onClick={(): undefined => {
                            setIsUserMapManagerPopupOpen(false);
                        }}
                    />
                </div>
            </GamePopup>

            <GameSnackbar
                isOpen={isMapLoadedSnackbarOpen}
                setIsOpen={setIsMapLoadedSnackbarOpen}
                useClosingOverlay={false}
            >
                <Locale stringKey="MAP_EDITOR__MAP_WAS_LOADED" />
            </GameSnackbar>
            <GameSnackbar
                isOpen={isMapRemovedSnackbarOpen}
                setIsOpen={setIsMapRemovedSnackbarOpen}
                useClosingOverlay={false}
            >
                <Locale stringKey="MAP_EDITOR__MAP_WAS_REMOVED" />
            </GameSnackbar>
        </>
    );
}
