import {useEffect, useRef, useState, useMemo} from "react";

import {useGameContext} from "../../../../provider/game/game-context";
import {getPlayerByPlayerId} from "../../../../model/game/player/player-helper";
import {GameSnackbar} from "../../../../layout/game-snackbar/game-snackbar";
import {type GamePlayerIdEnum, GamePlayerTypeEnum} from "../../../../model/game/player/player-type";
import {Locale, useLocale} from "../../../../provider/locale/locale-context";
import {saveGameAuto} from "../../../../provider/game/action/save-game-auto";
import {getIsCompaniesMap, getIsSkirmishMap, getIsUsersMap} from "../../../../model/map/map-helper";
import type {LangKeyType} from "../../../../provider/locale/translation/type";
import {getSaveName} from "../../../../provider/game/action/save-game";

export function ClientGameAutoSave(): JSX.Element {
    const {getLocalizedString} = useLocale();
    const [isAutoSaveSnackbarOpen, setIsAutoSaveSnackbarOpen] = useState<boolean>(false);
    const {gameState} = useGameContext();
    const {activeGamePlayerId, playerList, mapId} = gameState;
    const activeGamePlayerIdRef = useRef<GamePlayerIdEnum>(activeGamePlayerId);
    const isSkirmish = getIsSkirmishMap(mapId);
    const isUsersMap = getIsUsersMap(mapId);
    const isCompanyMap = getIsCompaniesMap(mapId);

    const popupKey = useMemo((): LangKeyType => {
        if (isCompanyMap) {
            return "POPUP__COMPANIES__GAME_SAVED";
        }

        if (isUsersMap) {
            return "POPUP__USERS_MAP__GAME_SAVED";
        }

        if (isSkirmish) {
            return "POPUP__SKIRMISH__GAME_SAVED";
        }

        console.error("can not detect game type");

        return "POPUP__GAME_SAVED";
    }, [isCompanyMap, isUsersMap, isSkirmish]);

    // eslint-disable-next-line complexity
    useEffect(() => {
        const currentPlayerById = getPlayerByPlayerId(activeGamePlayerId, playerList);

        if (!currentPlayerById) {
            console.error(`Can not get player by id: ${activeGamePlayerId}`);
            return;
        }

        if (activeGamePlayerIdRef.current === currentPlayerById.gamePlayerId) {
            return;
        }

        activeGamePlayerIdRef.current = currentPlayerById.gamePlayerId;

        if (currentPlayerById.playerType !== GamePlayerTypeEnum.human) {
            return;
        }

        if (saveGameAuto(gameState, getSaveName(gameState, getLocalizedString))) {
            setIsAutoSaveSnackbarOpen(true);
        }
    }, [gameState, playerList, activeGamePlayerId, getLocalizedString, mapId]);

    return (
        <GameSnackbar isOpen={isAutoSaveSnackbarOpen} setIsOpen={setIsAutoSaveSnackbarOpen} useClosingOverlay={false}>
            <Locale stringKey={popupKey} />
        </GameSnackbar>
    );
}
