import type {MutableRefObject, Dispatch, SetStateAction} from "react";

import type {GameStateType, GetAllActionsArgType} from "../../model/game/game-type";
import type {PointType} from "../../model/game/point/point-type";
import type {GameAllActionsType, GameUnitType, GameUnitTypeEnum} from "../../model/game/unit/unit-type";
import type {SizeType} from "../../model/map/map-type";
import type {MovePathType} from "../../model/game/unit/unit-path";
import type {GameFacedSnackbarType} from "../../layout/game-faced-snackbar-list/game-faced-snackbar-type";
import type {UseGestureHookType} from "../../hook/use-gesture/use-gesture-type";

export enum GameActionTypeEnum {
    addUnitList = "add-unit-list",
    buyUnit = "buy-unit",
    changeTurn = "change-turn",
    companiesForceDefeat = "companies-force-defeat",
    companiesForceWin = "companies-force-win",
    fixBuilding = "fix-building",
    occupyBuilding = "occupy-building",
    raiseSkeleton = "raise-skeleton",
    showBriefing = "show-briefing",
    unitMove = "unit-move",
    unitMoveRevert = "unit-move-revert",
    unitToBuildingAttack = "unit-to-building-attack",
    unitToUnitAttack = "unit-to-unit-attack",
}

export interface GameActionBriefingType<BriefingEndType> {
    briefingList: Array<GameFacedSnackbarType>;
    onBriefingEnd: BriefingEndType | null;
}

export interface GameActionType {
    actionType: GameActionTypeEnum;
    addUnitList: Array<GameUnitType>;
    briefing: GameActionBriefingType<GameActionType> | null;
    buyUnitType: GameUnitTypeEnum | null;
    movePointList: Array<PointType>;
    toPoint: PointType;
    unitCurrentPoint: PointType;
    unitId: string;
}

export interface MovedGameActionType {
    gameAction: GameActionType;
    moveFrom: PointType;
    movePathList: Array<MovePathType>;
    moveTo: PointType;
}

export interface GameAnimationType {
    action: GameActionType;
    animationOnFinish: () => void;
}

export interface GameContextType {
    activePoint: PointType;
    animationList: Array<GameAnimationType>;
    availableActions: GameAllActionsType;
    gameState: GameStateType;
    getAvailableActions: (data: GetAllActionsArgType) => GameAllActionsType;
    isCompanyEnd: boolean;
    isEnableEndTurnButton: boolean;
    isGameRunning: boolean;
    lastMoveAction: GameActionType | null;
    loadGame: (gameState: GameStateType) => Promise<void>;
    localAnimationList: Set<string>;
    mapSize: SizeType;
    moveViewToPoint: (point: PointType) => Promise<void>;
    pushAction: (action: GameActionType) => Promise<void>;
    resetState: () => void;
    setActivePoint: Dispatch<SetStateAction<PointType>>;
    setAvailableActions: Dispatch<SetStateAction<GameAllActionsType>>;
    setGameState: Dispatch<SetStateAction<GameStateType>>;
    setIsGameRunning: Dispatch<SetStateAction<boolean>>;
    setLocalAnimationList: Dispatch<SetStateAction<Set<string>>>;
    unitOverUnit: [GameUnitType, GameUnitType] | null;
    useGestureRef: MutableRefObject<UseGestureHookType | null>;
}

export interface ActionArgumentType {
    action: GameActionType;
    gameState: GameStateType;
    lastMoveAction: GameActionType | null;
    removeAnimationByAction: (action: GameActionType) => void;
    setAnimationList: Dispatch<SetStateAction<Array<GameAnimationType>>>;
    setGameState: Dispatch<SetStateAction<GameStateType>>;
    setLastMoveAction: Dispatch<SetStateAction<GameActionType | null>>;
}
