import type {PointType} from "../point/point-type";
import type {GameStateType, PointDataType} from "../game-type";
import type {GameBuildingType} from "../building/building-type";
import type {GameGraveType, GameUnitType} from "../unit/unit-type";
import type {GameLandscapeItemEnum} from "../landscape/landscape-type";
import {getLandscapeByPoint} from "../landscape/landscape-helper";
import {getBuildingByPoint} from "../building/building-helper";
import {getUnitByPoint} from "../unit/unit-helper";
import {getGraveByPoint} from "../grave/grave-helper";

// eslint-disable-next-line complexity
export function getByPoint(point: PointType, gameState: GameStateType): PointDataType {
    const {x, y} = point;
    const {landscape, unitList, buildingList, graveList} = gameState;

    const building: GameBuildingType | null = getBuildingByPoint(point, buildingList);
    // reverse unitList to get action of last unit
    // need to easy move unt from unit
    // if player buy new unit from store
    const unit: GameUnitType | null = getUnitByPoint(point, [...unitList].reverse());
    const grave: GameGraveType | null = getGraveByPoint(point, graveList);
    const landscapeItem: GameLandscapeItemEnum | null = getLandscapeByPoint(point, landscape);

    return {
        building,
        grave,
        landscapeItem,
        unit,
        x,
        y,
    };
}
