import {GamePopup} from "../../../../layout/game-popup/game-popup";
import gamePopupStyle from "../../../../layout/game-popup/game-popup.scss";
import {Locale} from "../../../../provider/locale/locale-context";
import {UiButton} from "../../../../layout/ui-button/ui-button";
import {useGameContext} from "../../../../provider/game/game-context";
import {type SavedGameType, deleteSavedGame, getSavedGameList} from "../../../../provider/game/action/save-game";
import {usePromptContext} from "../../../../provider/prompt/prompt-context";
import {noop} from "../../../../util/function";
import {getAutoSavedGameList, removeAllAutoSavedGame} from "../../../../provider/game/action/save-game-auto";
import {IsRender} from "../../../../layout/is-render/is-render";
import {waitForNextFrame} from "../../../../util/time";
import {getMapById} from "../../../../model/map/map-helper";
import {snackbarUiImage} from "../../../../layout/game-snackbar/image/sknackbar-image";
import {useSnackbarContext} from "../../../../provider/snackbar/snackbar-context";
import {SnackbarConstructorNameEnum} from "../../../../provider/snackbar/snackbar-type";

import clientLoadGamePopupStyle from "./client-load-game-popup.scss";

export interface ClientLoadGamePopupPropsType {
    readonly afterLoad?: () => Promise<void> | void;
    readonly handleClose: () => void;
    readonly isLoadGamePopupOpen: boolean;
    readonly isQuickLoad: boolean;
    readonly setIsGameMenuOpen: (isGameMenuOpen: boolean) => void;
}

export function ClientLoadGamePopup(props: ClientLoadGamePopupPropsType): JSX.Element {
    const {isLoadGamePopupOpen, handleClose, isQuickLoad, setIsGameMenuOpen, afterLoad = noop} = props;
    const {loadGame} = useGameContext();
    const {prompt} = usePromptContext();
    const {snackbar} = useSnackbarContext();
    const savedGameList: Array<SavedGameType> = isQuickLoad ? getAutoSavedGameList() : getSavedGameList();

    return (
        <GamePopup isOpen={isLoadGamePopupOpen} setIsOpen={handleClose}>
            <h3 className={gamePopupStyle.game_popup__header}>
                {isQuickLoad ? (
                    <Locale stringKey="SAVE_LOAD__QUICK_LOAD_GAME" />
                ) : (
                    <Locale stringKey="SAVE_LOAD__LOAD_GAME" />
                )}
            </h3>

            <div className={clientLoadGamePopupStyle.client_load_game_popup__saved_game_list}>
                {savedGameList.length === 0 ? (
                    <p className={clientLoadGamePopupStyle.client_load_game_popup__no_saved_game_text}>
                        <Locale stringKey="SAVE_LOAD__NO_SAVED_GAMES" />
                    </p>
                ) : null}

                {[...savedGameList]
                    .filter<SavedGameType>((savedGame: SavedGameType): savedGame is SavedGameType => {
                        return Boolean(getMapById(savedGame.gameState.mapId));
                    })
                    .reverse()
                    .map<JSX.Element>((savedGame: SavedGameType): JSX.Element => {
                        return (
                            <div
                                className={clientLoadGamePopupStyle.client_load_game_popup__saved_game_item}
                                key={savedGame.id}
                            >
                                <p className={clientLoadGamePopupStyle.client_load_game_popup__saved_game_item__name}>
                                    {savedGame.name}
                                </p>
                                <div
                                    className={
                                        // eslint-disable-next-line max-len
                                        clientLoadGamePopupStyle.client_load_game_popup__saved_game_item__button_list
                                    }
                                >
                                    <UiButton
                                        langKey="SAVE_LOAD__LOAD"
                                        onClick={(): undefined => {
                                            prompt({
                                                header: "SAVE_LOAD__LOAD_GAME",
                                                index: 0,
                                                onNo: noop,
                                                // eslint-disable-next-line @typescript-eslint/no-misused-promises
                                                onYes: async () => {
                                                    snackbar({
                                                        constructorName: SnackbarConstructorNameEnum.gameSnackbar,
                                                        elementProps: {
                                                            children: <Locale stringKey="SAVE_LOAD__LOAD_SUCCESSFUL" />,
                                                            isOpen: true,
                                                            setIsOpen: noop,
                                                            useClosingOverlay: false,
                                                        },
                                                        id: "game-loaded",
                                                    });
                                                    await loadGame(savedGame.gameState);
                                                    await waitForNextFrame();
                                                    // eslint-disable-next-line @typescript-eslint/await-thenable, @typescript-eslint/no-confusing-void-expression
                                                    await afterLoad();
                                                    setIsGameMenuOpen(false);
                                                },
                                                text: "SAVE_LOAD__ARE_YOU_SURE_TO_LOAD_GAME",
                                            });
                                            handleClose();
                                        }}
                                    />
                                    <IsRender isRender={!isQuickLoad}>
                                        <UiButton
                                            langKey="SAVE_LOAD__DELETE"
                                            onClick={(): undefined => {
                                                prompt({
                                                    header: "SAVE_LOAD__ARE_YOU_SURE_TO_DELETE_SAVED_GAME",
                                                    index: 0,
                                                    onNo: noop,
                                                    onYes: () => {
                                                        deleteSavedGame(savedGame.id);
                                                    },
                                                });
                                            }}
                                        />
                                    </IsRender>
                                </div>
                            </div>
                        );
                    })}
            </div>

            <div className={clientLoadGamePopupStyle.client_load_game_popup__footer}>
                <IsRender isRender={Boolean(isQuickLoad && savedGameList.length > 0)}>
                    <UiButton
                        langKey="SAVE_LOAD__DELETE_ALL"
                        onClick={(): undefined => {
                            prompt({
                                header: "SAVE_LOAD__ARE_YOU_SURE_TO_DELETE_ALL_AUTOSAVED_GAMES",
                                index: 0,
                                onNo: noop,
                                onYes: removeAllAutoSavedGame,
                            });
                        }}
                    />
                </IsRender>
                <UiButton iconSrc={snackbarUiImage.close.src} langKey="UI__CLOSE" onClick={handleClose} />
            </div>
        </GamePopup>
    );
}
