/* global fetch, HTMLAudioElement, Audio, Response, Blob */

import {throwError} from "./error";

export function getAudio(src: string): HTMLAudioElement {
    const audio: HTMLAudioElement = new Audio();

    fetch(src)
        .then(async (rawFile: Response): Promise<Blob> => {
            return rawFile.blob();
        })
        .catch(throwError);

    audio.src = src;
    audio.preload = "metadata";

    return audio;
}
