/* eslint-disable no-mixed-operators */

/* global HTMLDivElement, MouseEvent, Touch */

import type {PointType} from "../../model/game/point/point-type";

import type {MovingDataType, MovingLimitDataType} from "./use-gesture-type";

export function touchToPoint(touch: Touch): PointType {
    return {x: touch.clientX, y: touch.clientY};
}

export function mouseEventToPoint(moveEvent: MouseEvent): PointType {
    return {x: moveEvent.clientX, y: moveEvent.clientY};
}

export function getStyleString(data: MovingDataType): string {
    return `translate3d(${data.x}px, ${data.y}px, 0) scale(${data.zoom})`;
}

export function getSize(pointA: PointType, pointB: PointType): number {
    const deltaX = pointA.x - pointB.x;
    const deltaY = pointA.y - pointB.y;

    return Math.hypot(deltaX, deltaY);
}

export function fitValue(data: Record<"max" | "min" | "multiplier" | "value", number>): number {
    const {min, max, multiplier, value} = data;

    if (value > max) {
        return max + (value - max) * multiplier;
    }

    if (value < min) {
        return min - (min - value) * multiplier;
    }

    return value;
}

export function fitMovingData(
    movingData: MovingDataType,
    movingLimitData: MovingLimitDataType,
    multiplier: number
): MovingDataType {
    const {maxY, maxX, maxZoom, minZoom, minX, minY} = movingLimitData;

    const x = fitValue({
        max: maxX,
        min: minX,
        multiplier,
        value: movingData.x,
    });

    const y = fitValue({
        max: maxY,
        min: minY,
        multiplier,
        value: movingData.y,
    });

    const zoom = fitValue({
        max: maxZoom,
        min: minZoom,
        multiplier,
        value: movingData.zoom,
    });

    return {x, y, zoom};
}

export function getChildMinMax(htmlNode: HTMLDivElement, minMax: MovingLimitDataType): MovingLimitDataType {
    const {maxX, maxY, minY, minX, maxZoom, minZoom} = minMax;
    const {width, height} = htmlNode.getBoundingClientRect();

    return {
        maxX: Math.max(width / 2 - maxX, 0),
        maxY: Math.max(height / 2 - maxY, 0),
        maxZoom,
        minX: Math.min(-width / 2 - minX, 0),
        minY: Math.min(-height / 2 - minY, 0),
        minZoom,
    };
}
