import {useState} from "react";
import {useNavigate} from "react-router-dom";

import {UiButton} from "../../../../../layout/ui-button/ui-button";
import {ClientSaveGamePopup} from "../../client-save-game-popup/client-save-game-popup";
import {ClientLoadGamePopup} from "../../client-load-game-popup/client-load-game-popup";
import {fullPathToGameSetting} from "../../../../../app/app-route";
import {uiButtonImage} from "../../../../../layout/ui-button/image/ui-button-image";

interface ClientGameMenuPropsType {
    readonly setIsGameMenuOpen: (isGameMenuOpen: boolean) => void;
    readonly setIsGameStarMessageOpen: (isShow: boolean) => void;
}

export function ClientGameMenu(props: ClientGameMenuPropsType): JSX.Element {
    const {setIsGameStarMessageOpen, setIsGameMenuOpen} = props;
    const [isSaveGamePopupOpen, setIsSaveGamePopupOpen] = useState<boolean>(false);
    const [isLoadGamePopupOpen, setIsLoadGamePopupOpen] = useState<boolean>(false);
    const [isQuickLoadGamePopupOpen, setIsQuickLoadGamePopupOpen] = useState<boolean>(false);
    const navigate = useNavigate();

    return (
        <>
            <UiButton
                iconSrc={uiButtonImage.info.src}
                langKey="NOTIFICATION__OBJECTIVE"
                onClick={(): undefined => {
                    setIsGameStarMessageOpen(true);
                }}
            />
            <UiButton
                iconSrc={uiButtonImage.hammer.src}
                langKey="UI__BUTTON__SETTINGS"
                onClick={(): undefined => {
                    navigate(fullPathToGameSetting);
                    setIsGameMenuOpen(false);
                }}
            />
            <UiButton
                iconSrc={uiButtonImage.book.src}
                langKey="SAVE_LOAD__SAVE_GAME"
                onClick={(): undefined => {
                    setIsSaveGamePopupOpen(true);
                }}
            />
            <UiButton
                iconSrc={uiButtonImage.diamondGreen.src}
                langKey="SAVE_LOAD__LOAD_GAME"
                onClick={(): undefined => {
                    setIsLoadGamePopupOpen(true);
                }}
            />
            <UiButton
                iconSrc={uiButtonImage.star.src}
                langKey="SAVE_LOAD__QUICK_LOAD_GAME"
                onClick={(): undefined => {
                    setIsQuickLoadGamePopupOpen(true);
                }}
            />
            <UiButton
                iconSrc={uiButtonImage.grave.src}
                langKey="END_GAME__QUIT_BATTLE"
                onClick={(): undefined => {
                    navigate(-1);
                }}
            />

            <ClientLoadGamePopup
                handleClose={(): undefined => {
                    setIsLoadGamePopupOpen(false);
                }}
                isLoadGamePopupOpen={isLoadGamePopupOpen}
                isQuickLoad={false}
                setIsGameMenuOpen={setIsGameMenuOpen}
            />
            <ClientLoadGamePopup
                handleClose={(): undefined => {
                    setIsQuickLoadGamePopupOpen(false);
                }}
                isLoadGamePopupOpen={isQuickLoadGamePopupOpen}
                isQuickLoad
                setIsGameMenuOpen={setIsGameMenuOpen}
            />
            <ClientSaveGamePopup
                isSaveGamePopupOpen={isSaveGamePopupOpen}
                setIsSaveGamePopupOpen={setIsSaveGamePopupOpen}
            />
        </>
    );
}
