import type {GameStateType, GetAllActionsArgType} from "../game-type";
import type {PointType} from "../point/point-type";
import {getTeamIdByPlayerId} from "../player/player-helper";
import type {GamePlayerTeamIdEnum} from "../player/player-type";
import {getByPoint} from "../game-state/game-state";
import {GameBuildingStateEnum} from "../building/building-type";
import {getLandscapeSize} from "../landscape/landscape-helper";
import {getGraveByPoint} from "../grave/grave-helper";
import type {SizeType} from "../../map/map-type";
import {getCanOpenStoreByPoint} from "../../../page/client/client-game/unit-store/unit-store-helper";
import {getIsEqualPoint, getPointByPoint} from "../point/point-helper";

import type {GameAllActionsType, GameUnitType} from "./unit-type";
import {getUnitByPoint, getUnitInfoByUnit, getUnitOverUnit} from "./unit-helper";
import {unitUnderPoisonMoveSizeReduce} from "./unit-const";
import {
    type MovePathType,
    getAvailableMovePathList,
    getMaxSquareByMoveSize,
    getUniquePointListFromPathList,
    landscapeToNumbers,
} from "./unit-path";
import {getIsUnitPoisoned} from "./unit-state";
import {getUnitAllAvailableAttackPointList} from "./unit-to-unit-attack";
import {getUnitAvailableUnitToBuildingAttack} from "./unit-to-building-attack";

// eslint-disable-next-line complexity
export function getUnitAvailableMove(
    unit: GameUnitType,
    gameState: GameStateType
): Pick<GameAllActionsType, "moveOnAllyPointList" | "movePathList" | "movePointList"> {
    const {
        x,
        y,
        gamePlayerId,
        didMove,
        didAttackUnit,
        didFixBuilding,
        didOccupyBuilding,
        didRaiseSkeleton,
        didAttackBuilding,
    } = unit;
    const {landscape, unitList, playerList, buildingList, stepCount} = gameState;

    if (didMove || didAttackBuilding || didAttackUnit || didFixBuilding || didOccupyBuilding || didRaiseSkeleton) {
        return {
            moveOnAllyPointList: [],
            movePathList: [],
            movePointList: [],
        };
    }

    const {moveSize} = getUnitInfoByUnit(unit);
    const reduceMoveSize = getIsUnitPoisoned(unit, stepCount) ? unitUnderPoisonMoveSizeReduce : 0;
    const reduceMoveToBeLikeOriginalGame = 1;

    const noPathList: Array<PointType> = unitList
        // by enemy unit
        .filter<GameUnitType>((unitInList: GameUnitType): unitInList is GameUnitType => {
            return (
                getTeamIdByPlayerId(gamePlayerId, playerList) !==
                getTeamIdByPlayerId(unitInList.gamePlayerId, playerList)
            );
        })
        .map<PointType>((unitToPoint: GameUnitType): PointType => {
            return {x: unitToPoint.x, y: unitToPoint.y};
        });

    const landscapeNumbers: Array<Array<number>> = landscapeToNumbers(unit, landscape, buildingList, noPathList);

    // console.timeStamp("getAvailableMovePathList");
    const movePathList: Array<MovePathType> = getAvailableMovePathList(
        {x, y},
        moveSize - reduceMoveSize - reduceMoveToBeLikeOriginalGame,
        landscapeNumbers
    );
    // console.timeEnd("getAvailableMovePathList");

    // console.timeStamp("getAvailableMovePathListWasm" + random);
    // const movePathListWasm: Array<MovePathType> = getAvailableMovePathListWasm(
    //     x,
    //     y,
    //     moveSize - reduceMoveSize - reduceMoveToBeLikeOriginalGame,
    //     new Int32Array(landscapeNumbers.flat()),
    //     landscapeNumbers.at(0)?.length ?? 0
    // );
    // console.timeEnd("getAvailableMovePathListWasm" + random);

    // console.log({
    //     movePathList,
    //     movePathListWasm,
    // });

    const moveOnAllyPointList: Array<PointType> = getUniquePointListFromPathList(movePathList)
        // filter by units enemy units
        .filter<PointType>((point: PointType): point is PointType => {
            if (point.x === x && point.y === y) {
                return false;
            }

            return !unitList.some((unitInList: GameUnitType): boolean => {
                return unitInList.x === point.x && unitInList.y === point.y && unitInList.gamePlayerId !== gamePlayerId;
            });
        });

    const movePointList: Array<PointType> = moveOnAllyPointList
        // filter by any units
        .filter<PointType>((point: PointType): point is PointType => {
            return !unitList.some((unitInList: GameUnitType): boolean => {
                return unitInList.x === point.x && unitInList.y === point.y;
            });
        });

    return {
        moveOnAllyPointList,
        movePathList,
        movePointList,
    };
}

function getUnitAvailableUnitToUnitAttack(unit: GameUnitType, gameState: GameStateType): Array<PointType> {
    const {playerList, unitList} = gameState;
    const playerTeamId: GamePlayerTeamIdEnum = getTeamIdByPlayerId(unit.gamePlayerId, playerList);

    return getUnitAllAvailableAttackPointList(unit, gameState).filter<PointType>(
        (point: PointType): point is PointType => {
            const unitByPoint = getUnitByPoint(point, unitList);

            if (!unitByPoint) {
                return false;
            }

            const unitByPointTeamId = getTeamIdByPlayerId(unitByPoint.gamePlayerId, playerList);

            return unitByPointTeamId !== playerTeamId;
        }
    );
}

// eslint-disable-next-line complexity
function getUnitAvailableOccupyBuilding(unit: GameUnitType, gameState: GameStateType): boolean {
    const {occupyBuildingList} = getUnitInfoByUnit(unit);
    const {x, y, didAttackUnit, didRaiseSkeleton, didFixBuilding, didOccupyBuilding, didAttackBuilding, gamePlayerId} =
        unit;
    const pointData = getByPoint({x, y}, gameState);
    const {playerList} = gameState;
    const {building} = pointData;

    if (didAttackBuilding || didAttackUnit || didFixBuilding || didOccupyBuilding || didRaiseSkeleton) {
        return false;
    }

    if (!building) {
        return false;
    }

    if (building.state !== GameBuildingStateEnum.buildingOk) {
        return false;
    }

    if (getTeamIdByPlayerId(building.gamePlayerId, playerList) === getTeamIdByPlayerId(gamePlayerId, playerList)) {
        return false;
    }

    return occupyBuildingList.includes(building.buildingType);
}

// eslint-disable-next-line complexity
function getUnitAvailableFixBuilding(unit: GameUnitType, gameState: GameStateType): boolean {
    const {canFixBuilding} = getUnitInfoByUnit(unit);
    const {x, y, didAttackUnit, didRaiseSkeleton, didFixBuilding, didOccupyBuilding, didAttackBuilding} = unit;

    if (didAttackBuilding || didAttackUnit || didFixBuilding || didOccupyBuilding || didRaiseSkeleton) {
        return false;
    }

    if (!canFixBuilding) {
        return false;
    }

    const pointData = getByPoint({x, y}, gameState);
    const {building} = pointData;

    if (!building) {
        return false;
    }

    return building.state === GameBuildingStateEnum.buildingBroken;
}

// eslint-disable-next-line complexity
function getRaiseSkeletonPointList(unit: GameUnitType, gameState: GameStateType): Array<PointType> {
    const {raiseSkeletonRange} = getUnitInfoByUnit(unit);
    const {landscape, graveList, unitList} = gameState;
    const mapSize: SizeType = getLandscapeSize(landscape);
    const {x, y, didAttackUnit, didRaiseSkeleton, didFixBuilding, didOccupyBuilding, didAttackBuilding} = unit;

    if (raiseSkeletonRange === 0) {
        return [];
    }

    if (didAttackBuilding || didAttackUnit || didFixBuilding || didOccupyBuilding || didRaiseSkeleton) {
        return [];
    }

    const maxPointList = getMaxSquareByMoveSize({x, y}, mapSize, raiseSkeletonRange);

    return maxPointList.filter<PointType>((point: PointType): point is PointType => {
        return Boolean(getGraveByPoint(point, graveList)) && !getUnitByPoint(point, unitList);
    });
}

// eslint-disable-next-line max-statements
export function getUnitAvailableActionsData(unit: GameUnitType, gameState: GameStateType): GameAllActionsType {
    const {x, y, unitId, gamePlayerId} = unit;
    const unitOverUnit = getUnitOverUnit(gameState.unitList);
    const {movePointList, movePathList, moveOnAllyPointList} = getUnitAvailableMove(unit, gameState);

    if (unitOverUnit) {
        return {
            attackUnitToBuildingPointList: [],
            attackUnitToUnitPointList: [],
            canFixBuilding: false,
            canOccupyBuilding: false,
            canOpenStore: false,
            moveOnAllyPointList,
            movePathList,
            movePointList,
            raiseSkeletonPointList: [],
            unitCurrentPoint: {x, y},
            unitId,
        };
    }

    const isCurrentPlayerUnit: boolean = gamePlayerId === gameState.activeGamePlayerId;

    const canOpenStore = getCanOpenStoreByPoint({x, y}, gameState);
    const unitCurrentPoint: PointType = {x, y};

    if (isCurrentPlayerUnit) {
        const attackUnitToUnitPointList = getUnitAvailableUnitToUnitAttack(unit, gameState);
        const attackUnitToBuildingPointList = getUnitAvailableUnitToBuildingAttack(unit, gameState);
        const canOccupyBuilding = getUnitAvailableOccupyBuilding(unit, gameState);
        const canFixBuilding = getUnitAvailableFixBuilding(unit, gameState);
        const raiseSkeletonPointList = getRaiseSkeletonPointList(unit, gameState);

        return {
            attackUnitToBuildingPointList,
            attackUnitToUnitPointList,
            canFixBuilding,
            canOccupyBuilding,
            canOpenStore,
            moveOnAllyPointList,
            movePathList,
            movePointList,
            raiseSkeletonPointList,
            unitCurrentPoint,
            unitId,
        };
    }

    /*
        const filteredUnitList: Array<GameUnitType> = gameState.unitList.filter<GameUnitType>(
            (unitToFilter: GameUnitType): unitToFilter is GameUnitType => {
                return unitToFilter.gamePlayerId !== gameState.activeGamePlayerId;
            }
        );
    */

    const enemyMovePoint = movePointList;
    const enemyMoveOnAllyPointList = moveOnAllyPointList;

    // const {movePointList: enemyMovePoint, moveOnAllyPointList: enemyMoveOnAllyPointList} = getUnitAvailableMove(
    //     unit,
    //     gameState
    // );

    const enemyUnitInfo = getUnitInfoByUnit(unit);
    const {
        canAttackAfterMove,
        attack: {range: enemyUnitAttackRange},
        canMeleeAttack: canEnemyMeleeAttack,
    } = enemyUnitInfo;

    const pointToAttackList: Array<PointType> = canAttackAfterMove ? enemyMovePoint : [{x, y}];
    const attackPointList: Array<PointType> = [];

    const mapSize: SizeType = getLandscapeSize(gameState.landscape);

    pointToAttackList.forEach((movePoint: PointType) => {
        const rejectedPointList: Array<PointType> = canEnemyMeleeAttack
            ? []
            : getMaxSquareByMoveSize(movePoint, mapSize, 1);

        const attackFromPointList: Array<PointType> = getMaxSquareByMoveSize(
            movePoint,
            mapSize,
            enemyUnitAttackRange
        ).filter<PointType>((pointToAttackInList: PointType): pointToAttackInList is PointType => {
            return !getPointByPoint(pointToAttackInList, rejectedPointList);
        });

        attackPointList.push(...attackFromPointList);
    });

    return {
        attackUnitToBuildingPointList: [],
        attackUnitToUnitPointList: attackPointList.filter<PointType>(
            (pointInList: PointType): pointInList is PointType => {
                return !getIsEqualPoint(unitCurrentPoint, pointInList);
            }
        ),
        canFixBuilding: false,
        canOccupyBuilding: false,
        canOpenStore,
        moveOnAllyPointList: enemyMoveOnAllyPointList,
        movePathList: [],
        movePointList: enemyMovePoint.filter<PointType>((pointInList: PointType): pointInList is PointType => {
            return !getIsEqualPoint(unitCurrentPoint, pointInList);
        }),
        raiseSkeletonPointList: [],
        unitCurrentPoint,
        unitId,
    };
}

export function getPointAvailableActionsData(data: GetAllActionsArgType, gameState: GameStateType): GameAllActionsType {
    const {point} = data;
    const unitOverUnit = getUnitOverUnit(gameState.unitList);
    const allActions: GameAllActionsType = {
        attackUnitToBuildingPointList: [],
        attackUnitToUnitPointList: [],
        canFixBuilding: false,
        canOccupyBuilding: false,
        canOpenStore: false,
        moveOnAllyPointList: [],
        movePathList: [],
        movePointList: [],
        raiseSkeletonPointList: [],
        unitCurrentPoint: point,
        unitId: "",
    };

    if (unitOverUnit) {
        return getUnitAvailableActionsData(unitOverUnit[1], gameState);
    }

    const {unit} = getByPoint(point, gameState);

    if (!unit) {
        return allActions;
    }

    return getUnitAvailableActionsData(unit, gameState);
}

// eslint-disable-next-line complexity
export function getHasUnitAvailableAction(unit: GameUnitType, gameState: GameStateType): boolean {
    const {
        attackUnitToBuildingPointList,
        attackUnitToUnitPointList,
        movePointList,
        raiseSkeletonPointList,
        canOccupyBuilding,
        canFixBuilding,
    } = getUnitAvailableActionsData(unit, gameState);

    return (
        canFixBuilding ||
        canOccupyBuilding ||
        attackUnitToBuildingPointList.length > 0 ||
        attackUnitToUnitPointList.length > 0 ||
        movePointList.length > 0 ||
        raiseSkeletonPointList.length > 0
    );
}
