/* global document, HTMLDivElement, Animation, KeyframeAnimationOptions, Keyframe */
import {type ReactNode, type MutableRefObject, useEffect, useRef, useState, useCallback} from "react";
import {createPortal} from "react-dom";

import {useSettingContext} from "../../provider/setting/setting-context";
import {getRandomString} from "../../util/string";
import {useGameContext} from "../../provider/game/game-context";
import type {GamePlayerColorIdEnum} from "../../model/game/player/player-type";
// eslint-disable-next-line max-len
import {colorClassNameMap} from "../../page/client/client-local-game-setup/client-local-game-setup-player/image/image-color";

import gamePopupStyle from "./game-popup.scss";

interface GamePopupPropsType {
    readonly children: ReactNode;
    readonly color?: GamePlayerColorIdEnum | null;
    readonly isOpen: boolean;
    readonly setIsOpen: (isOpen: boolean) => void;
}

export function GamePopup(props: GamePopupPropsType): JSX.Element | null {
    const {
        setting: {
            numberSetting: {animationStepTime},
        },
    } = useSettingContext();
    const {setLocalAnimationList} = useGameContext();
    const {children, isOpen, color, setIsOpen} = props;
    const [isViewOpen, setIsViewOpen] = useState<boolean>(false);
    const overlayNode: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(document.createElement("div"));
    const contentNode: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(document.createElement("div"));
    const popupIdRef: MutableRefObject<string> = useRef<string>(getRandomString());
    const contentShowAnimationRef: MutableRefObject<Animation | null> = useRef<Animation | null>(null);
    const overlayShowAnimationRef: MutableRefObject<Animation | null> = useRef<Animation | null>(null);

    const handleShowOff = useCallback(() => {
        setIsViewOpen(false);
        setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
            const updatedLocalAnimationList: Set<string> = new Set<string>(localAnimationList);

            updatedLocalAnimationList.delete(popupIdRef.current);

            return updatedLocalAnimationList;
        });
    }, [setLocalAnimationList]);

    const handleShowOn = useCallback(() => {
        setIsViewOpen(true);
        setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
            // eslint-disable-next-line unicorn/prefer-spread
            return new Set<string>([...Array.from(localAnimationList), popupIdRef.current]);
        });
    }, [setLocalAnimationList]);

    // eslint-disable-next-line complexity
    useEffect(() => {
        const popupAnimationOptions: KeyframeAnimationOptions = {
            duration: animationStepTime,
            easing: "ease-in-out",
            fill: "forwards",
            iterations: 1,
        };

        const overlayKeyframeList: Array<Keyframe> = [{opacity: 0}, {opacity: 1}];
        const contentKeyframeList: Array<Keyframe> = [
            {opacity: 0, transform: "translate3d(-50%, -50%, 0) scaleY(0)"},
            {opacity: 1, transform: "translate3d(-50%, -50%, 0) scaleY(1)"},
        ];

        const isOpening: boolean = isOpen && !isViewOpen;
        const isClosing: boolean = !isOpen && isViewOpen;

        if (isOpening) {
            handleShowOn();

            contentShowAnimationRef.current?.cancel();
            overlayShowAnimationRef.current?.cancel();

            contentShowAnimationRef.current = contentNode.current.animate(contentKeyframeList, popupAnimationOptions);
            overlayShowAnimationRef.current = overlayNode.current.animate(overlayKeyframeList, popupAnimationOptions);
            return;
        }

        if (isClosing) {
            contentShowAnimationRef.current?.reverse();
            overlayShowAnimationRef.current?.reverse();

            if (contentShowAnimationRef.current) {
                contentShowAnimationRef.current.onfinish = handleShowOff;
            } else {
                console.error("[Game Popup]: NO content animation");
                handleShowOff();
            }
        }
    }, [
        contentShowAnimationRef,
        overlayShowAnimationRef,
        animationStepTime,
        popupIdRef,
        isOpen,
        isViewOpen,
        setLocalAnimationList,
        handleShowOn,
        handleShowOff,
    ]);

    useEffect(() => {
        const popupId = popupIdRef.current;

        return () => {
            setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
                const updatedLocalAnimationList: Set<string> = new Set<string>(localAnimationList);

                updatedLocalAnimationList.delete(popupId);

                return updatedLocalAnimationList;
            });
        };
    }, [popupIdRef, setLocalAnimationList]);

    if (typeof document === "undefined") {
        return null;
    }

    if (!isOpen && !isViewOpen) {
        return null;
    }

    const contentClassName: string = `${gamePopupStyle.game_popup__content} ${
        // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
        color ? colorClassNameMap[color].bgGradient : ""
    }`.trim();

    return createPortal(
        <>
            {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */}
            <div
                className={gamePopupStyle.game_popup__overlay}
                onClick={(): undefined => {
                    setIsOpen(false);
                }}
                ref={overlayNode}
            />
            <div className={contentClassName} ref={contentNode}>
                {children}
            </div>
        </>,
        document.body
    );
}
