import {useCallback, useEffect, useMemo, useState} from "react";
import {useDocumentVisibility} from "react-system-hook";

import {getRandomNumber} from "../../util/number";
import {getNextArrayLoopIndex} from "../../util/array";

import type {AudioPlayerConfigType, AudioPlayerType} from "./audio-player-type";
import {
    type PlayAudioArgumentType,
    getAudioByRow,
    playAudio,
    getAudioRowVolume,
    getAudioRowIsMuted,
} from "./audio-player-helper";

// eslint-disable-next-line sonarjs/cognitive-complexity
export function useAudioPlayer(config: AudioPlayerConfigType): AudioPlayerType {
    const {isPlaying: isPlayingInitial, isShuffle, trackList: trackListInitial, isLoop, audioRow} = config;
    const [isPlaying, setIsPlaying] = useState<boolean>(isPlayingInitial);
    const isMinTwoItems = trackListInitial.length === 1 && (isShuffle || isLoop);
    const trackList = useMemo<Array<string>>((): Array<string> => {
        return isMinTwoItems ? [...trackListInitial, ...trackListInitial] : [...trackListInitial];
    }, [trackListInitial, isMinTwoItems]);
    const trackListLength = trackList.length;
    const [audioIndex, setAudioIndex] = useState<number>(0);
    const volume = getAudioRowVolume(audioRow);
    const isMuted = getAudioRowIsMuted(audioRow);
    const isDocumentVisible = useDocumentVisibility();

    useEffect(() => {
        getAudioByRow(audioRow).muted = !isDocumentVisible || getAudioRowIsMuted(audioRow);
    }, [audioRow, isDocumentVisible]);

    useEffect(() => {
        setIsPlaying(isPlayingInitial);
    }, [isPlayingInitial]);

    useEffect(() => {
        setAudioIndex(isShuffle ? getRandomNumber(0, trackList.length) : 0);
    }, [trackList, isShuffle]);

    // eslint-disable-next-line complexity
    const onAudioEnded = useCallback(() => {
        const nextIndex = isShuffle
            ? getRandomNumber(0, trackListLength)
            : getNextArrayLoopIndex(trackListLength, audioIndex);

        const isShouldPlayNext = isShuffle || isLoop || nextIndex !== 0;

        if (!isShouldPlayNext) {
            return;
        }

        setAudioIndex((currentIndex: number): number => {
            return currentIndex === nextIndex ? getNextArrayLoopIndex(trackListLength, audioIndex) : nextIndex;
        });
    }, [isShuffle, isLoop, audioIndex, trackListLength]);

    useEffect(() => {
        if (!isPlaying) {
            return;
        }

        const src = trackList.at(audioIndex);

        // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
        if (!src) {
            console.error(`can not get audio by index: ${audioIndex}, list: ${JSON.stringify(trackList)}`);
            return;
        }

        const playAudioData: PlayAudioArgumentType = {
            audioRow,
            // IsMuted,
            onEnded: onAudioEnded,
            src,
            // Volume,
        };

        playAudio(playAudioData)
            .then(() => {
                console.log(`[useAudioPlayer]: play - ${src}`);
            })
            .catch((playAudioError: unknown) => {
                console.log(`[ERROR] [useAudioPlayer]: can not play audio: ${src}`);
                console.log(`[ERROR] [useAudioPlayer]: ${String(playAudioError)}`);

                onAudioEnded();
            });
    }, [audioRow, isPlaying, onAudioEnded, trackList, audioIndex, volume, isMuted]);

    const stopAudioInHook = useCallback(() => {
        const currentAudio = getAudioByRow(audioRow);

        try {
            setIsPlaying(false);
            currentAudio.pause();
            currentAudio.currentTime = 0;
        } catch (stopError: unknown) {
            console.log(stopError);
        }
    }, [audioRow]);

    const pauseAudioInHook = useCallback(() => {
        const currentAudio = getAudioByRow(audioRow);

        try {
            setIsPlaying(false);
            currentAudio.pause();
        } catch (pauseError: unknown) {
            console.log(pauseError);
        }
    }, [audioRow]);

    const playAudioInHook = useCallback(async () => {
        const currentAudio = getAudioByRow(audioRow);

        try {
            setAudioIndex((currentIndex: number): number => {
                return isShuffle ? getRandomNumber(0, trackListLength) : currentIndex;
            });
            setIsPlaying(true);
            await currentAudio.play();
            // CurrentAudio.currentTime = 0;
        } catch (playError: unknown) {
            console.log(playError);
        }
    }, [audioRow, isShuffle, trackListLength]);

    return useMemo<AudioPlayerType>((): AudioPlayerType => {
        return {
            isPlaying,
            pause: pauseAudioInHook,
            // eslint-disable-next-line @typescript-eslint/no-misused-promises
            play: playAudioInHook,
            stop: stopAudioInHook,
        };
    }, [playAudioInHook, stopAudioInHook, pauseAudioInHook, isPlaying]);
}
