import type {GameBuildingType} from "../building/building-type";

import type {PointType} from "./point-type";

export function getPointByPoint(pointToFind: PointType, pointList: Array<PointType>): PointType | null {
    return (
        pointList.find<PointType>((pointInList: PointType): pointInList is GameBuildingType => {
            return pointInList.x === pointToFind.x && pointInList.y === pointToFind.y;
        }) ?? null
    );
}

export function getIsPointInList(pointToFind: PointType, pointList: Array<PointType>): boolean {
    return pointList.some((pointInList: PointType): boolean => {
        return pointInList.x === pointToFind.x && pointInList.y === pointToFind.y;
    });
}

export function getIsEqualPoint(pointA: PointType, pointB: PointType): boolean {
    return pointA.x === pointB.x && pointA.y === pointB.y;
}

export function getStepsBetweenPoints(pointA: PointType, pointB: PointType): number {
    return Math.abs(pointA.x - pointB.x) + Math.abs(pointA.y - pointB.y);
}

