import type {ReactNode} from "react";
import {Link} from "react-router-dom";

import type {LangKeyType} from "../../provider/locale/translation/type";
import {Locale} from "../../provider/locale/locale-context";
import {cls} from "../../util/css";
import {usePlayClick} from "../../media/play-media";

import uiButtonStyle from "./ui-button.scss";

interface UiLinkPropsType {
    readonly children?: ReactNode;
    // eslint-disable-next-line unicorn/no-keyword-prefix
    readonly className?: string;
    readonly href: string;
    readonly iconSrc?: string;
    readonly isActive?: boolean;
    readonly isDisabled?: boolean;
    readonly isSquared?: boolean;
    readonly isTextToCenter?: boolean;
    readonly langKey?: LangKeyType;
}

export function UiLink(props: UiLinkPropsType): JSX.Element {
    // eslint-disable-next-line unicorn/no-keyword-prefix
    const {href, isActive, isTextToCenter, isDisabled, iconSrc, langKey, children, isSquared, className} = props;
    const {play: playClick} = usePlayClick();

    return (
        <Link
            className={cls(
                uiButtonStyle.ui_button,
                {
                    [uiButtonStyle.ui_button__disabled]: isDisabled,
                    [uiButtonStyle.ui_button__squared]: isSquared,
                    [uiButtonStyle.ui_button__with_image]: Boolean(iconSrc),
                    [uiButtonStyle.ui_button__active]: Boolean(isActive),
                    // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
                    [uiButtonStyle.ui_button__squared__image_only]: Boolean(!children) && !langKey,
                },
                className
            )}
            // eslint-disable-next-line @typescript-eslint/no-misused-promises
            onClick={playClick}
            to={href}
        >
            {
                // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
                iconSrc ? <img alt={langKey} className={uiButtonStyle.ui_button__icon} src={iconSrc} /> : null
            }
            <span
                className={cls(uiButtonStyle.ui_button__text, {
                    [uiButtonStyle.ui_button__text__align_center]: isTextToCenter,
                })}
            >
                {langKey ? <Locale stringKey={langKey} /> : null}
                {children}
            </span>
        </Link>
    );
}
