import type {ActionArgumentType, GameAnimationType} from "../game-context-type";
import type {GameGraveType, GameUnitType} from "../../../model/game/unit/unit-type";
import {getUnitToUnitAttackResult} from "../../../model/game/unit/unit-to-unit-attack";
import type {GameStateType} from "../../../model/game/game-type";
import {getUnitByPoint, getUnitInfoByUnit} from "../../../model/game/unit/unit-helper";
import {waitForNextFrame} from "../../../util/time";
import type {PromiseResolveType} from "../../../util/promise";
import {removeGraveByPointProcedure} from "../../../model/game/grave/grave-helper";
import type {PointType} from "../../../model/game/point/point-type";

// eslint-disable-next-line sonarjs/cognitive-complexity
export async function unitToUnitAttackAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState} = data;
    const unitAggressor: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, gameState.unitList);
    const unitDefender: GameUnitType | null = getUnitByPoint(action.toPoint, gameState.unitList);

    if (!unitAggressor || !unitDefender) {
        return waitForNextFrame();
    }

    const {aggressor, defender} = getUnitToUnitAttackResult(
        {aggressor: unitAggressor, defender: unitDefender},
        gameState
    );

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>): Array<GameAnimationType> => {
            return [
                ...animList,
                {
                    action,
                    // eslint-disable-next-line @typescript-eslint/no-misused-promises
                    animationOnFinish: async (): Promise<void> => {
                        setGameState((currentGameState: GameStateType): GameStateType => {
                            const updatedUnitList = [...currentGameState.unitList];
                            const updatedGraveList = [...currentGameState.graveList];
                            const updatedCemetery = [...currentGameState.cemetery];
                            const {stepCount} = currentGameState;

                            [aggressor, defender].forEach((unitToUpdate: GameUnitType, index: number) => {
                                const unitToUpdatePoint: PointType = {
                                    x: unitToUpdate.x,
                                    y: unitToUpdate.y,
                                };
                                const unitInListToUpdate: GameUnitType | null = getUnitByPoint(
                                    unitToUpdatePoint,
                                    updatedUnitList
                                );
                                const isAggressor = index === 0;
                                const unitInfo = getUnitInfoByUnit(unitToUpdate);

                                if (!unitInListToUpdate) {
                                    console.error("Can not find unit in list to update:");
                                    console.log("unitToUpdate");
                                    console.log(unitToUpdate);
                                    return;
                                }

                                if (unitToUpdate.health <= 0) {
                                    updatedUnitList.splice(updatedUnitList.indexOf(unitInListToUpdate), 1);

                                    if (unitInfo.hasGrave) {
                                        removeGraveByPointProcedure(unitToUpdatePoint, updatedGraveList);

                                        const updatedGrave: GameGraveType = {
                                            createdStepCount: stepCount,
                                            ...unitToUpdatePoint,
                                        };

                                        updatedGraveList.push(updatedGrave);
                                    }
                                    if (unitInfo.isHero) {
                                        updatedCemetery.push({
                                            ...unitInListToUpdate,
                                            deathCount: unitInListToUpdate.deathCount + 1,
                                        });
                                    }
                                    return;
                                }

                                updatedUnitList[updatedUnitList.indexOf(unitInListToUpdate)] = {
                                    ...unitToUpdate,
                                    didAttackUnit: isAggressor,
                                };
                            });

                            const updatedGameState: GameStateType = {
                                ...currentGameState,
                                cemetery: updatedCemetery,
                                graveList: updatedGraveList,
                                unitList: updatedUnitList,
                            };

                            return updatedGameState;
                        });
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
