import type {GameUnitType} from "../../../../../model/game/unit/unit-type";
import {cls} from "../../../../../util/css";
import {colorClassNameMap} from "../../../client-local-game-setup/client-local-game-setup-player/image/image-color";

import {getImageListByUnit} from "../../client-game-unit/client-game-unit-item/client-game-unit-item-helper";
import {useGameContext} from "../../../../../provider/game/game-context";
import {getPlayerColorIdByPlayerId} from "../../../../../model/game/player/player-helper";
import type {GamePlayerColorIdEnum} from "../../../../../model/game/player/player-type";
import {BigText} from "../../../../../layout/game-font/text/big-text";

import clientGameUnitFightBottomBarStyle from "./client-game-unit-fight-bottom-bar.scss";

interface ClientGameUnitFightBottomBarPropsType {
    readonly aggressor: GameUnitType;
    readonly defender: GameUnitType;
}

export function ClientGameUnitFightBottomBar(props: ClientGameUnitFightBottomBarPropsType): JSX.Element {
    const {aggressor, defender} = props;
    const {gameState} = useGameContext();
    const [aggressorUnitImage] = getImageListByUnit(aggressor, gameState.playerList);
    const [defenderUnitImage] = getImageListByUnit(defender, gameState.playerList);
    const aggressorColor: GamePlayerColorIdEnum = getPlayerColorIdByPlayerId(
        aggressor.gamePlayerId,
        gameState.playerList
    );
    const defenderColor: GamePlayerColorIdEnum = getPlayerColorIdByPlayerId(
        defender.gamePlayerId,
        gameState.playerList
    );

    return (
        <div className={clientGameUnitFightBottomBarStyle.client_game_unit_fight_bottom_bar}>
            <div className={clientGameUnitFightBottomBarStyle.client_game_unit_fight_bottom_bar__frame} />
            <div className={clientGameUnitFightBottomBarStyle.client_game_unit_fight_bottom_bar__unit_list}>
                <div
                    className={cls(
                        colorClassNameMap[aggressorColor].bgGradient,
                        clientGameUnitFightBottomBarStyle.client_game_unit_fight_bottom_bar__unit__aggressor
                    )}
                >
                    <img
                        alt="aggresor"
                        className={
                            clientGameUnitFightBottomBarStyle.client_game_unit_fight_bottom_bar__unit__image__aggressor
                        }
                        src={aggressorUnitImage.src}
                    />
                    <BigText scale={2} text={aggressor.health.toString(10).padEnd(3, "S")} />
                </div>
                <div
                    className={cls(
                        colorClassNameMap[defenderColor].bgGradient,
                        clientGameUnitFightBottomBarStyle.client_game_unit_fight_bottom_bar__unit__defender
                    )}
                >
                    <BigText scale={2} text={defender.health.toString(10).padStart(3, "S")} />
                    <img
                        alt="defender"
                        className={
                            clientGameUnitFightBottomBarStyle.client_game_unit_fight_bottom_bar__unit__image__defender
                        }
                        src={defenderUnitImage.src}
                    />
                </div>
            </div>
        </div>
    );
}
