export const emptySprite: SpriteType = {
    height: 1,
    imgSrc: getImage(emptyPixel).src,
    offsetLeft: 0,
    offsetTop: 0,
    scaleX: 1,
    scaleY: 1,
    width: 1,
};

export const [spriteSmoke0, spriteSmoke1, spriteSmoke2, spriteSmoke3] = [
    smoke0,
    smoke1,
    smoke2,
    smoke3,
].map<SpriteType>((imgSrc: string): SpriteType => {
    return {
        height: 20,
        imgSrc: getImage(imgSrc).src,
        offsetLeft: 0,
        offsetTop: 0,
        scaleX: 1,
        scaleY: 1,
        width: 24,
    };
});

