import {Page} from "../../../layout/page/page";
import {PageHeader} from "../../../layout/page/page-header/page-header";
import {useLocale} from "../../../provider/locale/locale-context";
import {PageContent} from "../../../layout/page/page-content/page-content";
import {Markdown} from "../../../layout/markdown/markdown";
import type {LangKeyType} from "../../../provider/locale/translation/type";
import {usePlayTitleTheme} from "../../../media/play-media";

import {clientInstructionsKeyList} from "./client-instructions-const";
import clientInstructionsStyle from "./client-instructions.scss";

export function ClientInstructions(): JSX.Element {
    const {getLocalizedString} = useLocale();

    usePlayTitleTheme();

    const localizedString: string = clientInstructionsKeyList
        .map<string>((langKey: LangKeyType, index: number): string => {
            return `${(index + 1).toString(10).padStart(2, "0")} - ${getLocalizedString(langKey)}`;
        })
        .join("\n\n");

    return (
        <Page>
            <PageHeader langKey="UI__BUTTON__INSTRUCTIONS" />
            <PageContent hasBorder>
                <Markdown className={clientInstructionsStyle.client_instructions__markdown} mdInput={localizedString} />
            </PageContent>
        </Page>
    );
}
