import {getRandomNumber} from "../../../util/number";

import clientGameStyle from "./client-game.scss";

const clientGameBgClassNameList = [
    clientGameStyle.client_game__bg_01,
    clientGameStyle.client_game__bg_02,
    clientGameStyle.client_game__bg_03,
    clientGameStyle.client_game__bg_04,
    clientGameStyle.client_game__bg_05,
    clientGameStyle.client_game__bg_06,
    // ClientGameStyle.client_game__bg_07,
    clientGameStyle.client_game__bg_08,
] as const;

export function getRandomGamePageClassName(): string {
    return (
        clientGameBgClassNameList.at(getRandomNumber(0, clientGameBgClassNameList.length)) ??
        clientGameStyle.client_game__bg_01
    );
}
