/* global document, HTMLDivElement, Animation, KeyframeAnimationOptions, Keyframe */

import {
    type Dispatch,
    type SetStateAction,
    type MutableRefObject,
    useEffect,
    useRef,
    useState,
    useCallback,
} from "react";
import {createPortal} from "react-dom";

import {useSettingContext} from "../../../provider/setting/setting-context";
import {UiButton} from "../../ui-button/ui-button";
import {Markdown} from "../../markdown/markdown";
import {useLocale} from "../../../provider/locale/locale-context";
import type {GameFacedSnackbarType} from "../game-faced-snackbar-type";
import {snackbarUiImage} from "../../game-snackbar/image/sknackbar-image";
import {cls} from "../../../util/css";

import facedSnackbarItemStyle from "./game-faced-snackbar-item.scss";

interface GameSnackbarPropsType {
    readonly facedData: GameFacedSnackbarType;
    readonly index: number;
    readonly isOpenList: Array<boolean>;
    readonly onHideLastItem: () => void;
    readonly setIsOpenList: Dispatch<SetStateAction<Array<boolean>>>;
    readonly useClosingOverlay: boolean;
}

// eslint-disable-next-line complexity, sonarjs/cognitive-complexity
export function GameFacedSnackbarItem(props: GameSnackbarPropsType): JSX.Element | null {
    const {
        setting: {
            numberSetting: {animationStepTime},
        },
    } = useSettingContext();
    const {getLocalizedString} = useLocale();
    const {isOpenList, setIsOpenList, onHideLastItem, index, facedData, useClosingOverlay} = props;
    const [isViewOpen, setIsViewOpen] = useState<boolean>(false);
    const {faceSrc, markdownLangKey} = facedData;
    const contentNode = useRef<HTMLDivElement>(document.createElement("div"));
    const isOpen = isOpenList[index];
    const contentShowAnimationRef: MutableRefObject<Animation | null> = useRef<Animation | null>(null);

    const handleShowOff = useCallback(() => {
        setIsViewOpen(false);
    }, []);

    const handleShowOn = useCallback(() => {
        setIsViewOpen(true);
    }, []);

    // eslint-disable-next-line complexity
    useEffect(() => {
        const snackbarAnimationOptions: KeyframeAnimationOptions = {
            duration: animationStepTime * 2,
            easing: "ease-in-out",
            fill: "forwards",
            iterations: 1,
        };

        const contentKeyframeList: Array<Keyframe> = [
            {opacity: 0, transform: "translate3d(-50%, 200%, 0)"},
            {opacity: 1, transform: "translate3d(-50%, 0, 0)"},
        ];

        function onClosingFinish(): undefined {
            handleShowOff();

            const nextIndex = index + 1;

            if (!(nextIndex in isOpenList)) {
                onHideLastItem();
            }
        }

        const isOpening: boolean = isOpen && !isViewOpen;
        const isClosing: boolean = !isOpen && isViewOpen;

        if (isOpening) {
            handleShowOn();

            contentShowAnimationRef.current?.cancel();

            contentShowAnimationRef.current = contentNode.current.animate(
                contentKeyframeList,
                snackbarAnimationOptions
            );
            return;
        }

        if (isClosing) {
            if (contentShowAnimationRef.current) {
                if (contentShowAnimationRef.current.playbackRate > 0) {
                    contentShowAnimationRef.current.reverse();
                    contentShowAnimationRef.current.onfinish = onClosingFinish;
                }
                return;
            }
            console.error("[Face Game Snackbar]: NO content animation");
            onClosingFinish();
        }
    }, [animationStepTime, handleShowOff, handleShowOn, index, isOpen, isOpenList, isViewOpen, onHideLastItem]);

    const hideSnackbar = useCallback(() => {
        setIsOpenList((currentIsOpenList: Array<boolean>): Array<boolean> => {
            const updatedIsOpenList: Array<boolean> = [...currentIsOpenList];
            const nextIndex = index + 1;

            updatedIsOpenList[index] = false;

            if (nextIndex in currentIsOpenList) {
                updatedIsOpenList[nextIndex] = true;
            }

            return updatedIsOpenList;
        });
    }, [index, setIsOpenList]);

    if (typeof document === "undefined") {
        return null;
    }

    if (!isOpen && !isViewOpen) {
        return null;
    }

    return createPortal(
        <>
            {useClosingOverlay ? (
                /* eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */
                <div className={facedSnackbarItemStyle.faced_snackbar__overlay} onClick={hideSnackbar} />
            ) : null}
            <div className={facedSnackbarItemStyle.faced_snackbar__content} ref={contentNode}>
                <div className={facedSnackbarItemStyle.faced_snackbar__content_wrapper}>
                    {faceSrc ? (
                        <img
                            alt={markdownLangKey}
                            className={facedSnackbarItemStyle.faced_snackbar__face}
                            src={faceSrc}
                        />
                    ) : null}
                    <Markdown
                        className={cls(
                            facedSnackbarItemStyle.faced_snackbar__markdown_item,
                            faceSrc ? "" : facedSnackbarItemStyle.faced_snackbar__markdown_item__no_image
                        )}
                        mdInput={getLocalizedString(markdownLangKey)}
                    />
                </div>
                <UiButton iconSrc={snackbarUiImage.close.src} isSquared onClick={hideSnackbar} />
            </div>
        </>,
        document.body
    );
}
