import wasmInit, {getAvailableMovePathList as getAvailableMovePathListWasm} from "get-available-move-path-list";
import wasmData from "get-available-move-path-list/get_available_move_path_list_bg.wasm";

import {type GameLandscapeType, GameLandscapeItemEnum} from "../landscape/landscape-type";
import {landscapeMoveCostMap} from "../landscape/landscape-const";
import type {PointType} from "../point/point-type";
import type {GameBuildingType} from "../building/building-type";
import {getBuildingByPoint} from "../building/building-helper";
import {getIsEqualPoint} from "../point/point-helper";
import type {SizeType} from "../../map/map-type";

import type {GameUnitType, UnitInfoType} from "./unit-type";
import {getUnitInfoByUnit} from "./unit-helper";

// eslint-disable-next-line jest/require-hook
export let isWasmInitialized = false;

export interface MovePathType {
    pointList: Array<PointType>;
    restMove: number;
}

function getIsInPathList(pathList: Array<PointType>, point: PointType): boolean {
    return pathList.some((pointToTest: PointType): pointToTest is PointType => {
        return getIsEqualPoint(pointToTest, point);
    });
}

/* eslint-disable-next-line no-unused-vars, @typescript-eslint/no-unused-vars */
export function getUniquePointListFromPathList(movePathList: Array<MovePathType>): Array<PointType> {
    const pointList: Array<PointType> = [];

    movePathList.forEach((movePath: MovePathType) => {
        movePath.pointList.forEach((point: PointType) => {
            if (getIsInPathList(pointList, point)) {
                return;
            }

            pointList.push(point);
        });
    });

    return pointList;
}

/* eslint-disable-next-line no-unused-vars, @typescript-eslint/no-unused-vars,@typescript-eslint/max-params */
export function landscapeToNumbers(
    unit: GameUnitType,
    landscape: GameLandscapeType,
    buildingList: Array<GameBuildingType>,
    noPathList: Array<PointType>
): Array<Array<number>> {
    const unitInfo: UnitInfoType = getUnitInfoByUnit(unit);
    const {moveType} = unitInfo;
    const costMap = landscapeMoveCostMap[moveType];
    const unavailablePathSize = 1e3;

    return landscape.map<Array<number>>((line: Array<GameLandscapeItemEnum>, y: number): Array<number> => {
        return line.map<number>((landscapeItem: GameLandscapeItemEnum, x: number): number => {
            if (getIsInPathList(noPathList, {x, y})) {
                return unavailablePathSize;
            }

            if (getBuildingByPoint({x, y}, buildingList)) {
                return costMap[GameLandscapeItemEnum.rd0];
            }

            return costMap[landscapeItem];
        });
    });
}

function getClosePoint(start: PointType, landscape: Array<Array<number>>): Array<PointType> {
    const {x, y} = start;
    const pointUp: PointType = {x, y: y - 1};
    const pointDown: PointType = {x, y: y + 1};
    const pointLeft: PointType = {x: x - 1, y};
    const pointRight: PointType = {x: x + 1, y};

    return [pointUp, pointDown, pointLeft, pointRight].filter<PointType>((point: PointType): point is PointType => {
        return Boolean(landscape[point.y]?.[point.x]);
    });
}

function getAvailableClosePoint(start: PointType, moveSize: number, landscape: Array<Array<number>>): Array<PointType> {
    const pointList: Array<PointType> = getClosePoint(start, landscape);

    return pointList.filter<PointType>((point: PointType): point is PointType => {
        return moveSize >= landscape[point.y][point.x];
    });
}

function makeNewPathList(movePath: MovePathType, landscape: Array<Array<number>>): Array<MovePathType> {
    const lastPointList = movePath.pointList.at(-1);

    if (!lastPointList) {
        console.error("can not get lastPointList movePath.pointList.at(-1)");
        return [];
    }

    const closePointList: Array<PointType> = getAvailableClosePoint(lastPointList, movePath.restMove, landscape);

    return closePointList.map<MovePathType>((point: PointType): MovePathType => {
        return {
            pointList: [...movePath.pointList, point],
            restMove: movePath.restMove - landscape[point.y][point.x],
        };
    });
}

function getAvailableMovePathListRecursive(
    pathListToExpand: Array<MovePathType>,
    storedPathList: Array<MovePathType>,
    landscape: Array<Array<number>>
): Array<MovePathType> {
    const updatedPathListToExpand: Array<MovePathType> = [];
    const defaultPoint: PointType = {x: Number.NaN, y: Number.NaN};

    storedPathList.push(...pathListToExpand);

    pathListToExpand.forEach((movePath: MovePathType) => {
        // check for storedPathList already has path from\to point
        const updatedMovePathList: Array<MovePathType> = makeNewPathList(movePath, landscape).filter(
            (updatedMovePath: MovePathType): updatedMovePath is MovePathType => {
                // eslint-disable-next-line complexity
                const isPathAlreadyExists = storedPathList.some((storedMovePath: MovePathType): boolean => {
                    const storedMoviePathStart: PointType = storedMovePath.pointList.at(0) ?? defaultPoint;
                    const storedMoviePathEnd: PointType = storedMovePath.pointList.at(-1) ?? defaultPoint;
                    const updatedMoviePathStart: PointType = updatedMovePath.pointList.at(0) ?? defaultPoint;
                    const updatedMoviePathEnd: PointType = updatedMovePath.pointList.at(-1) ?? defaultPoint;

                    return (
                        getIsEqualPoint(storedMoviePathStart, updatedMoviePathStart) &&
                        getIsEqualPoint(storedMoviePathEnd, updatedMoviePathEnd)
                    );
                });

                return !isPathAlreadyExists;
            }
        );

        updatedPathListToExpand.push(...updatedMovePathList);
    });

    if (updatedPathListToExpand.length === 0) {
        return storedPathList;
    }

    return getAvailableMovePathListRecursive(updatedPathListToExpand, storedPathList, landscape);
}

export function getAvailableMovePathList(
    start: PointType,
    moveSize: number,
    landscape: Array<Array<number>>
): Array<MovePathType> {
    if (isWasmInitialized) {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
        const movePathListWasm: Array<MovePathType> = getAvailableMovePathListWasm(
            start.x,
            start.y,
            moveSize,
            new Int32Array(landscape.flat()),
            landscape.at(0)?.length ?? landscape.length
        );

        return movePathListWasm;
    }

    return getAvailableMovePathListRecursive(
        [
            {
                pointList: [start],
                restMove: moveSize,
            },
        ],
        [],
        landscape
    );
}

export function getMovePathFromToByPathList(
    start: PointType,
    end: PointType,
    pathList: Array<MovePathType>
): Array<PointType> {
    if (getIsEqualPoint(start, end)) {
        return [start];
    }

    const filteredPathList: Array<MovePathType> = pathList.filter<MovePathType>(
        (movePath: MovePathType): movePath is MovePathType => {
            const {pointList} = movePath;
            const listPoint = pointList.at(-1);

            if (!listPoint) {
                console.error("can not get last point - pointList.at(-1)");
            }

            return listPoint ? getIsEqualPoint(end, listPoint) : true;
        }
    );

    const sortedPathList: Array<MovePathType> = filteredPathList.sort(
        (movePathA: MovePathType, movePathB: MovePathType): number => {
            return movePathA.pointList.length - movePathB.pointList.length;
        }
    );

    const shorMovePath = sortedPathList.at(0);

    if (!shorMovePath) {
        console.error("shorMovePath is undefined");
        console.error("pathList", pathList);
        return [];
    }

    return shorMovePath.pointList;
}

