// eslint-disable-next-line complexity
export function getMaxSquareByMoveSize(point: PointType, mapSize: SizeType, moveSize: number): Array<PointType> {
    const result: Array<PointType> = [];
    const {x: startX, y: startY} = point;
    const {height, width} = mapSize;

    // eslint-disable-next-line no-loops/no-loops
    for (let x = -moveSize; x <= moveSize; x += 1) {
        // eslint-disable-next-line no-loops/no-loops
        for (let y = -moveSize; y <= moveSize; y += 1) {
            const pointX: number = startX + x;
            const pointY: number = startY + y;

            if (Math.abs(x) + Math.abs(y) > moveSize) {
                // eslint-disable-next-line no-continue
                continue;
            }

            if (pointX === startX && pointY === startY) {
                // eslint-disable-next-line no-continue
                continue;
            }

            if (pointX < 0 || pointY < 0 || pointX >= width || pointY >= height) {
                // eslint-disable-next-line no-continue
                continue;
            }

            result.push({x: pointX, y: pointY});
        }
    }

    return result;
}

// eslint-disable-next-line jest/require-hook
wasmInit(wasmData)
    .then(() => {
        // compare results from getAvailableMovePathListWasm and getAvailableMovePathListRecursive
        const start: PointType = {x: 6, y: 7};
        const moveSize = 15;
        const landscape: Array<Array<number>> = [
            [2, 1, 2, 1, 2, 1, 2, 1],
            [1, 2, 1, 1, 1, 2, 1, 1],
            [2, 1, 2, 1, 2, 1, 2, 1],
            [1, 3, 1, 3, 3, 3, 1, 3],
            [3, 1, 3, 3, 3, 1, 3, 1],
            [1, 2, 1, 2, 1, 2, 1, 2],
            [2, 1, 2, 1, 2, 1, 2, 1],
            [1, 2, 1, 1, 1, 2, 1, 1],
        ];

        console.time("getAvailableMovePathListWasm");
        // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
        const wasmPathList: Array<MovePathType> = getAvailableMovePathListWasm(
            start.x,
            start.y,
            moveSize,
            new Int32Array(landscape.flat()),
            landscape.at(0)?.length ?? 1
        );
        console.timeEnd("getAvailableMovePathListWasm");

        console.time("getAvailableMovePathListJs");
        const jsPathList: Array<MovePathType> = getAvailableMovePathListRecursive(
            [
                {
                    pointList: [start],
                    restMove: moveSize,
                },
            ],
            [],
            landscape
        );
        console.timeEnd("getAvailableMovePathListJs");

        if (JSON.stringify(wasmPathList) !== JSON.stringify(jsPathList)) {
            console.log({jsPathList, wasmPathList});
            throw new Error("getAvailableMovePathListWasm and getAvailableMovePathListRecursive are different!");
        }

        isWasmInitialized = true;

        console.log("[wasmInit]: done");
    })
    // eslint-disable-next-line unicorn/prefer-top-level-await
    .catch((error: Error) => {
        console.error(error.message);
    });
