/* eslint-disable multiline-comment-style, capitalized-comments, line-comment-position, multiline-comment-style */

export const levelUnitData = {
    additionalArmor: 2,
    additionalAttack: 2,
    base: 200,
    experience: {
        destroyBuilding: 60,
    },
    max: 9,
    min: 0,
} as const;

export interface GameUnitLevelDataType {
    experience: {
        // from: number;
        // part: number;
        // percent: number;
        // size: number;
        to: number;
        value: number;
    };
    level: number;
}

export function getUnitLevelData(damageGiven: number): GameUnitLevelDataType {
    const experience = damageGiven;
    const maxLevel = levelUnitData.max;
    const levelBase = levelUnitData.base;

    let experienceFrom = 0;
    let experienceTo = 0;

    // eslint-disable-next-line no-loops/no-loops
    for (let level = 0; level < maxLevel; level += 1) {
        experienceFrom = level * levelBase;
        experienceTo = (level + 1) * levelBase;

        if (experience >= experienceFrom && experience < experienceTo) {
            return {
                experience: {
                    // from: experienceFrom,
                    // part: experience - experienceFrom,
                    // percent: ((experience - experienceFrom) / (experienceTo - experienceFrom)) * 100,
                    // size: experienceTo - experienceFrom,
                    to: experienceTo,
                    value: experience,
                },
                level,
            };
        }
    }

    console.log("--- max level ---");

    return {
        experience: {
            // from: experienceFrom,
            // part: experience - experienceFrom,
            // percent: ((experience - experienceFrom) / (experienceTo - experienceFrom)) * 100,
            // size: experienceTo - experienceFrom,
            to: experienceTo,
            value: experience,
        },
        level: levelUnitData.max,
    };
}

