/* eslint-disable multiline-comment-style, capitalized-comments, line-comment-position, multiline-comment-style */

/* global HTMLDivElement */

import {type CSSProperties, type MutableRefObject, useRef, useState, useEffect} from "react";

import {SpriteView} from "../sprite-view/sprite-view";
import {waitForAnimateTime} from "../../util/time";

import type {KeyFrameAnimationIndexType, SpriteAnimationType} from "./animation-type";
import {getDefaultSpriteStyle} from "./sprite-helper";

interface AnimationViewPropsType {
    readonly frameTimeInterval: number;
    readonly onEnd: () => void;
    readonly spriteAnimation: SpriteAnimationType;
    readonly wrapperStyle: CSSProperties;
    readonly x: number;
    readonly y: number;
}

export function AnimationView(props: AnimationViewPropsType): JSX.Element {
    const {spriteAnimation, onEnd, x, y, frameTimeInterval, wrapperStyle} = props;
    const {spriteList, animationIndexList, zIndex} = spriteAnimation;
    const [frameIndex, setFrameIndex] = useState<number>(0);
    const frame: MutableRefObject<HTMLDivElement | null> = useRef<HTMLDivElement | null>(null);
    const frameStyleRef: MutableRefObject<CSSProperties> = useRef<CSSProperties>(
        getDefaultSpriteStyle(animationIndexList)
    );

    useEffect(() => {
        (async (): Promise<undefined> => {
            if (frameIndex === spriteList.length) {
                onEnd();
                return;
            }

            await waitForAnimateTime(frameTimeInterval);

            setFrameIndex(frameIndex + 1);
        })();
    }, [frameIndex, frameTimeInterval, onEnd, spriteList.length]);

    useEffect(() => {
        const animationByIndexList: Array<KeyFrameAnimationIndexType> =
            animationIndexList.filter<KeyFrameAnimationIndexType>(
                (
                    animationIndexInList: KeyFrameAnimationIndexType
                ): animationIndexInList is KeyFrameAnimationIndexType => {
                    return animationIndexInList.frameIndex === frameIndex;
                }
            );

        animationByIndexList.forEach((animationIndexInList: KeyFrameAnimationIndexType) => {
            if (!frame.current) {
                return;
            }

            const animation = frame.current.animate(animationIndexInList.keyframeList, {
                composite: "replace",
                direction: "normal",
                easing: "linear",
                fill: "forwards",
                iterations: 1,
                ...animationIndexInList.keyframeAnimationOptions,
                delay: Number(animationIndexInList.keyframeAnimationOptions.delay ?? 0) * frameTimeInterval,
                duration: Number(animationIndexInList.keyframeAnimationOptions.duration ?? 0) * frameTimeInterval,
            });

            animation.onfinish = (): undefined => {
                const lastKeyFrame = animationIndexInList.keyframeList.at(-1);

                if (!lastKeyFrame) {
                    return;
                }

                Object.keys(lastKeyFrame).forEach((key: string) => {
                    if (key === "transform" && frame.current) {
                        animation.cancel();
                        frame.current.style.transform = String(lastKeyFrame[key]);
                    }
                });
            };
        });
    }, [frameIndex, animationIndexList, frameTimeInterval]);

    const spriteByIndex = spriteList.at(frameIndex) ?? spriteList.at(frameIndex - 1);

    if (!spriteByIndex) {
        console.error(`[ERROR]: AnimationView: can not get first sprite index: ${frameIndex}`);
        console.log(spriteList);

        return <div key="error-no-frame" />;
    }

    return (
        <div
            style={{
                ...wrapperStyle,
                zIndex: (Number(wrapperStyle.zIndex) || 0) + zIndex,
            }}
        >
            <SpriteView
                // fix linter
                frame={frame}
                key="frame"
                sprite={spriteByIndex}
                // style={getDefaultSpriteStyle(animationIndexList)}
                style={frameStyleRef.current}
                x={x}
                y={y}
            />
        </div>
    );
}
