/* global KeyframeAnimationOptions, Keyframe, document */
import {useCallback, useEffect} from "react";

import {useGameContext} from "../../../../provider/game/game-context";
import {GameActionTypeEnum} from "../../../../provider/game/game-context-type";
import {
    getIsSkirmishEnd,
    getPlayerByPlayerId,
    getPlayerColorIdByPlayerId,
    getPlayerTurnIncome,
} from "../../../../model/game/player/player-helper";
import {GamePopup} from "../../../../layout/game-popup/game-popup";
import {colorFightKeyLangMap} from "../../client-local-game-setup/client-local-game-setup-player/image/image-color";
import {Locale} from "../../../../provider/locale/locale-context";
import {type GamePlayerColorIdEnum, GamePlayerTypeEnum} from "../../../../model/game/player/player-type";
import gamePopupStyle from "../../../../layout/game-popup/game-popup.scss";
import {BigText} from "../../../../layout/game-font/text/big-text";
import {useSettingContext} from "../../../../provider/setting/setting-context";

export function ClientGameNewTurnPopup(): JSX.Element {
    const {
        setting: {
            numberSetting: {animationStepTime},
        },
    } = useSettingContext();
    const {animationList, gameState} = useGameContext();
    const first = animationList.at(0);
    const {activeGamePlayerId, buildingList, playerList} = gameState;
    const isBot = getPlayerByPlayerId(activeGamePlayerId, playerList)?.playerType === GamePlayerTypeEnum.bot;
    const popupVisibleBot = animationStepTime * 3;
    const popupVisibleHuman = animationStepTime * 10;

    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;

        if (action.actionType !== GameActionTypeEnum.changeTurn || getIsSkirmishEnd(playerList)) {
            return;
        }

        const moveTransformList: Array<Keyframe> = [{opacity: 0, transform: "translate3d(0%, 0, 0)"}];

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: isBot ? popupVisibleBot : popupVisibleHuman,
            easing: "ease-in-out",
            fill: "forwards",
            iterations: 1,
        };

        const animation = document.createElement("div").animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [popupVisibleBot, popupVisibleHuman, playerList, first, isBot]);

    const hidePopup = useCallback(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;

        if (action.actionType !== GameActionTypeEnum.changeTurn) {
            return;
        }

        animationOnFinish();
    }, [first]);

    const playerColor: GamePlayerColorIdEnum = getPlayerColorIdByPlayerId(activeGamePlayerId, playerList);

    return (
        <GamePopup
            color={playerColor}
            isOpen={
                Boolean(first && first.action.actionType === GameActionTypeEnum.changeTurn) &&
                !getIsSkirmishEnd(playerList)
            }
            setIsOpen={hidePopup}
        >
            <button className={gamePopupStyle.game_popup__button_as_header} onClick={hidePopup} type="button">
                <Locale stringKey={colorFightKeyLangMap[playerColor]} />
                <br />
                <span className={gamePopupStyle.game_popup__income}>
                    <Locale
                        stringKey="NOTIFICATION__INCOME"
                        valueMap={{
                            income: (
                                <BigText
                                    scale={2}
                                    text={`  g  ${getPlayerTurnIncome(activeGamePlayerId, buildingList)}`}
                                />
                            ),
                        }}
                    />
                </span>
            </button>
        </GamePopup>
    );
}
