/* global Audio, HTMLAudioElement, Event, localStorage */

import type {PromiseResolveType} from "../../util/promise";
import {noop} from "../../util/function";

import {AudioPlayerAudioRowEnum} from "./audio-player-type";

const audioRowVolumeStoragePrefix = "audio-row-volume-";
const audioRowIsMutedStoragePrefix = "audio-row-is-muted-";
const audioRowIsMutedTrue = "muted";
const audioRowIsMutedFalse = "not-muted";

function makeAudioItem(): HTMLAudioElement {
    const audio: HTMLAudioElement = new Audio();

    audio.preload = "metadata";

    return audio;
}

const audioRowMap: Record<AudioPlayerAudioRowEnum, HTMLAudioElement> = {
    [AudioPlayerAudioRowEnum.ambient]: makeAudioItem(),
    [AudioPlayerAudioRowEnum.master]: makeAudioItem(),
    [AudioPlayerAudioRowEnum.fight]: makeAudioItem(),

    // [AudioPlayerAudioRowEnum.defeat]: makeAudioItem(),
    [AudioPlayerAudioRowEnum.uiEffect]: makeAudioItem(),
    // [AudioPlayerAudioRowEnum.victory]: makeAudioItem(),
};

export function getAudioByRow(audioRow: AudioPlayerAudioRowEnum): HTMLAudioElement {
    return audioRowMap[audioRow];
}

export function saveAudioRowVolume(audioRow: AudioPlayerAudioRowEnum, volume: number): void {
    localStorage.setItem(audioRowVolumeStoragePrefix + audioRow, volume.toString(10));
}

export function getAudioRowVolume(audioRow: AudioPlayerAudioRowEnum): number {
    const isMasterRow: boolean = audioRow === AudioPlayerAudioRowEnum.master;
    const defaultAudioRowVolume = isMasterRow ? 1 : 0.5;
    const rawSavedVolume: unknown = localStorage.getItem(audioRowVolumeStoragePrefix + audioRow);

    const savedVolume: number = Number.parseFloat(String(rawSavedVolume));

    return Number.isNaN(savedVolume) ? defaultAudioRowVolume : savedVolume;
}

export function saveAudioRowIsMuted(audioRow: AudioPlayerAudioRowEnum, isMuted: boolean): void {
    localStorage.setItem(audioRowIsMutedStoragePrefix + audioRow, isMuted ? audioRowIsMutedTrue : audioRowIsMutedFalse);
}

export function getAudioRowIsMuted(audioRow: AudioPlayerAudioRowEnum): boolean {
    const rawSavedVolume: unknown = localStorage.getItem(audioRowIsMutedStoragePrefix + audioRow);

    return rawSavedVolume === audioRowIsMutedTrue;
}

export interface PlayAudioArgumentType {
    audioRow: AudioPlayerAudioRowEnum;
    // IsMuted: boolean;
    onEnded?: () => void;
    src: string;
    // Volume: number;
}

export async function playAudio(playAudioData: PlayAudioArgumentType): Promise<unknown> {
    const {src, audioRow, onEnded = noop} = playAudioData;
    const audio = getAudioByRow(audioRow);
    const volume = getAudioRowVolume(audioRow);
    const isMuted = getAudioRowIsMuted(audioRow);
    const volumeMaster = getAudioRowVolume(AudioPlayerAudioRowEnum.master);
    const isMutedMaster = getAudioRowIsMuted(AudioPlayerAudioRowEnum.master);

    return new Promise((resolve: PromiseResolveType<void>, reject: PromiseResolveType<Error>) => {
        if (!String(audio.src).includes(src)) {
            audio.src = src;
        }

        // WARNING: onended works on Android 6.x+
        // eslint-disable-next-line unicorn/prefer-add-event-listener
        audio.onended = (): undefined => {
            console.log(`[playAudio] [onended]: ${src}, ${audioRow}`);

            // eslint-disable-next-line unicorn/prefer-add-event-listener, no-param-reassign
            audio.onended = null;

            audio.src = "";
            audio.currentTime = 0;

            onEnded();
        };

        // eslint-disable-next-line unicorn/prefer-add-event-listener
        audio.oncanplay = async (): Promise<undefined> => {
            console.log(`[playAudio] [oncanplay]: ${src}, ${audioRow}`);

            // eslint-disable-next-line unicorn/prefer-add-event-listener, no-param-reassign
            audio.oncanplay = null;

            // Audio.playbackRate = 16;

            try {
                audio.muted = isMuted || isMutedMaster;
                audio.volume = volume * volumeMaster;
                await audio.play();
                console.log(`[playAudio]: ${src}`);
                resolve();
                // eslint-disable-next-line unicorn/prefer-optional-catch-binding
            } catch (tryToPlayError: unknown) {
                console.log(`[ERROR] [playAudio]: ${src}`);
                console.log(tryToPlayError);
                reject(new Error(String(tryToPlayError)));
            }
        };

        // eslint-disable-next-line unicorn/prefer-add-event-listener
        audio.onerror = (errorEvent: Event | string): undefined => {
            console.log(`[playAudio] [onerror]: ${src}, ${audioRow}`);

            // eslint-disable-next-line unicorn/prefer-add-event-listener, no-param-reassign
            audio.onerror = null;

            reject(new Error(String(errorEvent)));
        };
    });
}
