import {type ActionArgumentType, type GameAnimationType, GameActionTypeEnum} from "../game-context-type";
import type {GameUnitType} from "../../../model/game/unit/unit-type";
import type {GameStateType} from "../../../model/game/game-type";
import {getUnitByPoint, getUnitByUnitId} from "../../../model/game/unit/unit-helper";
import {waitForNextFrame} from "../../../util/time";
import type {PromiseResolveType} from "../../../util/promise";

export async function unitMoveAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState} = data;
    const unit: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, gameState.unitList);
    const isForwardMove: boolean = action.actionType === GameActionTypeEnum.unitMove;

    if (!unit) {
        return waitForNextFrame();
    }

    /*
    if (getIsEqualPoint(action.toPoint, action.unitCurrentPoint)) {
        console.log('!getIsEqualPoint!');
        console.log(action);
        /!*
        await waitForNextFrame();
        await waitForNextFrame();
*!/

        setGameState((currentGameState: GameStateType): GameStateType => {
            const newUnitList = [...currentGameState.unitList];
            const unitToMove: GameUnitType | null = getUnitByUnitId(action.unitId, currentGameState.unitList);

            if (!unitToMove) {
                console.error('can not find unit to move');
                return currentGameState;
            }

            newUnitList[newUnitList.indexOf(unitToMove)] = {
                ...unitToMove,
                // didAttackBuilding: true,
                // didAttackUnit: true,
                // didFixBuilding: true,
                didMove: true,
                // didOccupyBuilding: true,
                // didRaiseSkeleton: true,
            };
            const newGameState: GameStateType = {
                ...currentGameState,
                unitList: newUnitList,
            };

            return newGameState;
        });

        await waitForNextFrame();

        removeAnimationByAction(action);

        return waitForNextFrame();
    }
*/

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>): Array<GameAnimationType> => {
            return [
                ...animList,
                {
                    action,
                    // eslint-disable-next-line @typescript-eslint/no-misused-promises
                    animationOnFinish: async (): Promise<void> => {
                        setGameState((currentGameState: GameStateType): GameStateType => {
                            const updatedUnitList = [...currentGameState.unitList];
                            const unitToMove: GameUnitType | null = getUnitByUnitId(
                                action.unitId,
                                currentGameState.unitList
                            );

                            if (!unitToMove) {
                                console.error("can not find unit to move");
                                return currentGameState;
                            }

                            updatedUnitList[updatedUnitList.indexOf(unitToMove)] = {
                                ...unitToMove,
                                didMove: isForwardMove,
                                x: action.toPoint.x,
                                y: action.toPoint.y,
                            };

                            /*
                        unitToMove.x = action.toPoint.x;
                        unitToMove.y = action.toPoint.y;
                        unitToMove.didMove = true;
*/

                            const updatedGameState: GameStateType = {
                                ...currentGameState,
                                unitList: updatedUnitList,
                            };

                            return updatedGameState;
                        });
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
