import type {ReactNode} from "react";

import {LocalizationProvider} from "../provider/locale/locale-context";
import {GameProvider} from "../provider/game/game-context";
import {PromptProvider} from "../provider/prompt/prompt-context";
import {AlertProvider} from "../provider/alert/alert-context";
import {SettingProvider} from "../provider/setting/setting-context";
import {SnackbarProvider} from "../provider/snackbar/snackbar-context";

interface PropsType {
    readonly children: ReactNode;
}

export function AppProvider(props: PropsType): JSX.Element {
    const {children} = props;

    return (
        <LocalizationProvider>
            <SettingProvider>
                <GameProvider>
                    <PromptProvider>
                        <AlertProvider>
                            <SnackbarProvider>{children}</SnackbarProvider>
                        </AlertProvider>
                    </PromptProvider>
                </GameProvider>
            </SettingProvider>
        </LocalizationProvider>
    );
}
