/* global document, HTMLDivElement, HTMLImageElement, KeyframeAnimationOptions, Keyframe */
import {useEffect, useRef} from "react";

import {useGameContext} from "../../../../provider/game/game-context";
import {GameActionTypeEnum} from "../../../../provider/game/game-context-type";
import {squareSize} from "../../../../css/style";
import type {GameUnitType} from "../../../../model/game/unit/unit-type";
import {getUnitToUnitAttackResult} from "../../../../model/game/unit/unit-to-unit-attack";
import {getUnitByPoint} from "../../../../model/game/unit/unit-helper";
import {FrameByFrame} from "../../../../layout/frame-by-frame/frame-by-frame";
import {useSettingContext} from "../../../../provider/setting/setting-context";
import {getNeedShowFightAnimation} from "../../../../layout/unit-fight-animation/unit-fight-animation-helper";
import {useUnitFightAnimation} from "../../../../layout/unit-fight-animation/use-unit-fight-animation";
import {IsRender} from "../../../../layout/is-render/is-render";

import {attackImageSize, unitEffectImage} from "./image/effect-image";
import clientGameEffectStyle from "./client-game-effect.scss";

const attackImageList: Array<string> = [
    unitEffectImage.attackAnimation0.src,
    unitEffectImage.attackAnimation1.src,
    unitEffectImage.attackAnimation2.src,
    unitEffectImage.attackAnimation3.src,
    unitEffectImage.attackAnimation4.src,
    unitEffectImage.attackAnimation5.src,
];

// eslint-disable-next-line complexity, max-statements
export function ClientGameEffect(): JSX.Element {
    const {
        setting: {
            numberSetting: {animationStepTime},
            booleanSetting: {isUnitFightAnimationEnable},
        },
    } = useSettingContext();
    const {animationList, gameState} = useGameContext();
    const attackNode = useRef<HTMLDivElement>(document.createElement("div"));
    const destroyBuildingNode = useRef<HTMLDivElement>(document.createElement("div"));
    const raiseSkeletonNode = useRef<HTMLDivElement>(document.createElement("div"));
    const occupyNode = useRef<HTMLImageElement>(document.createElement("img"));
    const fixNode = useRef<HTMLImageElement>(document.createElement("img"));
    const first = animationList.at(0);
    const isUseUnitFightAnimation = useUnitFightAnimation();

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;
        const {unitCurrentPoint, toPoint, actionType} = action;

        if (actionType !== GameActionTypeEnum.buyUnit) {
            return;
        }

        const transformFromPoint: Keyframe = {
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${unitCurrentPoint.y * squareSize}px, 0)`,
        };

        const transformToPoint: Keyframe = {
            transform: `translate3d(${toPoint.x * squareSize}px, ${toPoint.y * squareSize}px, 0)`,
        };

        const moveTransformList: Array<Keyframe> = [
            transformFromPoint,
            transformToPoint,
            transformToPoint,
            transformToPoint,
        ];

        const unitMoveTiming: KeyframeAnimationOptions = {
            // The duration: animationStepTime * (moveTransformList.length - 1),
            duration: 0,
            fill: "forwards",
            iterations: 1,
        };

        const animation = document.createElement("div").animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [first, gameState]);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;
        const {unitCurrentPoint, toPoint, actionType} = action;

        if (actionType !== GameActionTypeEnum.unitToUnitAttack) {
            return;
        }

        const {unitList} = gameState;
        const aggressor: GameUnitType | null = getUnitByPoint(unitCurrentPoint, unitList);
        const defender: GameUnitType | null = getUnitByPoint(toPoint, unitList);

        if (getNeedShowFightAnimation(isUnitFightAnimationEnable, aggressor, defender) === true) {
            return;
        }

        if (!aggressor || !defender) {
            animationOnFinish();
            console.error("ClientGameEffect - can not find unit to action");
            console.log(first);
            console.log({aggressor, defender});
            return;
        }

        const {hasStrikeBack} = getUnitToUnitAttackResult({aggressor, defender}, gameState);

        const transformFromPoint: Keyframe = {
            opacity: 1,
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${unitCurrentPoint.y * squareSize}px, 0)`,
        };

        const transformFromPointHide: Keyframe = {
            opacity: 0,
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${unitCurrentPoint.y * squareSize}px, 0)`,
        };

        const transformToPoint: Keyframe = {
            opacity: 1,
            transform: `translate3d(${toPoint.x * squareSize}px, ${toPoint.y * squareSize}px, 0)`,
        };

        const transformToPointHide: Keyframe = {
            opacity: 0,
            transform: `translate3d(${toPoint.x * squareSize}px, ${toPoint.y * squareSize}px, 0)`,
        };

        const moveTransformList: Array<Keyframe> = [
            transformFromPoint,
            transformToPoint,
            transformToPoint,
            transformToPoint,
        ];

        if (hasStrikeBack) {
            moveTransformList.push(transformFromPoint, transformFromPoint, transformFromPoint, transformFromPointHide);
        } else {
            moveTransformList.push(transformToPointHide);
        }

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: animationStepTime * (moveTransformList.length - 1),
            fill: "forwards",
            iterations: 1,
        };

        const animation = attackNode.current.animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [animationStepTime, first, gameState, isUnitFightAnimationEnable]);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;
        const {unitCurrentPoint, toPoint, actionType} = action;

        if (actionType !== GameActionTypeEnum.unitToBuildingAttack) {
            return;
        }

        const transformFromPoint: Keyframe = {
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${unitCurrentPoint.y * squareSize}px, 0)`,
        };

        const transformToPoint: Keyframe = {
            transform: `translate3d(${toPoint.x * squareSize}px, ${toPoint.y * squareSize}px, 0)`,
        };

        const moveTransformList: Array<Keyframe> = [
            transformFromPoint,
            transformToPoint,
            transformToPoint,
            transformToPoint,
        ];

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: animationStepTime * (moveTransformList.length - 1),
            fill: "forwards",
            iterations: 1,
        };

        const animation = destroyBuildingNode.current.animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [animationStepTime, first, gameState]);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;
        const {unitCurrentPoint, actionType} = action;

        if (actionType !== GameActionTypeEnum.occupyBuilding) {
            return;
        }

        const transformFromPoint: Keyframe = {
            opacity: 1,
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${unitCurrentPoint.y * squareSize}px, 0)`,
        };

        const transformToPoint: Keyframe = {
            opacity: 1,
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${
                // eslint-disable-next-line no-mixed-operators
                unitCurrentPoint.y * squareSize - squareSize / 2
            }px, 0)`,
        };

        const moveTransformList: Array<Keyframe> = [
            transformFromPoint,
            transformToPoint,
            {...transformToPoint, opacity: 0},
        ];

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: animationStepTime * (moveTransformList.length - 1) * 1.5,
            fill: "forwards",
            iterations: 1,
        };

        const animation = occupyNode.current.animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [animationStepTime, first, gameState]);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;
        const {unitCurrentPoint, actionType, toPoint} = action;

        if (actionType !== GameActionTypeEnum.raiseSkeleton) {
            return;
        }

        const transformFromPoint: Keyframe = {
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${unitCurrentPoint.y * squareSize}px, 0)`,
        };

        const transformToPoint: Keyframe = {
            transform: `translate3d(${toPoint.x * squareSize}px, ${toPoint.y * squareSize}px, 0)`,
        };

        const moveTransformList: Array<Keyframe> = [
            transformFromPoint,
            transformToPoint,
            transformToPoint,
            transformToPoint,
        ];

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: animationStepTime * (moveTransformList.length - 1),
            fill: "forwards",
            iterations: 1,
        };

        const animation = raiseSkeletonNode.current.animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [animationStepTime, first, gameState]);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;
        const {unitCurrentPoint, actionType} = action;

        if (actionType !== GameActionTypeEnum.fixBuilding) {
            return;
        }

        const transformFromPoint: Keyframe = {
            opacity: 1,
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${unitCurrentPoint.y * squareSize}px, 0)`,
        };

        const transformToPoint: Keyframe = {
            opacity: 1,
            transform: `translate3d(${unitCurrentPoint.x * squareSize}px, ${
                // eslint-disable-next-line no-mixed-operators
                unitCurrentPoint.y * squareSize - squareSize / 2
            }px, 0)`,
        };

        const moveTransformList: Array<Keyframe> = [
            transformFromPoint,
            transformToPoint,
            {...transformToPoint, opacity: 0},
        ];

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: animationStepTime * (moveTransformList.length - 1) * 1.5,
            fill: "forwards",
            iterations: 1,
        };

        const animation = fixNode.current.animate(moveTransformList, unitMoveTiming);

        animation.onfinish = animationOnFinish;
    }, [animationStepTime, first, gameState]);

    const mainWrapperKey = "effect-wrapper";

    if (!first) {
        return <div className={clientGameEffectStyle.effect_layer} key={mainWrapperKey} />;
    }

    if (first.action.actionType === GameActionTypeEnum.occupyBuilding) {
        return (
            <div className={clientGameEffectStyle.effect_layer} key={mainWrapperKey}>
                <img
                    alt="occupy"
                    className={clientGameEffectStyle.effect_layer__occupy_image}
                    ref={occupyNode}
                    src={unitEffectImage.occupyBuilding.src}
                />
            </div>
        );
    }

    if (first.action.actionType === GameActionTypeEnum.fixBuilding) {
        return (
            <div className={clientGameEffectStyle.effect_layer} key={mainWrapperKey}>
                <img
                    alt="occupy"
                    className={clientGameEffectStyle.effect_layer__occupy_image}
                    ref={fixNode}
                    src={unitEffectImage.fixBuilding.src}
                />
            </div>
        );
    }

    if (first.action.actionType === GameActionTypeEnum.unitToUnitAttack) {
        return (
            <div className={clientGameEffectStyle.effect_layer} key={mainWrapperKey}>
                <div className={clientGameEffectStyle.effect_layer__attack_square} ref={attackNode}>
                    <IsRender isRender={isUseUnitFightAnimation === false}>
                        <FrameByFrame
                            duration={animationStepTime * 3}
                            frameList={attackImageList}
                            height={attackImageSize}
                            isAnimated
                            staticFrameIndex={2}
                            width={attackImageSize}
                        />
                    </IsRender>
                </div>
            </div>
        );
    }

    if (first.action.actionType === GameActionTypeEnum.raiseSkeleton) {
        return (
            <div className={clientGameEffectStyle.effect_layer} key={mainWrapperKey}>
                <div className={clientGameEffectStyle.effect_layer__attack_square} ref={raiseSkeletonNode}>
                    <FrameByFrame
                        duration={animationStepTime * 3}
                        frameList={attackImageList}
                        height={attackImageSize}
                        isAnimated
                        staticFrameIndex={2}
                        width={attackImageSize}
                    />
                </div>
            </div>
        );
    }

    if (first.action.actionType === GameActionTypeEnum.unitToBuildingAttack) {
        return (
            <div className={clientGameEffectStyle.effect_layer} key={mainWrapperKey}>
                <div className={clientGameEffectStyle.effect_layer__attack_square} ref={destroyBuildingNode}>
                    <FrameByFrame
                        duration={animationStepTime * 3}
                        frameList={attackImageList}
                        height={attackImageSize}
                        isAnimated
                        staticFrameIndex={2}
                        width={attackImageSize}
                    />
                </div>
            </div>
        );
    }

    return <div className={clientGameEffectStyle.effect_layer} key={mainWrapperKey} />;
}
