import {getRandomString} from "../../util/string";

import frameByFrameStyle from "./frame-by-frame.scss";

interface FrameByFramePropsType {
    readonly duration: number;
    readonly frameList: Array<string>;
    readonly height: number;
    readonly isAnimated: boolean;
    readonly staticFrameIndex: number;
    readonly width: number;
}

export function FrameByFrame(props: FrameByFramePropsType): JSX.Element {
    const {frameList, duration, height, width, isAnimated, staticFrameIndex} = props;
    const animationId = `frame-by-frame-${getRandomString()}`;

    const frameCount = frameList.length;

    const css = `
        @keyframes ${animationId} {
            from {transform: translateY(0);}
            to {transform: translateY(-100%);}
        }
        .${frameByFrameStyle.frame_by_frame_list} {
            animation-name: ${animationId};
            animation-duration: ${duration}ms;
            animation-timing-function: steps(${frameCount});
        }
    `;

    if (isAnimated) {
        return (
            <div className={frameByFrameStyle.frame_by_frame} style={{height, width}}>
                {/* eslint-disable-next-line react/no-danger, id-match */}
                <style dangerouslySetInnerHTML={{__html: css}} />
                <div className={frameByFrameStyle.frame_by_frame_list} style={{height: height * frameCount, width}}>
                    {frameList.map((src: string, index: number): JSX.Element => {
                        return (
                            <img
                                alt={index.toString(10)}
                                className={frameByFrameStyle.frame_by_frame_image}
                                key={`${src}-${index.toString(10)}`}
                                src={src}
                            />
                        );
                    })}
                </div>
            </div>
        );
    }

    const staticFrameSrc = frameList.at(staticFrameIndex);

    // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
    if (!staticFrameSrc) {
        console.error(JSON.stringify({frameList, staticFrameIndex}));
        console.error("can not get static frame by index");
    }

    return (
        <div className={frameByFrameStyle.frame_by_frame} style={{height, width}}>
            <div className={frameByFrameStyle.frame_by_frame_list} style={{height, width}}>
                <img
                    alt="static frame"
                    className={frameByFrameStyle.frame_by_frame_image}
                    src={staticFrameSrc ?? frameList.at(0)}
                />
            </div>
        </div>
    );
}
