export enum AudioPlayerAudioRowEnum {
    ambient = "ambient",
    fight = "fight",
    master = "master",
    uiEffect = "ui-effects",
}

export interface AudioPlayerConfigType {
    readonly audioRow: AudioPlayerAudioRowEnum;
    readonly isLoop: boolean;
    readonly isPlaying: boolean;
    readonly isShuffle: boolean;
    readonly trackList: ReadonlyArray<string>;
}

export interface AudioPlayerType {
    isPlaying: boolean;
    pause: () => void;
    play: () => void;
    stop: () => void;
}

export type SoundSettingType = Record<AudioPlayerAudioRowEnum, {isMuted: boolean; volume: number}>;
